import {
	isFirstOccurrence,
	correctMode,
	extractDate,
	FREQUENCE,
	fillWdays,
	next,
} from "./recurring";

/* public functions */

/**
 * Changes the mode if the user selected the 1st occurrence in whole sequence or in 'next'
 * @param {string} mode - the mode of editing a recurring event ("all", "next", "this")
 * @param {object} obj - the event object
 * @param {Date} start - the start date of the event
 * @param {Date, string} date_id - the ID of an occurrence of a repeated event or the date of this occurrence
 */
function changeMode(mode, obj, start, date_id) {
	if (mode && mode === "next" && isFirstOccurrence(date_id, start)) {
		mode = correctMode.call(this, obj, mode);
	}

	return mode;
}

/**
 * Corrects recurring.COUNT or recurring.UNTIL based on all sub-events that are included in the edited range
 * @param {Object} obj - the event data
 * @param {string} mode - the mode of recurring editing ("all", "next")
 * @param {Date} sdate - the date of the currently selected event occurrence
 * @param {Object} collection - DataCollection of events
 */
function correctCountUntil(obj, mode, sdate, collection) {
	const oid = obj.origin_id || obj.$id || obj.id;
	const parts = collection
		.find(e => {
			return oid == e.origin_id && sdate < e.start_date;
		})
		.sort((a, b) => {
			if (a.start_date > b.start_date) return -1;
			if (a.start_date < b.start_date) return 1;
			return 0;
		});

	const rec = webix.copy(obj.$recurring);

	for (let i = 0, last; i < parts.length; ++i) {
		last = parts[i];

		if (last.$recurring) {
			if (last.$recurring.COUNT) {
				if (rec.COUNT && obj.$id != last.id) {
					rec.COUNT = rec.COUNT * 1 + last.$recurring.COUNT * 1;
				} else {
					rec.UNTIL = countToUntil(last);
					break;
				}
			} else {
				rec.UNTIL = last.$recurring.UNTIL;
				break;
			}
		}
	}

	if (mode === "next" && !parts.length) {
		if (rec.COUNT) {
			rec.COUNT = cutCount(obj, sdate);
		} else if (rec.UNTIL && obj.start_date >= rec.UNTIL) {
			rec.UNTIL = webix.Date.add(
				webix.Date.dayStart(obj.start_date),
				1,
				FREQUENCE[rec.FREQ],
				true
			);
		}
	}

	return rec;
}

/**
 * Checks if the selected repeating event has only 1 occurrence
 * @param {Object} obj - the event data
 */
function hasOneOccurrence(obj) {
	// as this a 'this' event
	if (!obj.recurring) return true;

	// as 'this' any occurrence in unedited sequence
	if (!obj.$recurring.UNTIL) return false;

	const end = webix.Date.dayStart(webix.Date.add(obj.end_date, 1, "day", true));
	return webix.Date.equal(end, obj.$recurring.UNTIL);
}

/* EO public functions */

/* private functions */
function countToUntil(last) {
	const lr = last.$recurring;
	let step = lr.COUNT * 1; // !
	if (lr.FREQ === "WEEKLY") {
		step = Math.floor((lr.BYDAY.split(",").length / 7) * lr.COUNT);
	}
	return webix.Date.add(last.start_date, step, FREQUENCE[lr.FREQ], true);
}

function cutCount(obj, sdate) {
	const date = extractDate(sdate);

	const consts = {};
	fillWdays(obj.$recurring, consts);
	let nexto = webix.Date.dayStart(obj.start_date);
	let count = 0;
	while (nexto < date) {
		count++;
		nexto = next(nexto, obj.$recurring, consts);
	}
	return obj.$recurring.COUNT - count;
}
/* EO private functions */

export default function EditRecurringMixin(child) {
	child.ChangeMode = changeMode;
	child.CorrectCountUntil = correctCountUntil;
	child.HasOneOccurrence = hasOneOccurrence;
}
