import { JetView } from "webix-jet";

export default class BarView extends JetView {
	config() {
		const active = webix.skin.$active;
		const _ = this.app.getService("locale")._;
		const state = this.getParam("state", true);

		return {
			view: "toolbar",
			height: active.barHeight,
			elements: [
				{},
				{
					view: "richselect",
					label: _("Timeline scale"),
					labelWidth: 120,
					width: 260,
					value: state.timelineMode,
					options: [
						{ id: "day", value: _("Day") },
						{ id: "week", value: _("Week") },
						{ id: "month", value: _("Month") },
					],
					on: {
						onChange: v => {
							state.timelineMode = v;
						},
					},
				},
			],
		};
	}
}
