import { JetView } from "webix-jet";
import HourscaleView from "../common/hourscale";
import UnitEventsView from "./events";
import UnitsHeaderView from "./header";
import UnitMultidayView from "./multiday";

import { isMultiDay } from "helpers/dates";

export default class UnitsView extends JetView {
	config() {
		this.Compact = this.getParam("compact", true);
		const units = this.app.getService("local").units();

		const multi = {
			localId: "multi",
			hidden: true,
			cols: [UnitMultidayView],
		};

		const days = {
			localId: "scroll",
			view: "scrollview",
			scroll: "y",
			css: "webix_scheduler_week_days",
			body: {
				cols: [HourscaleView],
			},
		};

		return units.then(unitsCache => {
			const data = unitsCache.serialize();
			for (let i = 0; i < data.length; ++i) {
				days.body.cols.push(
					new (this.app.dynamic(UnitEventsView))(this.app, {
						unit: data[i],
					})
				);
			}

			return {
				rows: [UnitsHeaderView, multi, days],
			};
		});
	}
	init() {
		const state = this.app.getState();

		this.on(state.$changes, "date", value => this.RefreshData(value));
		this.on(state.$changes, "active", () => this.RefreshData(state.date));
		this.on(this.app, "events:refresh", () => this.RefreshData(state.date));

		// updates made in form
		const events = this.app.getService("local").events(true);
		this.on(
			events.data,
			"onStoreUpdated",
			(_i, _o, mode) => mode && this.RefreshData(state.date)
		);
	}

	RefreshData(value) {
		this.GetDay(value).then(data => {
			// this.app will be null if compact trigger was called in main.js
			if (this.app) this.setParam("data", data, true);

			if (data.multi.length) {
				this.$$("multi").show();
			} else this.$$("multi").hide();
		});
	}

	GetDay(date) {
		const end = webix.Date.add(date, 1, "day", true);
		return this.app
			.getService("local")
			.getEvents(date, end)
			.then(evs => {
				const multi = [],
					single = [];
				evs.forEach(ev => {
					if (isMultiDay(ev)) {
						multi.push(ev);
					} else {
						single.push(ev);
					}
				});
				return { multi, single };
			});
	}
}
