function _select(id, functor) {
	const trgs = this.LocateSelected(id);
	if (trgs) {
		for (let i = 0; i < trgs.length; i++)
			functor(trgs[i], "webix_scheduler_event_selected");
	}
}

function Select(id) {
	return _select.call(this, id, webix.html.addCss);
}

function Unselect(id) {
	return _select.call(this, id, webix.html.removeCss);
}

function HandleSelection() {
	const mode = this._SelectionMode;
	this.State = this.app.getState();

	this.on(this.State.$changes, "selected", (v, o) => {
		if (o) this.Unselect(o.date && typeof o.date === "string" ? o.date : o.id);

		if (v) {
			const id = v.id === "0" || !v.date ? v.id : v.date;
			setTimeout(
				() => this.Select(id),
				mode !== "multi" && id === "0" ? 150 : 0
			);
		}
	});

	// reapply selection to a saved new event
	if (mode !== "multi") {
		const events = this.app.getService("local").events(true);
		this.on(events.data, "onStoreUpdated", (id, o, operation) => {
			if (
				operation === "add" &&
				id != "0" &&
				this.State.selected &&
				this.State.selected.id === "0"
			) {
				this.State.selected.id = id;
			}
		});
	}
}

function locate(inner) {
	return function LocateSelected(id) {
		if (this._DataObj) {
			return Array.prototype.map.call(
				this._DataObj.querySelectorAll(
					`[webix_${inner ? "l" : "e"}_id="${id}"]`
				),
				el => (inner ? el.firstElementChild : el)
			);
		}
		return null;
	};
}

export default function SelectionMixin(child, mode, inner) {
	child._SelectionMode = mode;

	child.Select = Select;
	child.Unselect = Unselect;
	child.HandleSelection = HandleSelection;
	child.LocateSelected = locate(inner);

	child.HandleSelection();
}
