import MultiMoreView from "../common/multimore";
import UnitMultidayList from "./multidaylist";
export default class UnitMultidayView extends MultiMoreView {
	config() {
		const ui = super.config();
		const units = this.app.getService("local").units();

		const more = ui.cols[0];
		more.onClick = {
			"wxi-angle-down": () =>
				this.app.callEvent("units:multi:state", ["up", true]),
			"wxi-angle-up": () =>
				this.app.callEvent("units:multi:state", ["down", true]),
		};

		ui.cols[1] = { cols: [] };

		return units.then(unitsCache => {
			const data = unitsCache.serialize();
			for (let i = 0; i < data.length; ++i) {
				ui.cols[1].cols.push(
					new (this.app.dynamic(UnitMultidayList))(this.app, {
						unit: data[i],
					})
				);
			}

			return ui;
		});
	}

	init() {
		this.State = this.app.getState();
		this.MoreIcon = this.$$("more");

		this.on(this.State.$changes, "date", () => {
			this.SetMoreIcon();
		});

		this.on(this.app, "units:multi:state", (state, animate) => {
			this.Animate(state, animate ? 150 : 1);
		});
	}

	/**
	 * Sets a delay for animation of inner views (collapse/expand)
	 * @param {string} mode - "up" or "down" - the direction of the icon
	 * @param {number} delay - delay in milliseconds
	 */
	Animate(mode, delay) {
		setTimeout(() => {
			this.SetMoreIcon(mode);
		}, delay);
	}
}
