import { JetView } from "webix-jet";

export default class BaseStructureView extends JetView {
	HideField(id) {
		this.$$("fields").updateItem(id, { show: false });
		this.SaveOrder();
	}

	ShowField(id) {
		this.$$("fields").updateItem(id, { show: true });
		this.SaveOrder();
	}

	HideAllEditors(id, name) {
		if (!name) name = "fields";
		if (this._ActiveEditor[name])
			this.$$(name).closeSub(this._ActiveEditor[name]);

		this._ActiveEditor[name] = id;
		if (id) {
			webix.delay(
				() => {
					const subView = this.$$(name).getSubView(id);
					if (subView) subView.focus();
				},
				null,
				null,
				100
			);
		}
	}

	ResetEditor(name) {
		this._ActiveEditor[name || "fields"] = null;
	}

	ToggleSub(id, name) {
		const table = this.$$(name || "fields");
		var row = table.getItem(id);
		if (row.$subopen) table.closeSub(id);
		else table.openSub(id);
	}

	SaveOrder() {}
}
