import { createState, ignoreInitial } from "jet-restate";

import ColumnProperties from "./column";

import "../../../helpers/rsearch";
import BaseView from "./baseview";

export default class ColumnsView extends BaseView {
	config() {
		const _ = (this._ = this.app.getService("locale")._);
		this._ActiveEditor = {};
		const header = {
			padding: { top: webix.skin.$active.layoutPadding.toolbar },
			rows: [
				{ view: "label", label: _("Columns") },
				{
					view: "text-search",
					localId: "search",
					on: {
						onSearchChange: () => this.FilterFields(),
					},
				},
			],
		};

		const fields = {
			view: "datatable",
			css: "webix_rpt_columns_list",
			localId: "fields",
			borderless: true,
			autoheight: true,
			header: false,
			select: false,
			subview: (obj, target) => {
				const column = createState({
					name: obj.meta.name || obj.name,
					header: obj.meta.header || "none",
				});
				this.on(
					column.$changes,
					"*",
					ignoreInitial(() => {
						this.Update(obj.id, column);
					})
				);

				const sub = new ColumnProperties(this.app, column);
				this.ui(sub, { container: target });
				return sub.getRoot();
			},
			columns: [
				{
					fillspace: true,
					template: (item, common) => this.RowTemplate(item, common),
				},
			],
			drag: "order",
			on: {
				onItemDblClick: function(id) {
					if (id.row !== "$freeze") this.openSub(id);
				},
				onBeforeDrag: ctx => {
					this.HideAllEditors();
					const list = this.$$("fields");
					webix.html.addCss(list.$view, "webix_rpt_drag");

					if (ctx.start === "$freeze") {
						ctx.html = `<div class="webix_drag_zone"><div class="webix_dd_drag wbx-mark_frozen" style="width:${
							list.$width
						}px;"><div>${this.FrozenTemplate()}</div></div></div>`;
					} else if (!list.getItem(ctx.start).show) return false;
				},
				onAfterRender: () => {
					if (this._hasFocus) {
						const el = this._hasFocus;
						this._hasFocus = null;

						if (el && document.contains(el) && el !== document.activeElement)
							el.focus();
					}
				},
				onAfterDrop: () => {
					webix.html.removeCss(this.$$("fields").$view, "webix_rpt_drag");
					this.SaveOrder();
				},
				onSubViewOpen: id => this.HideAllEditors(id),
				onSubViewClose: () => this.ResetEditor(),
			},
			onClick: {
				"action-visible": (e, id) => this.HideField(id),
				"action-hidden": (e, id) => this.ShowField(id),
				webix_rpt_config_row_name: (e, id) => this.ToggleSub(id),
			},
		};

		return {
			view: "form",
			scroll: "auto",
			padding: { top: webix.skin.$active.layoutPadding.toolbar },
			rows: [header, fields],
		};
	}

	init() {
		this.State = this.getParam("state", true);

		const table = this.$$("fields");
		const has = {};
		const columns = this.State.columns.map(a => {
			has[a.id] = 1;
			return { ...a, show: true };
		});

		(this.State.publicFields || this.State.fields).forEach(a => {
			if (!has[a.id]) columns.push({ ...a, show: false });
		});

		columns.splice(this.State.meta.freeze || 0, 0, {
			id: "$freeze",
			$css: "wbx-mark_frozen",
			name: this.FrozenTemplate(),
		});
		table.parse(columns);
	}

	FilterFields() {
		const f = this.$$("search")
			.getValue()
			.toLowerCase();
		this.$$("fields").filter(a => a.name.toLowerCase().indexOf(f) !== -1);
	}

	Update(id, update) {
		this._hasFocus = document.activeElement;
		const columns = [...this.State.columns];
		const ind = columns.findIndex(a => a.id == id);
		Object.assign(columns[ind].meta, update);
		this.State.$batch({
			columns,
			meta: { ...this.State.meta, freeze: this.GetFreeze() },
		});
	}

	GetFreeze() {
		const table = this.$$("fields");
		const order = table.data.order;

		let freezeColumn = 0;
		for (var i = 0; i < order.length; i++) {
			if (order[i] === "$freeze") break;
			freezeColumn += table.getItem(order[i]).show ? 1 : 0;
		}

		return freezeColumn;
	}

	SaveOrder() {
		const table = this.$$("fields");
		const columns = [];
		table.data.each(a => {
			if (a.show)
				columns.push(
					(this.State.publicFields || this.State.fields).find(
						b => b.id === a.id
					)
				);
		});

		this.State.$batch({
			columns,
			meta: { ...this.State.meta, freeze: this.GetFreeze() },
		});
	}

	FrozenTemplate() {
		return (
			"<span class='webix_rpt_frozen webix_icon rpi-arrow-horizontal-lock'></span><span>" +
			this._("Frozen columns above") +
			"</span>"
		);
	}

	RowTemplate(item, common) {
		let html =
			"<div class='webix_rpt_config_row'><div class='webix_icon rpi-drag-vertical'></div>";
		html +=
			item.id !== "$freeze"
				? "<span class='webix_icon " +
				  (item.show
						? "action-visible rpi-eye"
						: "action-hidden rpi-eye-off-outline") +
				  "'></span>"
				: "";
		html +=
			"<div class='webix_rpt_config_row_name'>" +
			item.name +
			(item.model
				? "<span class='webix_rpt_sources_path'>" + item.model + "</span>"
				: "") +
			"</div>";
		html += item.id !== "$freeze" ? common.subrow(item, common) : "";
		return html + "</div>";
	}
}
