export const Resizer = {
	$dragIn: function() {
		const ctx = this.getContext();

		if (ctx.from.mode !== this.mode) return false;
		return true;
	},
	$dragPos: function(pos) {
		const ctx = this.getContext();

		const box = webix.html.offset(ctx.target);
		let y = pos.y - box.y;

		if (y < ctx.y_diff) y = ctx.y_diff;
		else if (y > box.height) y = box.height;

		const height = y - ctx.y_diff;
		Resizer.updateNodeHeight.call(this, height);

		if (ctx.duration !== height) {
			ctx.duration = height;
			this.updateText(ctx);
		}

		pos.x = ctx.x_diff;
		pos.y = ctx.y_diff;
	},
	updateNodeHeight: function(height) {
		const node = webix.DragControl.getNode();
		node.style.height = Math.max(this.master.minEventHeight, height) + "px";
	},
	getActualDate: function(master, any) {
		const ctx = this.getContext();

		const date = webix.Date.copy(ctx.event.start_date);
		const minutes =
			(Math.round((ctx.duration * 60) / master.List.type.height / 5) || 1) * 5;

		webix.Date.add(date, minutes, "minute");

		if (!any && webix.Date.equal(date, ctx.event.end_date)) return null;

		return {
			start_date: webix.Date.copy(ctx.event.start_date),
			end_date: date,
		};
	},
};
