import { cutOccurrence } from "helpers/recurring";
import hotkey from "jet-hotkey";

/**
 * Common function used to prepare data of a copied event
 * @param {Object} newEvent
 */
export function cleanEventData(newEvent) {
	// in recurring event, copy only this event and make sure it stays a part of the series
	const isRecurring = newEvent.recurring || newEvent.$id || newEvent.origin_id;
	if (isRecurring) {
		newEvent.recurring = "";
		delete newEvent.$recurring;
		if (!newEvent.origin_id) newEvent.origin_id = newEvent.$id || newEvent.id;

		delete newEvent.$id;
	}

	delete newEvent.id;

	const _ = this.app.getService("locale")._;
	if (newEvent.text)
		newEvent.text = `${_("Copy of")} ${newEvent.text.replace(
			`${_("Copy of")} `,
			""
		)}`;
}

/** CopyPaste Mixin adds logic for copying and cutting events with hotkeys */

/**
 * Handles events related to copying, cutting and pasting events (desktop devices)
 * @param {Object} view - a Webix data widget that is used to display events
 */
function HandleCopyPaste(view) {
	this.clipboardModes = ["copy", "cut"];
	this.Ops = this.app.getService("operations");
	const ctrlKey = webix.env.isMac ? "COMMAND" : "CTRL";
	// when form is opened, hotkeys do not work because focus is on form, not on view
	this.on(hotkey(view), `${ctrlKey}+C`, v => this.AddToClipboard(v, "copy"));
	this.on(hotkey(view), `${ctrlKey}+X`, v => this.AddToClipboard(v, "cut"));
	this.on(hotkey(view), `${ctrlKey}+V`, (v, e) => this.PasteFromClipboard(e));
	this.on(hotkey(view), "Esc", (v, e) => this.PurgeClipboard(v, e));

	this.on(this.State.$changes, "mode", () => {
		webix.UIManager.setFocus(view);
	});

	this.on(this.State.$changes, "clipboard", (v, o) => {
		if (v) {
			webix.html.addCss(view.$view, "webix_scheduler_clipboard_target");
		} else if (o) {
			webix.html.removeCss(view.$view, "webix_scheduler_clipboard_target");
		}
	});

	this.on(this.app, "events:rendered", () => {
		this.RestoreClipboardSource(view);
	});

	if (this.$multi) {
		this._globalClickHandler = webix.event(this.List.$view, "click", () => {
			if (this.State.clipboard && this.State.clipboard.source) {
				this.ChangePasteTarget(this.List.$view);
			}
		});
	} else {
		this.on(view, "onItemClick", (id, e, node) => {
			if (this.State.clipboard && this.State.clipboard.source) {
				if (this.State.mode === "timeline") {
					node = this.CreatePasteTarget(view, e);
				}
				this.ChangePasteTarget(node);
			}
		});
	}
}

/**
 * Cleans up click handler when the view is destroyed
 */
function OnDestroy() {
	if (this._globalClickHandler) {
		webix.eventRemove(this._globalClickHandler);
		this._globalClickHandler = null;
	}
}

/**
 * Creates an HTML element that represents a paste target for widgets that do not have suitable elements (timeline mode)
 * @param {Object} view - a Webix data widget that is used to display events
 * @param {MouseEvent} e
 * @returns {HTMLElement}
 */
function CreatePasteTarget(view, e) {
	const scroll = view.getScrollState();
	const x = e.clientX - view.$view.offsetLeft + scroll.x;
	const y = e.clientY - view.$view.offsetTop + scroll.y;
	this.mousePosUnit = Math.floor(x / this.Scales.cellWidth);

	const target = webix.html.create("div");
	const sheight = this.GetSectionHeight();
	target.style.top = `${y - (y % sheight)}px`;
	target.style.left = `${x - (x % this.Scales.cellWidth)}px`;
	target.style.width = `${this.Scales.cellWidth}px`;
	target.style.height = `${sheight}px`;
	target.style.position = "absolute";

	// view.$view.firstChild.prepend(target); // when IE dies, replace with this line
	const parent = view.$view.firstChild;
	parent.insertBefore(target, parent.childNodes[0]);

	return target;
}

/**
 * Sets styling for a paste target and sets it to state.clipboard
 * @param {HTMLElement} node
 */
function SetPasteTargetStyle(node) {
	webix.html.addCss(node, "webix_scheduler_paste_target");
	this.State.clipboard.target = node;
}

/**
 * Removes styling from a paste target, cleans up its reference from state and removes it from the DOM if it was created specifically (timeline mode)
 */
function ClearPasteTargetStyle() {
	const clipboard = this.State.clipboard;
	if (clipboard && clipboard.target) {
		webix.html.removeCss(
			this.State.clipboard.target,
			"webix_scheduler_paste_target"
		);
		if (this.State.mode === "timeline" && clipboard.target.parentNode) {
			// this.State.clipboard.target.remove(); // when IE dies, replace with this line
			const target = clipboard.target;
			target.parentNode.removeChild(target);
		}
		this.State.clipboard.target = null;
	}
}

/**
 * Makes another element the target for pasting
 * @param {HTMLElement} node
 */
function ChangePasteTarget(node) {
	if (this.State.clipboard.target !== node) {
		this.ClearPasteTargetStyle();
		this.SetPasteTargetStyle(node);
	}
}

/**
 * Locates event node by event ID (checks the given one and locates it anew if the parent was re-rendered)
 * @param {HTMLElement} view - parent html element (top html element of a widget)
 * @param {HTMLElement} node - given html element of the event
 * @param {string} id - the ID of the event
 * @returns {HTMLElement}
 */
function locateEvent(view, node, id) {
	if (node && view.contains(node)) return node;
	return (
		view.querySelectorAll(`[webix_e_id="${id}"]`)[0] ||
		view.querySelectorAll(`[webix_l_id="${id}"]`)[0]
	);
}

/**
 * Adds the selected event to clipboard, sets mode, styles the node of the copied/cut event
 * @param {string} mode - "copy" or "cut"
 */
function AddToClipboard(view, mode) {
	const sel = this.State.selected;
	if (sel && sel.id !== "0") {
		this.ClearPasteTargetStyle();

		const source = locateEvent(view.$view, sel.node, sel.date || sel.id);
		if (source) this.SetClipboardSourceStyle(mode, source);

		const event = this.GetEvent(sel.date || sel.id);
		this.State.clipboard = {
			mode,
			selected: webix.copy(event),
			source,
		};
	}
}

/**
 * Adds relevant styling to the node of the copied or cut event
 * @param {string} mode - "copy" or "cut"
 * @param {HTMLElement} node
 */
function SetClipboardSourceStyle(mode, node) {
	const css = `webix_scheduler_${mode}`;
	if (node.className.indexOf(css) === -1) webix.html.addCss(node, css);
	const opMode = this.clipboardModes[this.clipboardModes.indexOf(mode) ^ 1];
	const prevCss = `webix_scheduler_${opMode}`;
	webix.html.removeCss(node, prevCss);

	const clipboard = this.State.clipboard;
	if (clipboard && clipboard.source && node != clipboard.source) {
		webix.html.removeCss(this.State.clipboard.source, css);
		webix.html.removeCss(this.State.clipboard.source, prevCss);
	}
}

/**
 * Locates the copied or cut event and highlights it
 * @param {Object} view - the widget that displays events
 */
function RestoreClipboardSource(view) {
	const clip = this.State.clipboard;
	if (clip) {
		const source = locateEvent(view.$view, clip.source, clip.selected.id);
		if (source) {
			if (source !== clip.source) {
				this.State.clipboard.source = source;
			}
			this.SetClipboardSourceStyle(clip.mode, source);
		}
	}
}

/**
 * Pastes the event from clipboard to the target
 * @param {KeyboardEvent} e
 */
function PasteFromClipboard(e) {
	const clipboard = this.State.clipboard;
	if (clipboard && clipboard.source) {
		const newEvent = this.GetEventData(clipboard, e);

		if (clipboard.mode === "copy") {
			this.CopyEvent(newEvent);
		} else if (clipboard.mode === "cut") {
			this.MoveEvent(newEvent);
		}

		this.PurgeClipboard();
	}
}

/**
 * Collects new event data for the pasted event based on the chosen target
 * @param {Object} clipboard - clipboard property from the global app state
 * @param {KeyboardEvent} e
 * @returns {Object}
 */
function GetEventData(clipboard, e) {
	const newEvent = clipboard.selected;

	const evLength =
		Math.floor(newEvent.end_date - newEvent.start_date) / (60 * 1000); // minutes
	newEvent.start_date = this.GetTargetDate(e.target.getAttribute("webix_l_id"));
	newEvent.end_date = webix.Date.add(
		newEvent.start_date,
		evLength,
		"minute",
		true
	);

	if (this.State.mode === "units") {
		newEvent.units = this.Unit.id;
	} else if (this.State.mode === "timeline") {
		const sec = e.target.getAttribute("webix_l_id");
		if (sec) newEvent.section = sec;
	}

	return newEvent;
}

/**
 * Saves the copy of the event
 * @param {Object} newEvent
 */
function CopyEvent(newEvent) {
	cleanEventData.call(this, newEvent);
	this.Ops.addEvent(newEvent, true).then(id =>
		this.UpdateSelection(id, newEvent)
	);
}

/**
 * Saves the event to a new target
 * @param {Object} newEvent
 */
function MoveEvent(newEvent) {
	// cut operation is either update - for normal events
	// or edit as 'this' - for recurring events
	if (newEvent.recurring) {
		const recurring = cutOccurrence(newEvent.id, newEvent);
		const updateId = newEvent.$id;
		cleanEventData.call(this, newEvent);

		// [todo] create 1 Op for updating as this because calling 2 methods (update, add) for this wasn't the best idea
		this.Ops.updateEvent(
			updateId,
			{ recurring },
			"this",
			newEvent.start_date,
			true
		).then(() =>
			this.Ops.addEvent(newEvent, true).then(id =>
				this.UpdateSelection(id, newEvent)
			)
		);
	} else {
		const update = {
			start_date: newEvent.start_date,
			end_date: newEvent.end_date,
		};
		if (newEvent.units) update.units = newEvent.units;
		if (newEvent.section) update.section = newEvent.section;
		this.Ops.updateEvent(newEvent.id, update, null, null, true).then(() => {
			this.UpdateSelection(newEvent.id, newEvent);
		});
	}
}

function PurgeClipboard() {
	const clip = this.State.clipboard;
	if (clip) {
		const { mode, source } = clip;
		webix.html.removeCss(source, `webix_scheduler_${mode}`);

		this.ClearPasteTargetStyle();
		this.State.clipboard = null;

		return false;
	}
}

/**
 *
 * @param {Object} child - the object of a JetView instance
 * @param {Object} mainWidget - the main widget within a jet view
 * @param {Object} config - some config that is passed for this view (e.g. multi: true)
 */
export default function CopyPasteMixin(child, mainWidget, config) {
	child.AddToClipboard = AddToClipboard;
	child.SetClipboardSourceStyle = SetClipboardSourceStyle;
	child.RestoreClipboardSource = RestoreClipboardSource;
	child.PasteFromClipboard = PasteFromClipboard;
	child.GetEventData = GetEventData;
	child.CopyEvent = CopyEvent;
	child.MoveEvent = MoveEvent;
	child.ClearPasteTargetStyle = ClearPasteTargetStyle;
	child.SetPasteTargetStyle = SetPasteTargetStyle;
	child.ChangePasteTarget = ChangePasteTarget;
	child.CreatePasteTarget = CreatePasteTarget;
	child.PurgeClipboard = PurgeClipboard;

	if (config && config.multi) {
		child.$multi = true;
		child.destroy = OnDestroy;
	}

	HandleCopyPaste.call(child, mainWidget);
}
