import MoreWindowView from "../month/more";
import EditRecurringMixin from "helpers/editrecurring";
import { initDnD } from "helpers/dnd";

export default class MoreView extends MoreWindowView {
	config() {
		const ui = super.config();

		ui.width = 360;
		ui.relative = "bottom";

		return ui;
	}

	init(view) {
		this.Win = view;

		EditRecurringMixin(this);

		// set dnd mode to timeline to allow dnd between more popup and timeline chart
		initDnD.call(this, view.getBody(), "timelineMore", true);
	}

	/**
	 * Show the popup with extra events
	 * @param {(string|number)} id - the ID of a section
	 * @param {HTMLElement} node - the HTML element of the section from left part of the timeline chart
	 * @param {Array<Object>} data - an array of events
	 * @returns
	 */
	ShowWindow(id, node, data) {
		if (this.ID === id && this.Win.isVisible()) return this.Win.hide();

		this.ID = id;
		data = data.map(v => this.Events.getItem(v));

		const list = this.Win.getBody();
		list.clearAll();
		list.parse(data);

		this.Win.show(node, { x: node.clientWidth }); // here we position the popup to the left of the node as well as bottom
	}
}
