import { JetView } from "webix-jet";

export default class EventActionMenu extends JetView {
	config() {
		const _ = this.app.getService("locale")._;

		return {
			view: "popup",
			body: {
				view: "menu",
				layout: "y",
				width: 220,
				autoheight: true,
				data: [
					{ id: "this", value: _("This event") },
					{ id: "next", value: _("This event and the following") },
					{ id: "all", value: _("All events") },
				],
				on: {
					onMenuItemClick: id => {
						if (this.Result) this.Result.resolve(id);
						this.Result = null;
						this.Root.hide();
					},
				},
			},
			on: {
				onHide: () => {
					if (this.Result) this.Result.reject();
					this.Result = null;
				},
			},
		};
	}

	init(view) {
		this.Root = view;
	}

	Show(node) {
		this.Result = webix.promise.defer();
		this.Root.show(node);
		return this.Result;
	}
}
