let BRIGHTNESS;
let FONT;
webix.ready(() => {
	const dark = webix.skin.$name === "contrast" || webix.skin.$name === "dark";
	BRIGHTNESS = dark ? 0.35 : 0.75;
	FONT = dark ? "#9f9d9d" : "#ffffff";
});

/**
 * Gets contrasting text color for the event background color
 * @param {string} color - background color
 * @returns {string} text color
 */
export function getContrastingColor(color) {
	const rgb = webix.color.toRgb(color);
	const brightness = Math.round(
		(rgb[0] * 299 + rgb[1] * 587 + rgb[2] * 114) / 1000
	);
	return brightness > 180 ? "#475466" : "#ffffff";
}

export function getDimmedColor(color) {
	let [hue, saturation] = hexToHsv(color);
	hue *= 0.95;
	saturation *= 0.4;
	return `rgb(${webix.color.hsvToRgb(hue, saturation, BRIGHTNESS)})`;
}

export function getDimmedFont() {
	return FONT;
}

function hexToHsv(hex) {
	const [red, green, blue] = webix.color.toRgb(hex);
	return webix.color.rgbToHsv(red, green, blue);
}
