import { JetView } from "webix-jet";

export default class AddView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const compact = this.getParam("compact", true);
		const cals = this.app.config.calendars;

		const ui = {
			view: compact ? "icon" : "button",
			type: "icon",
			icon: "wxi-plus",
			css: "webix_primary",
			label: _("Create"),
			width: compact ? 50 : cals ? 185 : 100,
			height: compact ? 50 : webix.skin.$active.inputHeight,
			on: {
				onItemClick: () => this.AddEvent(),
			},
		};

		return { view: "align", body: ui, align: "middle" };
	}

	AddEvent() {
		this.app.getState().selected = { id: "0" };
	}
}
