export function prompt(config, app) {
	const result = new webix.promise.defer();

	webix
		.ui(
			app.getService("jet-win").updateConfig({
				view: "window",
				head: {
					view: "toolbar",
					padding: { left: 12, right: 4 },
					borderless: true,
					elements: [
						{ view: "label", label: config.text },
						{
							view: "icon",
							icon: "wxi-close",
							hotkey: "esc",
							click: function() {
								result.reject("prompt cancelled");
								this.getTopParentView().close();
							},
						},
					],
				},
				body: {
					view: "form",
					padding: { top: 5, left: 20, right: 20, bottom: 20 },
					rows: [
						{
							margin: 10,
							cols: [
								{
									view: "text",
									name: "name",
									value: config.value,
									width: 230,
									css: "fm-prompt-input",
								},
								{
									view: "button",
									value: config.button,
									width: 100,
									hotkey: "enter",
									click: function() {
										const popup = this.getTopParentView();
										const newname = popup.getBody().getValues().name;
										result.resolve(newname);
										popup.close();
									},
								},
							],
						},
					],
				},
				on: {
					onShow() {
						const input = this.getBody().elements.name.getInputNode();
						input.focus();
						if (config.selectMask) config.selectMask(input);
						else input.select();
					},
				},
			})
		)
		.show();

	return result;
}
