import DayEventsView from "../day/events";
import SelectionMixin from "helpers/selection";
import EditRecurringMixin from "helpers/editrecurring";
import { initDnD } from "helpers/dnd";
import CopyPasteMixin from "helpers/copypaste";

export default class UnitEventsView extends DayEventsView {
	constructor(app, config) {
		super(app);

		if (config) {
			this.Unit = config.unit;
		}
	}
	config() {
		return {
			view: "list",
			localId: "unitList",
			css: "webix_scheduler_day_events",
			scroll: false,
			autoheight: true,
			template: "",
			type: {
				height: 42,
				css: "webix_scheduler_day_scale_item",
			},
			onClick: {
				webix_scheduler_day_event: e => this.ShowEvent(e),
			},
			onDblClick: {
				webix_scheduler_day_scale_item: (_e, h) => this.ShowNew(h),
			},
		};
	}
	init() {
		this.State = this.app.getState();
		this.List = this.$$("unitList");

		SelectionMixin(this, "scale");
		EditRecurringMixin(this);

		this.List.$setSize = function(x, y) {
			if (webix.ui.view.prototype.$setSize.call(this, x, y)) {
				this.render();
			}
		};

		this.minEventHeight = 62;
		this.ParseHours();

		this.on(this.State.$changes, "readonly", () => this.List.render());

		this.List.attachEvent("onAfterRender", () => this.RenderEvents());
		initDnD.call(this, this.List, "units");

		const compact = this.getParam("compact", true);
		if (this.app.config.copypaste && !webix.env.mobile && !compact)
			CopyPasteMixin(this, this.List);
	}

	FilterToday(value, to) {
		const dayEvents = super.FilterToday(value, to);

		return dayEvents.filter(obj => {
			if (obj.units) {
				const units = obj.units.split(",");
				const unitID = this.Unit.id + "";
				return units.indexOf(unitID) !== -1;
			}
			return false;
		});
	}

	ShowNew(h) {
		const state = this.State;
		if (!state.readonly && !state.clipboard) {
			const date = webix.Date.copy(this.Day || state.date);
			const unit = this.Unit.id;
			date.setHours(h);
			state.selected = { id: "0", date, unit };
		}
	}
}
