import { JetView } from "webix-jet";

export default class OtherSettingsView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const groupByMode = {
			cols: [
				{
					view: "switch",
					localId: "groupBySwitch",
					css: "webix_rpt_title",
					width: 65,
					on: {
						onChange: v => this.ToggleGroupBy(v),
					},
				},
				{
					view: "label",
					label: _("Group"),
				},
			],
		};
		const buckets = {
			cols: [
				{
					view: "switch",
					localId: "bucketsSwitch",
					css: "webix_rpt_title",
					width: 65,
					on: {
						onChange: v => this.ToggleBuckets(v),
					},
				},
				{
					view: "label",
					label: _("Data buckets"),
				},
			],
		};
		const sortByMode = {
			cols: [
				{
					view: "switch",
					localId: "sortBySwitch",
					css: "webix_rpt_title",
					width: 65,
					on: {
						onChange: v => this.ToggleSortBy(v),
					},
				},
				{
					view: "label",
					label: _("Sort"),
				},
			],
		};

		return {
			view: "scrollview",
			body: {
				type: "form",
				rows: [
					groupByMode,
					{ $subview: "_hidden", name: "group" },
					buckets,
					{ $subview: "_hidden", name: "buckets" },
					sortByMode,
					{ $subview: "_hidden", name: "sort" },
					{ gravity: 0.000001 },
				],
			},
		};
	}

	init() {
		this.State = this.getParam("state", true);
		this.$$("groupBySwitch").setValue(!!this.State.group);
		this.$$("sortBySwitch").setValue(!!this.State.sort);
		this.$$("bucketsSwitch").setValue(!!this.State.buckets);
	}

	ToggleGroupBy(v) {
		if (v) {
			this.show("./editor.other.groups", { target: "group" });
		} else {
			this.State.group = null;
			this.show("_hidden", { target: "group" });
		}
	}

	ToggleSortBy(v) {
		if (v) {
			this.show("./editor.other.sorts", { target: "sort" });
		} else {
			this.State.sort = null;
			this.show("_hidden", { target: "sort" });
		}
	}
	ToggleBuckets(v) {
		if (v) {
			this.show("./editor.other.buckets", { target: "buckets" });
		} else {
			this.State.buckets = null;
			this.show("_hidden", { target: "buckets" });
		}
	}
}
