import { JetView } from "webix-jet";
import { bind } from "jet-restate-bind";

export default class SeriesPropertiesView extends JetView {
	constructor(app, data, chartState) {
		super(app);
		this.Series = data;
		this.ChartState = chartState;
	}

	config() {
		const _ = this.app.getService("locale")._;
		this.Charts = this.app.getService("charts");
		const inputs = [
			{
				labelAlign: "right",
				label: _("Title"),
				view: "text",
				name: "name",
			},
			{
				labelAlign: "right",
				label: _("Color"),
				view: "colorpicker",
				name: "color",
				css: "webix_rpt_colorpicker",
				suggest: {
					padding: 0, //for Flat skins
					type: "colorselect",
					body: {
						button: true,
					},
				},
			},
			{
				labelAlign: "right",
				label: _("Marker type"),
				view: "richselect",
				css: "webix_rpt_markerselect",
				name: "markerType",
				options: {
					padding: 0, //for Flat skins
					body: {
						css: "webix_rpt_markerselect_list",
						template: item => this.ItemListTemplate(item),
						data: this.Charts.getItemTypes(),
					},
				},
				batch: "marker",
			},
			{
				labelAlign: "right",
				label: _("Fill marker"),
				view: "checkbox",
				name: "fillMarker",
				batch: "marker",
			},
		];

		return {
			view: "form",
			borderless: true,
			elementsConfig: {
				labelWidth: 170,
			},
			visibleBatch: "default",
			rows: inputs,
		};
	}

	init() {
		bind(this.Series, "*", this.getRoot());
		if (this.Charts.itemSupport.indexOf(this.ChartState.chartType) > -1)
			this.getRoot().showBatch("marker");

		this.on(this.Series.$changes, "color", () => {
			const markerType = this.getRoot().elements["markerType"];
			markerType.refresh();
			markerType.getList().refresh();
		});
	}

	ItemListTemplate(item) {
		return `<div class="webix_rpt_config_row">${item.value} <span class='webix_icon ${item.icon}' style='color:${this.Series.color}'></span></div>`;
	}
}
