import { JetView } from "webix-jet";
import DayEventsView from "../day/events";
import WeekHeaderView from "./header";
import WeekEventsView from "./compact";
import HourscaleView from "../common/hourscale";
import MultiDayEventsView from "./multiday";

import { isMultiDay } from "helpers/dates";

export default class WeekView extends JetView {
	config() {
		this.Compact = this.getParam("compact", true);

		if (this.Compact) {
			return {
				rows: [WeekEventsView],
			};
		} else {
			const days = {
				localId: "scroll",
				view: "scrollview",
				scroll: "y",
				css: "webix_scheduler_week_days",
				body: {
					cols: [HourscaleView],
				},
			};

			const weekLength = 7;
			for (let i = 0; i < weekLength; ++i) {
				days.body.cols.push(
					new (this.app.dynamic(DayEventsView))(this.app, {
						day: i,
					})
				);
			}

			return {
				rows: [
					WeekHeaderView,
					{ localId: "multi", hidden: true, cols: [MultiDayEventsView] },
					days,
				],
			};
		}
	}
	init() {
		const state = this.app.getState();

		this.on(state.$changes, "date", value => this.RefreshData(value));
		this.on(state.$changes, "active", () => this.RefreshData(state.date));
		this.on(this.app, "events:refresh", () => this.RefreshData(state.date));

		// updates made in form
		const events = this.app.getService("local").events(true);
		this.on(
			events.data,
			"onStoreUpdated",
			(_i, _o, mode) => mode && this.RefreshData(state.date)
		);

		if (!this.Compact) {
			this.on(state.$changes, "selected", v => v && this.ScrollScale(v));
		}
	}

	RefreshData(date) {
		const start = webix.Date.weekStart(date);
		const end = webix.Date.add(start, 1, "week", true);
		this.GetWeek(start, end).then(data => {
			// this.app will be null if compact trigger was called in main.js
			if (this.app) this.setParam("data", data, true);

			if (data.multi.length) {
				this.$$("multi").show();
			} else this.$$("multi").hide();
		});
	}

	GetWeek(start, end) {
		return this.app
			.getService("local")
			.getEvents(start, end)
			.then(evs => {
				if (this.Compact) {
					return { start, end, all: evs };
				}

				const multi = [],
					single = [];

				evs.forEach(ev => {
					if (isMultiDay(ev)) {
						multi.push(ev);
					} else {
						single.push(ev);
					}
				});

				return { start, end, multi, single };
			});
	}

	ScrollScale(v) {
		if (v.id === "0" && !v.date) {
			setTimeout(() => {
				const y = (new Date().getHours() + 2) * 40;
				this.$$("scroll").scrollTo(0, y);
			}, 100);
		}
	}
}
