import { JetView } from "webix-jet";
import { types } from "../../../helpers/icons";

export default class StructureView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const tabbar = {
			view: "richselect",
			localId: "select",
			borderless: true,
			align: "left",
			inputWidth: 170,
			options: [
				{
					id: "table",
					value: types.table + " " + _("Table"),
				},
				{
					id: "chart",
					value: types.chart + " " + _("Chart"),
				},
				{
					id: "heatmap",
					value: types.heatmap + " " + _("Heatmap"),
				},
			],
			on: {
				onChange: v => this.ChangeReportType(v),
			},
		};

		const toolbar = {
			view: "form",
			padding: {
				left: webix.skin.$active.layoutPadding.form,
				top: 5,
				bottom: 0,
			},
			scroll: false,
			cols: [tabbar],
		};

		const ui = {
			width: 480,
			hidden: false,
			localId: "settings",
			type: "clean",

			rows: [toolbar, { $subview: true }],
		};

		this.Local = this.app.getService("local");
		return ui;
	}

	init() {
		this.State = this.getParam("state", true);

		this.on(this.State.$changes, "type", v => {
			this.show("editor.structure." + v);
		});
		this.$$("select").setValue(this.State.type);
	}

	ChangeReportType(type) {
		if (this.State.type != type) this.State.$batch({ type, meta: {} });
	}
}
