import { JetView } from "webix-jet";
import { createState } from "jet-restate";
import Toolbar from "./toolbar";

const typeToSort = {
	number: "int",
	date: "date",
	text: "string",
	boolean: "number",
	picklist: "text",
	reference: "text",
};

export default class TableView extends JetView {
	config() {
		const page = {
			type: "wide",
			margin: 5,
			cols: [{}],
		};

		return { rows: [Toolbar, page] };
	}

	init() {
		this.State = this.getParam("state", true);
		this.LocalState = createState({ search: "" });
		this.setParam("state", this.LocalState);

		this.Local = this.app.getService("local");
	}
	ready() {
		const parts = this.getRoot().getChildViews();
		this.Bar = parts[0];
		this.Host = parts[1];

		this.on(this.State.$changes, "module", mod => this.Show(mod));
		this.on(this.app, "exportModule", mode => this.Export(mode));
		this.on(this.LocalState.$changes, "search", v => this.Find(v));
	}

	Export(mode) {
		const params = {
			ignore: { $selection: true },
			plainOutput: true,
			filterHTML: true,
		};
		if (this.Table) {
			if (mode == "excel") webix.toExcel(this.Table, params);
			else if (mode == "csv") webix.toCSV(this.Table, params);
		}
	}

	Show(mod) {
		const host = this.Host;
		if (mod && mod.columns.length && mod.type === "table") {
			const config = this.GetTableConfig(mod);
			if (!config) return;

			this.Bar.show();
			webix.ui([config], host);
			const table = (this.Table = host.getChildViews()[0]);

			this.GetTableData(mod).then(([data, options]) => {
				// table already reconstructed on moment of data loading
				if (table.$destructed) return;
				options.forEach(({ field, data }) => {
					if (!this.IsBucketColumn(field))
						table.getColumnConfig(field).collection.parse(data);
				});
				table.parse(data);
			});
		} else {
			webix.ui([{}], host);
			this.Table = null;
			this.Bar.hide();
		}
	}
	IsBucketColumn(field, module) {
		module = module || this.State.module;
		return module.buckets && module.buckets.find(b => b.column == field);
	}
	GetTableConfig(mod) {
		const { groups, columns, meta } = mod;
		if (!columns) return null;
		let dateColumns = [];
		const gColumns = columns.map(a => {
			if (a.type == "date") dateColumns.push(a.id);
			return this.GetColumnConfig(a);
		});

		const edit = this.State.mode === "edit";
		const grid = {
			view: groups ? "treetable" : "datatable",
			columns: gColumns,
			resizeColumn: edit ? { headerOnly: true } : false,
			leftSplit: meta.freeze ? meta.freeze : 0,
			css: edit ? "webix_header_border" : "",
			on: {
				onColumnResize: (id, width, old, byUser) =>
					this.SetColWidth(id, width, old, byUser),
			},
		};

		if (dateColumns.length)
			grid.scheme = {
				$change: row =>
					dateColumns.forEach(cId => (row[cId] = new Date(row[cId]))),
			};

		return grid;
	}

	GetColumnConfig(a) {
		let column = {
			id: a.id,
			header:
				!a.meta.header || a.meta.header === "none"
					? a.meta.name || a.name
					: [
							a.meta.name || a.name,
							{
								content:
									a.meta.header === "text" ? "textFilter" : "richSelectFilter",
							},
					  ],
			type: a.type,
			sort: typeToSort[a.type],
			width: a.width || 200,
		};

		if (a.type === "date") column.format = webix.i18n.dateFormatStr;
		if (a.type === "boolean") {
			const _ = this.app.getService("locale")._;
			column.template = obj => (obj[a.id] == 1 ? _("True") : _("False"));
		}
		if (
			(a.type === "reference" || a.type === "picklist") &&
			!this.IsBucketColumn(a.id)
		)
			column.options = [];

		return column;
	}

	SetColWidth(id, width, old, byUser) {
		if (byUser) {
			if (id === "$selection" || id === "$subrow") {
				const table = this.Tbl;
				table.blockEvent();
				table.setColumnWidth(id, old);
				table.unblockEvent();
				return;
			}

			this.app.callEvent("onColumnResize", [id, width]);
		}
	}

	GetTableData(mod) {
		return Promise.all([
			this.Local.getData(
				this.Local.getDataConfig(mod),
				this.State.mode == "edit"
			),
			this.Local.getOptions(mod.columns),
		]);
	}

	Find(value) {
		const h = this.app.getService("helpers");
		const table = this.Table;
		let result = [];
		if (!table) return;
		table.clearCss("webix_rpt_found_record", true);

		if (value) {
			const ignoreDateNumber = h.hasNonDigits(value);
			const regexp = new RegExp(h.wildToRegexp(value), "gi");
			const fields = table.config.columns.filter(
				c =>
					!(c.id === "$selection" || c.id === "$subrow") &&
					(!ignoreDateNumber || !(c.type === "number" || c.type === "date"))
			);

			const matchers = [];

			for (let i = 0; i < fields.length; ++i) {
				const type = fields[i].type || "text";
				const colId = fields[i].id;
				if (type === "text" || type === "number") {
					matchers.push(o => regexp.test(o[colId]));
				} else if (type === "reference" || type === "picklist") {
					const collection = table.getColumnConfig(colId).collection;

					const optionsMatch = h.findOptions(collection, regexp);
					matchers.push(h.matchPerColumn(colId, a => optionsMatch[a]));
				} else if (type === "bool") {
					switch (value) {
						case "true":
							matchers.push(o => o[colId]);
							break;
						case "false":
							matchers.push(o => !o[colId]);
							break;
					}
				} else if (type === "date") {
					const dateFormats = table.getColumnConfig(colId).format;
					matchers.push(o => regexp.test(dateFormats(o[colId])));
				}
			}

			let found = false;
			result = table.find(obj => {
				for (let i = 0; i < matchers.length; ++i) {
					if (matchers[i](obj)) {
						if (!found) {
							found = true;
							table.showItem(obj.id);
						}
						table.addCss(obj.id, "webix_rpt_found_record", true);
						return true;
					}
				}
				return false;
			});
		}
		table.refresh();
		this.app.callEvent("onRecordsSearch", [value, result]);
	}
}
