import { JetView } from "webix-jet";

import "@xbs/query/codebase/query";

export default class QueryView extends JetView {
	config() {
		this.State = this.getParam("state", true);
		const local = this.app.getService("local");

		return {
			view: "query",
			localId: "query",
			css: "webix_rpt_query",
			locale: { lang: this.app.getService("locale").getLang() },
			data: field => {
				const column = this.State.fields.find(a => a.id === field);
				if (column.type === "boolean") return [0, 1];
				return local.getFieldData(field, column.type);
			},
			fields: this.State.fields
				.filter(a => a.type !== "reference" && a.type !== "picklist")
				.map(a => {
					const obj = {
						id: a.id,
						value:
							a.name +
							" " +
							(a.model
								? "<span class='webix_rpt_sources_path'>" + a.model + "</span>"
								: ""),
						type: a.type,
					};

					if (obj.type === "boolean") {
						obj.conditions = ["equal"];
						obj.type = "number";
					}

					return obj;
				}),
		};
	}

	init() {
		this.State = this.getParam("state", true);

		const query = this.getRoot();

		this.on(this.State.$changes, "query", value => {
			query.$app.callEvent("applyFilter", [null]);
			query.config.value = value ? fixDates(JSON.parse(value)) : null;
		});
		query.getState().$observe("value", v => {
			this.State.query = v ? JSON.stringify(v) : "";
		});
	}
}

function fixDates(value) {
	if (value.rules) value.rules.forEach(fixDates);
	else {
		if (value.type === "date") {
			var c = value.condition;
			if (c.filter && typeof value.condition.filter === "string")
				c.filter = new Date(c.filter);
			if (c.filter.start && typeof c.filter.start === "string")
				c.filter.start = new Date(c.filter.start);
			if (c.filter.end && typeof c.filter.end === "string")
				c.filter.end = new Date(c.filter.end);
		}
	}

	return value;
}
