import { JetView } from "webix-jet";
import BarView from "./bar";
import ScaleView from "./scale";
import ChartView from "./chart";

import {
	addUnit,
	resetScales,
	getUnitStart,
	smallerCount,
} from "@xbs/webix-gantt-lib";

import { createState } from "jet-restate";

export default class TimelineView extends JetView {
	config() {
		const ui = {
			rows: [BarView, ScaleView, ChartView],
		};

		return ui;
	}

	init() {
		const state = (this.State = this.getParam("state", true));

		const innerState = (this.InnerState = createState({
			top: 0,
			left: 0,
		}));
		this.setParam("innerState", innerState, true);

		this.on(state.$changes, "date", v => this.SetScales(v, state.timelineMode));

		this.on(state.$changes, "timelineMode", (t, o) => {
			if (o) this.SetScales(state.date, t);
		});
	}

	SetScales(date, type) {
		const scales = this.ResetScales(type, date);
		this.setParam("scales", scales, true);
	}

	GetScalesArray(type) {
		switch (type) {
			case "day":
				return [{ unit: "hour", format: "%H:00" }];
			case "week":
				return [{ unit: "day", step: 1, format: "%d" }];
			case "month":
				return [
					{
						unit: "week",
						format: function(start) {
							const parser = webix.Date.dateToStr("%d %M");
							const wstart = webix.Date.weekStart(start);
							const wend = webix.Date.add(
								webix.Date.add(wstart, 1, "week", true),
								-1,
								"day",
								true
							);
							return parser(wstart) + " - " + parser(wend);
						},
					},
				];
			default: {
				const unit = this.GetDefaultScaleUnit(type);
				return [
					{
						unit,
						format: (start, end) => {
							const parser = webix.Date.dateToStr("%d %M %YYYY");
							return parser(start) + " - " + parser(end);
						},
					},
				];
			}
		}
	}

	GetScalesCellWidth(type) {
		switch (type) {
			case "day":
				return 80;
			case "week":
				return 300;
			case "month":
				return 400;
			default:
				return 100;
		}
	}

	GetScalePrecision(type) {
		switch (type) {
			case "day":
				return false;
			case "week":
				return false;
			case "month":
				return true;
			default:
				return true;
		}
	}

	/* Method wrappers for gantt-lib functions */

	AddUnit(unit, date, step) {
		return addUnit(unit)(date, step);
	}

	GetUnitStart(unit, date) {
		return getUnitStart(unit, date);
	}

	ResetScales(unit, date) {
		const start = this.GetUnitStart(unit, date);
		const end = this.AddUnit(unit, start, 1);
		const active = webix.skin.$active;
		return resetScales(
			start,
			end,
			this.GetScalePrecision(unit),
			this.GetScalesCellWidth(unit),
			active.barHeight - active.borderWidth * 2,
			this.GetScalesArray(unit)
		);
	}

	GetDefaultScaleUnit(type) {
		return smallerCount[type][0] || type;
	}
}
