import { JetView } from "webix-jet";

export default class NavBarView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const compact = this.getParam("compact", true);
		const units = !!this.app.config.units;

		const toolbar = {
			hidden: compact,
			view: "tabbar",
			bottomOffset: 0,
			localId: "buttons",
			borderless: true,
			width: 320,
			optionWidth: 80,
			options: [
				{ id: "day", value: _("Day") },
				{ id: "week", value: _("Week") },
				{ id: "month", value: _("Month") },
				{ id: "agenda", value: _("Agenda") },
				{ id: "year", value: _("Year") },
			],
		};

		if (this.app.config.timeline) {
			toolbar.width += 80;
			toolbar.options.push({ id: "timeline", value: _("Timeline") });
		}

		if (!compact) {
			toolbar.on = { onChange: val => this.SetMode(val) };
		}

		if (units) {
			toolbar.width += 80;
			toolbar.options.push({ id: "units", value: _("Units") });
		}

		return toolbar;
	}

	init() {
		this.State = this.app.getState();
		this.on(this.State.$changes, "mode", val => {
			this.$$("buttons").setValue(val);
		});
	}
	SetMode(val) {
		this.State.$batch({
			mode: val,
			selected: null,
		});
	}
}
