import { JetView } from "webix-jet";

export default class ScalesView extends JetView {
	config() {
		const compact = this.getParam("compact", true);
		return {
			cols: [
				{
					width: compact ? 80 : 200,
					css: "webix_scheduler_timeline_space",
					borderless: false,
				},
				{
					view: "template",
					localId: "scale",
					height: 20,
					css: "webix_scheduler_timeline_scale",
				},
			],
		};
	}

	init() {
		const scale = this.$$("scale");
		const innerState = (this.InnerState = this.getParam("innerState", true));
		this.on(innerState.$changes, "left", x => scale.scrollTo(x, null));
	}

	urlChange() {
		this.RenderScales();
	}

	/**
	 * Renders gantt scales
	 */
	RenderScales() {
		const scales = this.getParam("scales", true);
		const view = this.$$("scale");
		const html = scales.rows
			.map(line => {
				return (
					`<div class="webix_scheduler_timeline_scale_row" style='height:${line.height}px;width:${scales.width}px'>` +
					line.cells.map(cell => this.CellTemplate(cell, line.type)).join("") +
					"</div>"
				);
			})
			.join("");

		view.config.height = scales.height;
		view.setHTML(html);
		view.resize();
	}

	/**
	 * Returns the template string for a scale unit
	 * @param {Object} cell - a scale unit
	 * @returns {string} - the template string of the scale cell
	 */
	CellTemplate(cell) {
		const css = `webix_scheduler_timeline_scale_cell ${cell.css}`;
		return `<div class="${css}" style="width:${cell.width}px;">${cell.format(
			cell.date
		)}</div>`;
	}

	DateIsToday(date) {
		const today = webix.Date.dayStart(new Date());
		return webix.Date.equal(today, webix.Date.dayStart(date));
	}
}
