import { JetView } from "webix-jet";
import { createState } from "jet-restate";
import { bind } from "jet-restate-bind";
import { prompt } from "../../helpers/prompt";
import "../../helpers/queryselect";

import Query from "./query";

export default class QueryBuilderView extends JetView {
	config() {
		const _ = (this._ = this.app.getService("locale")._);
		const querySelect = {
			localId: "qblock",
			type: "clean",
			rows: [
				{
					type: "form",
					margin: 5,
					padding: { bottom: 5 },
					rows: [
						{
							localId: "queryBar",
							visibleBatch: "def",
							cols: [
								{ view: "label", label: _("Filtering query"), width: 120 },
								{},
								{
									batch: "query",
									view: "icon",
									localId: "delete",
									icon: "rpi-delete",
									tooltip: _("Delete"),
									click: () => this.DeleteQuery(),
								},
								{
									batch: "query",
									view: "icon",
									localId: "copy",
									icon: "rpi-content-copy",
									tooltip: _("Create copy"),
									click: () => this.CopyQuery(),
								},
								{
									batch: "query",
									view: "icon",
									localId: "rename",
									icon: "rpi-pencil",
									tooltip: _("Rename"),
									click: () => this.RenameQuery(),
								},

								{
									view: "button",
									disabled: true,
									css: "webix_primary",
									localId: "save",
									label: _("Save"),
									tooltip: _("Save query"),
									width: 150,
									click: () => this.SaveQuery(null),
								},
							],
						},

						{
							view: "query-select",
							options: [],
							name: "id",
							css: "webix_rpt_qselect",
							localId: "qselect",
							suggest: {
								css: "webix_rpt_qselect_suggest",
								template: item => this.QSelectTemplate(item),
								body: {
									minHeight: 25,
								},
								on: {
									onShow: function() {
										if (
											!this.getBody().$view.firstChild.getAttribute(
												"placeholder"
											)
										)
											this.getBody().$view.firstChild.setAttribute(
												"placeholder",
												_("No saved queries")
											);
									},
								},
							},
							on: {
								onClearClick: () => this.NewQuery(),
							},
						},

						{
							autoheight: true,
							borderless: true,
							template: _("Save filter to use it in other reports"),
							css: "webix_rpt_description",
						},
					],
				},
				Query,
			],
		};

		return querySelect;
	}

	init() {
		this.Local = this.app.getService("local");
		this.Operations = this.app.getService("operations");
		const state = (this.State = this.getParam("state", true));
		const qData = (this.Queries = this.Local.getQueries());

		this.LocalState = createState({ id: "", saved: true });

		qData.waitData.then(() => {
			const now = this.State.query;
			const ctrl = this.$$("qselect");
			const list = ctrl.getList();

			const tables = { [this.State.data]: 1 };
			this.State.joins.forEach(a => (tables[a.tid] = 1));

			qData.filter(a => {
				let res = true;
				a.models.forEach(m => {
					if (!tables[m]) res = false;
				});
				return res;
			});
			list.sync(qData);

			qData.data.each(v => {
				if (v.text == now) ctrl.setValue(v.id);
			});

			this.on(state.$changes, "query", v => {
				const id = this.LocalState.id;
				const changed = (!id && v) || (id && qData.getItem(id).text != v);
				this.LocalState.saved = !changed;
			});
		});

		bind(this.LocalState, "id", this.$$("qselect"), this);

		const saveBtn = this.$$("save");
		this.on(this.LocalState.$changes, "saved", v => {
			if (v) saveBtn.disable();
			else saveBtn.enable();
		});

		this.on(this.LocalState.$changes, "id", v => {
			if (v) {
				const item = qData.getItem(v);
				this.State.query = item.text;
				this.$$("queryBar").showBatch("query");
			} else {
				this.$$("queryBar").showBatch("def");
			}
		});
	}

	NewQuery() {
		this.LocalState.id = null;
		this.LocalState.saved = true;
		this.State.query = null;
	}

	SaveQuery(name) {
		const id = this.LocalState.id;
		const text = this.State.query;

		if (id) {
			if (name === null) name = this.Queries.getItem(id).name;
			this.Operations.updateQuery(id, { name, text }).then(() => {
				this.$$("qselect").setValue(id);
				this.LocalState.saved = true;
			});
			return;
		}

		prompt(
			{
				text: this._("Enter query name"),
				button: this._("Save"),
				value: "",
			},
			this.app
		).then(name => {
			if (name) {
				this.Operations.addQuery({ name, text }).then(id => {
					this.LocalState.saved = true;
					this.LocalState.id = id;
				});
			}
		});
	}

	RenameQuery() {
		const id = this.LocalState.id;
		const { name } = this.Queries.getItem(id);

		prompt(
			{
				text: this._("Enter query name"),
				value: name,
				button: this._("Save"),
			},
			this.app
		).then(name => {
			if (!name) return;

			this.SaveQuery(name);
		});
	}

	CopyQuery() {
		const _ = this.app.getService("locale")._;
		let { name, text } = this.Queries.getItem(this.LocalState.id);
		name = _("Copy of") + " " + name;
		this.Operations.addQuery({ name, text }).then(id => {
			this.LocalState.id = id;
		});
	}

	DeleteQuery() {
		const _ = this.app.getService("locale")._;
		webix
			.confirm({
				container: this.app.getRoot().$view,
				title: _("Delete"),
				ok: _("Delete"),
				cancel: _("Cancel"),
				text: _("Are you sure to delete this query?"),
			})
			.then(() => {
				const id = this.LocalState.id;
				this.Operations.deleteQuery(id).then(() => {
					this.LocalState.$batch({ saved: true, id: null });
					this.State.query = null;
				});
			});
	}

	GetMenuOptions() {
		return [
			{ id: "copy", value: "Copy", icon: "rpi-content-copy" },
			{ id: "rename", value: "Rename", icon: "rpi-pencil" },
			{ id: "delete", value: "Delete", icon: "rpi-delete" },
		];
	}

	GetMenuTemplate(obj) {
		return `<span class='webix_icon ${obj.icon}'></span>${obj.value}`;
	}

	ApplyQueryAction(id) {
		this.QueryMenu.hide();
		switch (id) {
			case "rename":
				this.RenameQuery();
				break;
			case "copy":
				this.CopyQuery();
				break;
			case "delete":
				this.DeleteQuery();
				break;
		}
	}

	QSelectTemplate(obj) {
		return `${obj.value} <span class='webix_icon wxi-close webix_rpt_qselect_icon'></span>`;
	}
}
