export function waitVisible(view){
	const promise = webix.promise.defer();

	const interval = setInterval(()=> {
		const destructed = view.$destructed;
		if(destructed || view.isVisible()){
			if(destructed)
				promise.reject();
			else
				promise.resolve();
			clearInterval(interval);
		}
	}, 300);

	return promise;
}