import { JetView } from "webix-jet";
import {
	smallerCount,
	getUnitStart,
	grid,
	updateTask,
	addUnit,
} from "@xbs/webix-gantt-lib";
import { dayEventColor } from "helpers/templates";
import { initDnD, switchCursor, locateEvent } from "helpers/dnd";
import { getParams } from "helpers/recurring";
import SelectionMixin from "helpers/selection";
import EditRecurringMixin from "helpers/editrecurring";
import MoreWindowView from "./more";
import CopyPasteMixin from "helpers/copypaste";

export default class ChartView extends JetView {
	config() {
		const height = this.GetSectionHeight();
		const compact = this.getParam("compact", true);

		return {
			cols: [
				{
					view: "treetable",
					localId: "sections",
					css: "webix_scheduler_timeline_sections",
					width: compact ? 80 : 200,
					header: false,
					scroll: "y",
					rowHeight: height,
					scrollAlignY: false,
					columns: [
						{
							id: "text",
							header: "",
							fillspace: true,
						},
					],
				},
				{
					view: "list",
					localId: "list",
					type: { height, template: "" },
					css: `webix_scheduler_timeline${
						webix.env.touch ? "_touch" : ""
					}_bars`,
					onClick: {
						webix_scheduler_timeline_event: e => this.ShowEvent(e),
					},
					onDblClick: {
						webix_list_item: (e, id) => this.ShowNew(e, id),
					},
					tooltip: {
						template: "",
					},
					scroll: "xy",
				},
			],
		};
	}

	init() {
		this.Tree = this.$$("sections");
		this.Bars = this.$$("list");
		this.State = this.getParam("state", true);
		this.InnerState = this.getParam("innerState", true);
		this.Data = this.app.getService("local");
		this.Events = new webix.DataCollection({
			scheme: {
				$change: obj => this.RefreshTask(obj.id),
			},
		});

		this.MoreWindow = this.ui(
			new (this.app.dynamic(MoreWindowView))(this.app, {
				events: this.Events,
			})
		);

		SelectionMixin(this, "month");
		EditRecurringMixin(this);

		this._sectionsReady = this.Data.sections().then(data => {
			this.Sections = data;
			this.Tree.parse(data);
			this.Bars.parse(data);
		});

		this.HandleScroll();

		// updates made in form
		const events = this.app.getService("local").events(true);
		this.on(
			events.data,
			"onStoreUpdated",
			(_i, _o, mode) => mode && this.RefreshData()
		);
		// calendars
		this.on(this.app, "events:refresh", () => this.RefreshData());
		this.on(this.State.$changes, "active", (_v, o) => o && this.RefreshData());

		initDnD.call(this, this.Bars, "timeline");
		this._mousemove_handler = webix.event(this.Bars.$view, "pointermove", e => {
			if (e.pointerType == "mouse") switchCursor(e, this.Bars.$view);
		});

		const compact = this.getParam("compact", true);
		if (this.app.config.copypaste && !webix.env.mobile && !compact)
			CopyPasteMixin(this, this.Bars);
	}

	urlChange() {
		const scales = (this.Scales = this.getParam("scales", true));
		this.DrawGrid(scales);
		this.RefreshData(scales);
	}

	GetSectionHeight() {
		const active = webix.skin.$active;
		this._sectionSize = 5;
		return (active.barHeight - active.borderWidth * 2) * this._sectionSize;
	}

	/**
	 * Reloads data into timeline
	 * @param {Object} scales - current chart scales
	 */
	RefreshData(scales) {
		if (!scales) scales = this.Scales;
		webix.promise
			.all([this._sectionsReady, this.Data.getEvents(scales.start, scales.end)])
			.then(res => {
				this.ClearCollections();

				this.Events.parse(res[1]);
				this.RenderEvents();
			});
	}

	ClearCollections() {
		this.Events.clearAll();
		this._processed = [];
		this._lastRow = {};
		this._moreEvents = {};
	}

	/**
	 * Updates task object with new coordinates and allocates it to the correct position inside a section
	 * @param {(string|number)} updID - the ID of an event
	 */
	RefreshTask(updID) {
		const t = this.GetEvent(updID);

		const sectionIndex = indexOf(this.Sections.data.order, t.section);
		if (sectionIndex == -1) {
			t.$hide = true;
			return;
		}

		const type = this.State.timelineMode;
		let unit = this.GetUnitCount(type);
		if (type !== "day" || type !== "week") unit = this.GetUnitCount(unit);

		const prev = this._processed.find(obj => {
			if (obj.section == t.section) {
				if (type === "day")
					return this.CheckDates(obj.end_date, t.start_date, obj.all_day);

				const prev = webix.Date.timePart(obj.end_date)
					? this.GetUnitStart(unit, webix.Date.add(obj.end_date, 1, unit, true))
					: obj.end_date;
				const next = this.GetUnitStart(unit, t.start_date);
				return this.CheckDates(prev, next, obj.all_day);
			}
			return false;
		});

		let y;
		if (prev) {
			y = prev.$y;
			this._processed.splice(
				this._processed.findIndex(p => prev.id == p.id),
				1
			);
		} else {
			if (webix.isUndefined(this._lastRow[t.section]))
				this._lastRow[t.section] = 0;
			else this._lastRow[t.section]++;

			if (this._lastRow[t.section] >= this._sectionSize - 1) t.$more = true;
			else t.$more = false;
		}

		this.UpdateTask(t, sectionIndex, y);

		this._processed.push(t);
		this._processed.sort((a, b) => a.$y - b.$y);
	}

	/**
	 * Checks if the start of one event follows the end of the previous one
	 * @param {Date} prev - the end date of the previous event
	 * @param {Date} next - the start date of the next event
	 * @param {Boolean} all_day - the flag that indicates that the previous event is all-day (which means that the end date is really 1 day later)
	 * @returns {Boolean} true if the start follows the end, false otherwise
	 */
	CheckDates(prev, next, all_day) {
		return (all_day ? webix.Date.add(prev, 1, "day", true) : prev) <= next;
	}

	/**
	 * Creates a layer with events
	 */
	RenderEvents() {
		if (this._DataObj) {
			this.Bars.$view.firstChild.removeChild(this._DataObj);
			delete this._DataObj;
		}

		// No Events, No Render
		if (!this.Events.count() || !this.Bars.count()) return;

		const container = webix.html.create("div", {
			role: "presentation",
		});
		let html = "";
		const _ = this.app.getService("locale")._;
		this.Events.data.each(obj => {
			if (!obj.$hide) {
				if (!obj.$more || this._lastRow[obj.section] == 4) {
					html += this.BarsTemplate(obj, _);
				} else {
					if (!this._moreEvents[obj.section]) {
						this._moreEvents[obj.section] = [];

						html += this.MoreBarTemplate(obj, _);
					}
					this._moreEvents[obj.section].push(obj.id);
				}
			}
		});
		container.innerHTML = html;
		this._DataObj = this.Bars.$view.firstChild.appendChild(container);
		this.app.callEvent("events:rendered", []);
	}

	/**
	 * Paints a grid
	 * @param {Object} scales - scales object as returned by this.Local.getScales()
	 */
	DrawGrid(scales) {
		const view = this.Bars;
		const colors = { contrast: "#808080", dark: "#384047" };
		view.$view.style.backgroundImage = `url(${this.GetGrid(
			scales,
			colors[webix.skin.$name] || "#ddd"
		)})`;
		view.$view.style.marginTop = "0px";

		// sync grid to scroll position
		view.$view.style.backgroundPosition = `-${this.InnerState.left}px -${this.InnerState.top}px`;

		this.Resize(scales.width);
	}

	/**
	 * Changes the width of the root component (list with bars)
	 * @param {number} width
	 */
	Resize(width) {
		const area = this.Bars.$view.querySelector(".webix_scroll_cont");
		area.style.width = width + "px";
		area.style.minHeight = "1px";
	}

	/**
	 * Handles scrolling
	 */
	HandleScroll() {
		this.InnerState = this.getParam("innerState", true);

		this._scroll_handler = webix.event(this.Bars.$view, "scroll", ev => {
			const bars = ev.target;
			const top = Math.round(bars.scrollTop);
			const left = Math.round(bars.scrollLeft);
			this.Bars.$view.style.backgroundPosition = `-${left}px -${top}px`;
			this.InnerState.$batch({ top, left });
			this.Tree.scrollTo(0, top);

			this.MoreWindow.HideWindow();
		});

		this.on(this.Tree, "onScrollY", function() {
			const y = this.getScrollState().y;
			this.$scope.Bars.scrollTo(this.$scope.InnerState.left, y);
		});
	}

	/**
	 * Returns a string with HTML of a task node content
	 * @param {Object} obj - task data
	 * @param {function} _ - the translator function of jet locale plugin, see https://webix.gitbook.io/webix-jet/part-ii-webix-jet-in-details/plugins#applying-the-locale
	 * @returns {string} a string with HTML of an event bar
	 */
	BarsTemplate(obj, _) {
		const style = dayEventColor(obj);
		const scales = this.Scales;

		let w = obj.$w - 2;
		// all day events last 1 day longer than their end_date
		if (obj.all_day && obj.end_date < scales.end) {
			w += this.GetUnitWidth(scales, obj.start_date);
		}

		const content = `<div class="webix_scheduler_timeline_content">${obj.text ||
			_("(no title)")}</div>`;

		let css = "webix_scheduler_timeline_event";
		css +=
			this.State.selected &&
			(obj.id == this.State.selected.id || obj.id == this.State.selected.date)
				? " webix_scheduler_event_selected"
				: "";

		if (obj.start_date < this.Scales.start)
			css += " webix_scheduler_event_break_left";
		else if (obj.end_date > this.Scales.end)
			css += " webix_scheduler_event_break_right";

		const tooltip = this.GetTooltip(obj);
		return `<div webix_e_id="${obj.id}" 
			webix_tooltip="${tooltip}" class="${css}" 
			style="left:${obj.$x}px;top:${obj.$y}px;width:${w}px;height:${obj.$h}px;${style}">
			${content}
		</div>`;
	}

	/**
	 * Returns a string with a tooltip for an event
	 * @param {Object} obj - event data
	 * @returns {string} a string with HTML of a tooltip
	 */
	GetTooltip(obj) {
		return obj.text;
	}

	/**
	 * Defines the looks and contents of the bar that allows to see events that did not fit into a 5-line section
	 * @param {Object} obj - the event data
	 * @param {function} _ - the translator function of the Locale plugin
	 * @returns {string} with the HTML of the bar
	 */
	MoreBarTemplate(obj, _) {
		const text = _("Show more");
		const content = `<div class="webix_scheduler_timeline_content" webix_tooltip="${text}">${text}</div>`;
		const css = "webix_scheduler_timeline_event webix_scheduler_multi_more";

		return `<div webix_e_id="$wsh_multi_more" webix_r_id="${obj.section}" class="${css}" style="left:0px;top:${obj.$y}px;width:100%;height:${obj.$h}px;">
			${content}
		</div>`;
	}

	/**
	 * Either shows the selected event in Info or opens the list of extra events in a popup
	 * @param {MouseEvent} e - a native browser event
	 * @returns {Boolean} always false to prevent bubbling
	 */
	ShowEvent(e) {
		const event = locateEvent(e, this.Bars.$view);
		if (event.id === "$wsh_multi_more") {
			const id = event.node.getAttribute("webix_r_id");
			this.MoreWindow.ShowWindow(
				id,
				this.Tree.getItemNode(id),
				this._moreEvents[id] || []
			);
		} else if (event.id && event.id != "0") {
			const obj = this.GetEvent(event.id);
			const sel = webix.extend(event, getParams(obj), true);
			this.State.selected = sel;
		}
		return false;
	}

	/**
	 * Initiates creating a new event
	 * @param {MouseEvent} e - a native browser event
	 * @param {(string|number)} id - the ID of a section
	 */
	ShowNew(e, id) {
		const state = this.State;
		if (!state.readonly && !state.clipboard) {
			const scales = this.Scales;
			const start = scales.start;

			const unit = scales.precise
				? this.GetUnitCount(scales.minUnit)
				: scales.minUnit;
			const n = Math.floor(e.offsetX / this.GetUnitWidth(scales));
			const date = this.AddUnit(unit, start, n);

			state.selected = { id: "0", date, section: id };
		}
	}

	/**
	 * Returns the event data by its ID
	 * @param {(string|id)} id - the ID of an event
	 * @returns {Object} the event data
	 */
	GetEvent(id) {
		return this.Events.getItem(id);
	}

	/**
	 * Updates selected in global reactive state
	 * @param {(string|number)} id - the ID of an event
	 * @param {Object} newEvent - the new event that should be selected
	 */
	UpdateSelection(id, newEvent) {
		if (this.State.selected) {
			newEvent.id = id;
			this.State.selected = getParams(newEvent);
		}
	}

	/**
	 * Returns the target date for an event
	 * @returns {Date}
	 */
	GetTargetDate() {
		const clickedUnit = this.mousePosUnit || 0;
		const scales = this.Scales;
		return this.AddUnit(scales.minUnit, scales.start, clickedUnit);
	}

	/* Method wrappers for gantt-lib functions */

	GetUnitCount(type) {
		return smallerCount[type][0];
	}

	GetUnitStart(unit, date) {
		return getUnitStart(unit, date);
	}

	AddUnit(unit, date, count) {
		return addUnit(unit)(date, count);
	}

	GetGrid(scales, color) {
		return grid(scales.cellWidth, scales.cellHeight, color);
	}

	UpdateTask(task, sectionIndex, y) {
		return updateTask(
			task,
			this._lastRow[task.section] + sectionIndex * this._sectionSize,
			this.Scales,
			this.Scales.cellHeight - 12,
			y
		);
	}

	GetUnitWidth(scales, date) {
		if (
			scales.precise &&
			(scales.minUnit !== "day" || scales.minUnit !== "month")
		) {
			const count = smallerCount[scales.minUnit][1];
			const number = typeof count === "number" ? count : count(date);
			return Math.floor(scales.cellWidth / number);
		}
		return scales.cellWidth;
	}
}

/**
 * Case-insensitive indexOf
 * @param {Array<any>} arr - the array to be checked
 * @param {any} el - something that you need to find in the array
 * @returns {number} the index of the first found element or -1
 */
function indexOf(arr, el) {
	for (let i = 0; i < arr.length; ++i) {
		if (arr[i] == el) return i;
	}
	return -1;
}
