import { JetView } from "webix-jet";
import { bind } from "jet-restate-bind";

export default class AxisPropertiesView extends JetView {
	constructor(app, data, chartState) {
		super(app);
		this.Axis = data;
		this.ChartState = chartState;
	}

	config() {
		const _ = this.app.getService("locale")._;
		this.Charts = this.app.getService("charts");
		const inputs = [
			{
				label: _("Logarithmic scale"),
				view: "checkbox",
				name: "logarithmic",
				hidden: true,
			},
			{
				label: _("Title"),
				view: "text",
				name: "title",
			},
			{
				label: _("Color"),
				view: "colorpicker",
				name: "color",
				css: "webix_rpt_colorpicker",
				suggest: {
					padding: 0, //for Flat skins
					type: "colorselect",
					body: {
						button: true,
					},
				},
			},
			{
				cols: [
					{
						labelWidth: 190,
						label: _("Gridlines"),
						view: "checkbox",
						name: "lines",
					},
					{
						view: "colorpicker",
						name: "lineColor",
						width: 150,
						css: "webix_rpt_colorpicker",
						suggest: {
							padding: 0, //for Flat skins
							type: "colorselect",
							body: {
								button: true,
							},
						},
					},
				],
			},

			{
				label: _("Vertical labels"),
				view: "checkbox",
				name: "verticalLabels",
				hidden: true,
			},
		];

		return {
			view: "form",
			borderless: true,
			elementsConfig: {
				labelAlign: "right",
				labelWidth: 190,
			},
			visibleBatch: "default",
			rows: inputs,
		};
	}

	init() {
		this.on(this.Axis.$changes, "id", id => {
			if (id == "y") this.ApplyYAxis();
			else this.ApplyXAxis();
		});
		this.on(this.Axis.$changes, "lines", v => {
			const lineColor = this.getRoot().elements["lineColor"];
			if (v) lineColor.show();
			else lineColor.hide();
		});
		bind(this.Axis, "*", this.getRoot());
	}

	ApplyYAxis() {
		this.getRoot().elements["logarithmic"].show();
	}
	ApplyXAxis() {
		this.getRoot().elements["verticalLabels"].show();
	}
}
