import { JetView } from "webix-jet";
import { sortAsc, sortDesc } from "helpers/icons";
import "../../../helpers/groupselect";

export default class OtherSettingsView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;

		const sortBy = {
			rows: [
				{
					view: "label",
					label: _("Specify columns to sort by"),
					css: "webix_rpt_subtitle",
				},
				{
					view: "groupselect",
					localId: "sortBy",
					selectIcon: false,
					css: "webix_rpt_sort_selector",
					modifiers: [
						{ id: "asc", value: sortAsc },
						{ id: "desc", value: sortDesc },
					],
					options: [],
					on: {
						onChange: () => this.ChangeSortBy(),
						onModifierClick: id => this.ChangeDirection(id),
					},
				},
			],
		};

		return sortBy;
	}

	init() {
		this.State = this.getParam("state", true);
		this.on(this.State.$changes, "publicFields", v => {
			if (v) this.LoadColumns();
		});
		this.on(this.State.$changes, "fields", () => {
			if (!this.State.publicFields) this.LoadColumns();
		});
	}

	LoadColumns() {
		const columns = (this.State.publicFields || this.State.fields).map(a => ({
			id: a.id,
			value: a.name,
			type: a.type,
			model: a.model,
		}));

		const sortBy = this.$$("sortBy");
		const list = sortBy.getPopup().getList();
		list.type.template = obj => this.ListTemplate(obj);
		list.clearAll();
		list.parse(columns);

		let sort = this.State.sort;
		if (sort) sort = sort.filter(a => !!columns.find(b => b.id == a.id));
		sortBy.setValue(sort);
	}

	ListTemplate(obj) {
		return (
			obj.value +
			(obj.model
				? "<span class='webix_rpt_sources_path'>" + obj.model + "</span>"
				: "")
		);
	}

	ChangeSortBy() {
		const ui = this.$$("sortBy");
		let arr = ui.getValue();
		arr = arr ? (typeof arr === "string" ? arr.split(",") : arr) : [];

		const state = arr.map(id => {
			let mod = ui.getModifier(id);
			if (!mod) {
				mod = "asc";
				ui.setModifier(id, mod, true);
			}

			const obj = { id };
			if (mod) obj.mod = mod;

			return obj;
		});

		this.State.sort = state.length ? state : null;
	}

	ChangeDirection(id) {
		const ui = this.$$("sortBy");
		ui.setModifier(id, ui.getModifier(id) === "asc" ? "desc" : "asc");
	}
}
