import { JetView } from "webix-jet";

export default class BaseExportView extends JetView {
	config() {
		return {
			view: "popup",
			padding: 0,
			point: false,
			body: {
				view: "menu",
				subMenuPos: "right",
				layout: "y",
				borderless: true,
				type: {
					subsign: true,
				},
				css: "webix_rpt_popup_menu",
				template: obj => this.GetTemplate(obj),
				width: 160,
				submenuConfig: {
					point: false,
					width: 190,
					padding: 0,
					template: obj => this.GetTemplate(obj),
				},
				autowidth: true,
				autoheight: true,
				data: this.GetData(),
				on: {
					onMenuItemClick: (id, e) => this.PopupAction(id, e),
				},
			},
		};
	}
	/** Returns menu data */
	GetData() {
		const _ = this.app.getService("locale")._;
		return [
			{ value: _("To PNG"), id: "png" },
			{ value: _("To PDF"), id: "pdf" },
		];
	}
	/**
	 * Calls "exportModule" event for app
	 * @param {number} id - the ID of clicked menu option
	 */
	PopupAction(id) {
		this.app.callEvent("exportModule", [id]);
		this.getRoot().hide();
	}
	/**
	 * Returns html template menu option
	 * @param {Object} obj - data of menu option
	 * @returns {String} - a string with html for an option
	 */
	GetTemplate(obj) {
		return `<span class='webix_icon ${obj.icon}'></span>${obj.value}`;
	}
	/**
	 * Shows menu
	 * @param {Object} node - html node where menu should be shown
	 */
	Show(node) {
		if (this.getRoot().isVisible()) return;
		this.webix.delay(() => this.getRoot().show(node));
	}
}
