import { JetView } from "webix-jet";
import { bind } from "jet-restate-bind";
import "../../helpers/inputlist";
import { removeRight } from "../../helpers/icons";

export default class CommonView extends JetView {
	config() {
		const _ = (this._ = this.app.getService("locale")._);

		let form = {
			view: "form",
			scroll: "auto",
			elementsConfig: {
				labelWidth: 130,
				labelPosition: "top",
			},
			elements: [
				{
					rows: [
						{ view: "label", label: _("Report name") },
						{
							view: "text",
							name: "name",
							localId: "name",
						},
					],
				},
				{
					rows: [
						{ view: "label", label: _("Description") },
						{
							view: "textarea",
							labelPosition: "top",
							height: 150,
							name: "desc",
						},
					],
				},
				{
					rows: [
						{ view: "label", label: _("Data source") },
						{
							view: "combo",
							name: "data",
							options: [],
						},
					],
				},
				{
					view: "input-list",
					name: "joins",
					template: this.JoinListTemplate.bind(this),
					data: [],
					autoheight: true,
					borderless: true,
					onClick: {
						webix_rpt_action_remove: (ev, id) => this.RemoveJoin(id),
					},
				},
				{
					view: "button",
					localId: "add",
					label: _("Join data"),
					click: () => this.ShowJoins(),
				},

				{},
				{
					view: "button",
					label: _("Delete report"),
					localId: "delete",
					inputWidth: 170,
					align: "left",
					css: "webix_rpt_delete",
					click: () => this.app.callEvent("onMenuAction", [null, "delete"]),
				},
			],
		};
		return form;
	}

	init() {
		const els = this.getRoot().elements;
		this.State = this.getParam("state", true);
		this.AppState = this.app.getParam("state");
		this.Local = this.app.getService("local");
		this.Models = this.Local.getModels(true);

		const keys = Object.keys(this.Models);
		const combo = els.data;
		combo.getList().parse(
			keys.map(a => {
				const m = this.Models[a];
				return { id: m.id, value: m.name };
			})
		);

		bind(this.State, "*", this.getRoot(), this);
		this.on(this.State.$changes, "id", v => this.ShowDeleteButton(v));

		this.on(this.AppState.$changes, "toolbar", v => this.ShowDeleteButton(v));

		this.SourcesList = this.ui({
			view: "popup",
			body: {
				view: "list",
				autoheight: true,
				type: {
					template: this.JoinTemplate.bind(this),
				},
				click: id => {
					const item = this.SourcesList.getBody().getItem(id);
					this.AddJoin(item);
					this.SourcesList.hide();
				},
			},
		});
	}

	AddJoin(item) {
		const batch = this.Local.addJoin(item.tid, this.State);
		batch.joins = [...this.State.joins, item];
		this.State.$batch(batch);
	}

	RemoveJoin(id) {
		const next = this.Local.cleanLinkedModels(id, this.State);

		if (!next.block) this.State.$batch(next);
		else {
			if (next.block.filter)
				webix.alert({
					container: this.app.getRoot().$view,
					text: this._("Please delete the related query filter first"),
				});
			if (next.block.group)
				webix.alert({
					container: this.app.getRoot().$view,
					text: this._("Please delete the related group rule first"),
				});
		}
	}

	JoinTemplate(obj) {
		const s = this.Models[obj.sid];
		const t = this.Models[obj.tid];
		const f = obj.sf || obj.tf;
		const n = (obj.sf ? s : t).data.find(a => a.id === f).name;
		return `${t.name} <span class='webix_rpt_sources_path'>${s.name}${
			obj.sf ? ` (${n})` : ""
		} -> ${t.name}${obj.tf ? ` (${n})` : ""}</span>`;
	}

	JoinListTemplate(obj) {
		return removeRight + this.JoinTemplate(obj);
	}

	ShowJoins() {
		const data = this.Local.getLinkedModels(
			[this.State.data].concat(this.State.joins.map(a => a.tid)),
			true
		);
		if (!data.length) return;

		const list = this.SourcesList.getBody();

		list.clearAll();
		list.parse(data);

		this.SourcesList.show(this.$$("add").$view);
	}

	ShowDeleteButton() {
		if (this.State.id && this.AppState.toolbar) this.$$("delete").show();
		else this.$$("delete").hide();
	}
}
