import { JetView } from "webix-jet";

export default class CalendarView extends JetView {
	config() {
		this.Events = {};

		return {
			view: "calendar",
			localId: "calendar",
			events: (day, isOutside) => this.MarkEvent(day, isOutside),
			icons: false,
			navigation: false,
			monthHeader: false,
			skipEmptyWeeks: true,
			width: 0,
			height: 410,
		};
	}

	init() {
		this.Calendar = this.getRoot();
		this.Calendar.attachEvent("onAfterDateSelect", date => {
			this.app.getState().date = webix.Date.dayStart(date);
		});
	}

	urlChange() {
		this.data = this.getParam("data");
		if (this.data) this.RefreshData();
	}

	RefreshData() {
		let { start, weeks, data } = this.data;
		const end = webix.Date.add(start, weeks, "week", true);

		this.Events = {};

		while (start < end) {
			const next = webix.Date.add(start, 1, "day", true);
			for (let i = 0; i < data.length; i++)
				if (
					(data[i].all_day
						? data[i].end_date >= start
						: data[i].end_date > start) &&
					data[i].start_date < next
				)
					this.Events[start.valueOf()] = true;
			start = next;
		}

		this.Calendar.setValue(this.app.getState().date);
	}

	// adds dots to days with events
	MarkEvent(day, isOutside) {
		let css = "";

		if (!isOutside) css += webix.Date.isHoliday(day) || "";
		if (this.Events[day.valueOf()]) css += " webix_cal_day_with_event";

		return css;
	}
}
