import { JetView } from "webix-jet";

export default class ColorPopupView extends JetView {
	constructor(app, state) {
		super(app);
		this.State = state;
		this.Id = null;
	}
	config() {
		return {
			view: "popup",
			body: {
				view: "colorselect",
				button: true,
				on: {
					onColorSelect: color => {
						let s = this.GetSeries(this.Id);
						if (s) s.meta.color = color;
						this.State.updatedSeries = this.Id;
						this.getRoot().close();
					},
				},
			},
		};
	}

	Show(node, id) {
		const popup = this.getRoot();
		this.Id = id;
		popup.show(node);
		popup.getBody().setValue(this.GetSeries(id).meta.color);
	}

	GetSeries(id) {
		return this.State.series.find(s => s.id == id);
	}
}
