import { JetView } from "webix-jet";
import { empty } from "../helpers/icons";

export default class EmptyView extends JetView {
	config() {
		return {
			css: "webix_rpt_empty",
			view: "template",
			borderless: true,
			template: value => this.GetTemplate(value),
		};
	}
	init() {
		const mods = this.app.getService("local").getModules();
		mods.waitData.then(() => {
			const text = this.GetText(mods.data.count());
			this.getRoot().setValues({ text });
		});
	}
	GetText(count) {
		const _ = this.app.getService("locale")._;
		return count
			? _("Select any report from the list")
			: _("Click 'New' button to add a new report");
	}
	GetSVG() {
		const skin = webix.skin.$name;
		const icon = skin == "willow" || skin == "dark" ? "alt" : "main";
		return empty[icon];
	}
	GetTemplate(value) {
		const text =
			"<div class='webix_rpt_empty_placeholder'>" +
			(value.text || "") +
			"</div>";
		return text + this.GetSVG();
	}
}
