import { JetView } from "webix-jet";
import "../helpers/rsearch";
import MenuPopup from "./menu";
import { types } from "../helpers/icons";

export default class GridsView extends JetView {
	config() {
		const _ = (this._ = this.app.getService("locale")._);
		const bar = {
			view: "form",
			height: webix.skin.$active.barHeight + 6,
			padding: { left: 10, right: 10, top: 5, bottom: 5 },
			margin: 0,
			localId: "bar",
			cols: [
				{ view: "label", label: _("Reports") },
				{},
				{
					view: "icon",
					localId: "nameSorting",
					icon: "rpi-sort-alphabetical-ascending",
					css: "webix_rpt_sort_icon webix_rpt_btn_active",
					icons: {
						asc: "rpi-sort-alphabetical-ascending",
						desc: "rpi-sort-alphabetical-descending",
					},
					click: () => this.Sort("name"),
				},
				{ width: 4 },
				{
					view: "icon",
					localId: "updatedSorting",
					css: "webix_rpt_sort_icon",
					icon: "rpi-sort-calendar-descending",
					icons: {
						asc: "rpi-sort-calendar-ascending",
						desc: "rpi-sort-calendar-descending",
					},
					click: () => this.Sort("updated"),
				},
			],
		};

		const menu = {
			view: "form",
			padding: { left: 10, right: 10, top: 2, bottom: 4 },

			cols: [
				{
					view: "text-search",
					localId: "search",
					placeholder: _("Type to search"),
				},
			],
		};
		const table = {
			view: "list",
			localId: "list",
			scroll: true,
			select: true,
			css: "webix_rpt_list",
			type: {
				height: webix.skin.$active.rowHeight + 24,
				template: data => this.GetListTemplate(data),
			},
			scheme: {
				$change: function(obj) {
					if (obj.date && typeof obj.date === "string")
						obj.date = new Date(obj.date);
				},
			},
			onClick: {
				webix_rpt_action_menu: (e, id) => {
					this.Menu.Show(e, id);
					return false;
				},
			},
			onDblClick: {},
			on: {
				onAfterSelect: id => this.LoadModule(id, null),
				onItemDblClick: id => this.LoadModule(id, "edit"),
			},
		};

		return {
			width: 320,
			type: "clean",
			rows: [bar, { type: "clean", rows: [menu, table] }],
		};
	}

	init() {
		this.State = this.getParam("state", true);
		this.Menu = this.ui(MenuPopup);

		const list = this.$$("list");
		list.$view.firstChild.setAttribute("placeholder", this._("No reports"));

		const mods = this.app.getService("local").getModules();
		list.sync(mods);
		mods.waitData.then(() => {
			if (this.State.moduleId) list.select(this.State.moduleId);
		});

		this.on(this.State.$changes, "readonly", () => this.$$("list").refresh());

		this.$$("search").attachEvent("onSearchChange", v => this.Find(v));

		this.Sorting = { by: "name", dir: "asc" };
	}

	LoadModule(id, mode) {
		this.app.callEvent("loadModule", [id, mode]);
	}

	Hide() {
		this.app.callEvent("hideModuleList");
	}

	Find() {
		let text = this.$$("search").getValue();
		const list = this.$$("list");
		if (text) {
			text = text.toLowerCase();
			list.filter(data => this.SearchCompare(text, data));
		} else {
			list.filter();
		}
	}
	SearchCompare(value, item) {
		return item.name.toLowerCase().indexOf(value) > -1;
	}

	GetListTemplate(item) {
		const _ = this.app.getService("locale")._;
		let html = "<div class='webix_rpt_list_block'>";
		html +=
			"<div class='webix_rpt_list_report_name'>" +
			types[item.parsed.type] +
			" " +
			item.name +
			"</div>";
		html +=
			"<div class='webix_rpt_list_report_date'>" +
			_("Last modified") +
			": " +
			webix.i18n.longDateFormatStr(item.updated || new Date()) +
			"</div>";
		if (!this.State.readonly)
			html +=
				"<span class='webix_icon webix_rpt_action_menu rpi-dots-vertical'></span>";
		return html + "</div>";
	}

	Sort(by) {
		let sState = this.Sorting;
		const prevState = webix.copy(sState);
		if (sState.by == by) sState.dir = sState.dir == "desc" ? "asc" : "desc";
		else {
			sState.by = by;
			sState.dir = "desc";
		}
		sState.as = by == "updated" ? "date" : "string";
		this.ChangeSortButtons(sState, prevState);
		this.$$("list").sort(sState);
	}

	ChangeSortButtons(state, prevState) {
		const btn = this.$$(state.by + "Sorting");

		this.webix.html.addCss(btn.$view, "webix_rpt_btn_active");
		btn.config.icon = btn.config.icons[state.dir];
		btn.refresh();
		if (state.by != prevState.by) {
			const prevBtn = this.$$(prevState.by + "Sorting");
			this.webix.html.removeCss(prevBtn.$view, "webix_rpt_btn_active");
		}
	}
}
