import { JetView } from "webix-jet";
import { events, timeStart } from "helpers/templates";
import { shrinkTo, isMultiDay } from "helpers/dates";
import { getParams } from "helpers/recurring";

export default class MonthListView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const config = {
			view: "list",
			type: {
				height: 50,
				template: events(_),
				timeStart: ev => this.TimeStart(ev),
			},
			on: {
				onItemClick: id => this.ShowEvent(id),
			},
		};

		return config;
	}
	init() {
		this.List = this.getRoot();
		this.State = this.app.getState();

		webix.extend(this.List, webix.OverlayBox);
	}

	urlChange() {
		this.data = this.getParam("data");
		if (this.data) this.RefreshData();
	}

	RefreshData() {
		const start = webix.Date.datePart(this.State.date, true);
		const end = webix.Date.add(start, 1, "day", true);
		this.app
			.getService("local")
			.getEvents(start, end)
			.then(evs => {
				this.List.clearAll();
				this.List.parse(evs.map(shrinkTo(start, end)));
				this.ToggleOverlay();
			});
	}

	ToggleOverlay() {
		if (!this.List.count()) {
			const _ = this.app.getService("locale")._;
			this.List.showOverlay(_("No Events"));
		} else {
			this.List.hideOverlay();
		}
	}

	ShowEvent(id) {
		const obj = this.List.getItem(id);
		this.State.selected = getParams(obj);
	}

	TimeStart(ev) {
		const _ = this.app.getService("locale")._;
		if (isMultiDay(ev)) return _("All Day");
		else return timeStart(ev.start_date, ev.end_date);
	}
}
