import { JetView } from "webix-jet";
import { WEEKDAYS, fillWdays, next } from "helpers/recurring";

export default class RecurringFormView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const compact = this.getParam("compact", true);

		const aSkin = webix.skin.$active;
		const form = {
			view: "form",
			localId: "recurringForm",
			hidden: true,
			elementsConfig: { labelPosition: "left" },
			padding: {
				top: aSkin.layoutPadding.form,
				bottom: 0,
				left: compact ? aSkin.layoutPadding.form : 0,
				right: compact ? 14 : 0,
			},
			borderless: true,
			rows: [
				this.CreateIntervalControls(_),
				{
					localId: "customRecControls",
					hidden: true,
					rows: [this.CreateCheckboxes(_), this.CreateSelect(_)],
				},
				this.CreateRadio(_),
			],
		};

		if (!compact) {
			form.on = {
				onChange: (o, v, m) => this.UpdateEvent(m),
			};
		}

		return form;
	}

	init() {
		this.State = this.app.getState();
		this.Form = this.$$("recurringForm");
		this.Events = this.app.getService("local").events(true);

		// commonly called controls
		this.CheckBoxContainer = this.$$("weekly");
		this.Checkboxes = this.CheckBoxContainer.getChildViews().slice(1);
		this.Checkboxes.pop();
		this.PatternSelect = this.$$("patternSelect");
		this.UntilDateContainer = this.$$("untilDate");
		this.UntilDateCalendar = this.Form.elements.untilDate.getPopup().getBody();
		this.UntilNumContainer = this.$$("untilCount");
		this.UntilNum = this.UntilNumContainer.getBody();
		this.CustomRecControls = this.$$("customRecControls");
	}

	// methods of main controls: counter of INTERVAL and richselect of FREQ

	/**
	 * Returns the config object for interval counter and recurrence frequency selection
	 * @param {function} _ - the translator method of the Locale plugin
	 * @returns {Object}
	 */
	CreateIntervalControls(_) {
		return {
			margin: 8,
			cols: [
				{
					view: "counter",
					label: _("Every"),
					labelWidth: 50,
					min: 1,
					name: "INTERVAL",
					css: "webix_scheduler_counter",
				},
				{
					view: "richselect",
					name: "FREQ",
					options: [
						{ id: "DAILY", value: _("day") },
						{ id: "WEEKLY", value: _("week") },
						{ id: "MONTHLY", value: _("month") },
						{ id: "YEARLY", value: _("year") },
					],
					on: {
						onChange: v => this.ChangeControls(v),
					},
				},
			],
		};
	}

	/**
	 * Switches controls for detailed recurrence pattern selection depending on the frequency
	 * @param {string} v - recurrence frequency
	 */
	ChangeControls(v) {
		this._freq = v;
		if (v === "DAILY") {
			this.CustomRecControls.hide();
		} else {
			this.CustomRecControls.show();
			if (v === "WEEKLY") {
				this.CheckBoxContainer.show();
				this.PatternSelect.hide();
			} else {
				this.CheckBoxContainer.hide();
				if (v === "MONTHLY" && this.PatternSelect.getValue() == 3)
					this.PatternSelect.setValue(1);
				this.PatternSelect.show();
				this.PatternSelect.getList().refresh();
				this.PatternSelect.refresh();
			}
		}
	}

	// methods of checkboxes for WEEKLY

	/**
	 * Returns the config object for week day checkboxes
	 * @param {function} _ - the translator method of the Locale plugin
	 * @returns {Object}
	 */
	CreateCheckboxes(_) {
		const days = [];
		for (let i = 0; i < 7; ++i) {
			days.push({
				view: "checkbox",
				label: webix.i18n.calendar.dayShort[i],
				labelPosition: "top",
				labelAlign: "center",
				css: "webix_scheduler_day_checkbox",
				inputWidth: 26,
				width: 45,
				align: "center",
				name: WEEKDAYS[i],
				on: {
					onChange(v, o) {
						this.$scope.CheckboxGuard(this.config.name, v, o);
					},
				},
			});
		}
		return {
			localId: "weekly",
			cols: [
				{ view: "label", label: _("on"), width: 45 },
				...days,
				{ gravity: 0.00001 },
			],
		};
	}

	/**
	 * Prevents the user from unchecking the only checked weekday checkbox
	 * @param {string} day - the name of the related checkbox (SU, MO, TU, WE, TH, FR, or SA)
	 * @param {(Boolean|number)} v - the new value of a checkbox (false/0 or true/1)
	 * @param {(Boolean|number)} o - the old value of a checkbox (false/0 or true/1)
	 */
	CheckboxGuard(day, v, o) {
		if (!webix.isUndefined(o) && !v && !this.GetBYDAY()) {
			this.Form.setValues({ [day]: 1 }, true);
		}
	}

	/**
	 * Sets values to weekday checkboxes according to BYDAY recurrence pattern (week days)
	 * @param {Object} values - values from the form
	 */
	SetBYDAY(values) {
		const days = values.BYDAY.split(",");
		this.Checkboxes.forEach(check => {
			values[check.config.name] = !!days.find(d => d == check.config.name);
		});
	}

	/**
	 * Gets the BYDAY pattern from checkbox values
	 * @returns {string}
	 */
	GetBYDAY() {
		const BYDAY = [];
		this.Checkboxes.forEach(check => {
			if (check.getValue()) {
				BYDAY.push(check.config.name);
			}
		});
		return BYDAY.join(",");
	}

	// methods of richselect with MONTHLY and YEARLY patterns

	/**
	 * Returns the config object for a dropdown list with monthly/yearly recurrence patterns
	 * @param {function} _ - the translator method of the Locale plugin
	 * @returns {Object}
	 */
	CreateSelect(_) {
		return {
			view: "richselect",
			localId: "patternSelect",
			name: "rec_pattern",
			suggest: {
				data: [],
				body: {
					template: obj => {
						if (this._freq === "YEARLY" && obj.BYYEARDAY) {
							return `${_("Every")} ${obj.BYYEARDAY} ${_("day of year")}`;
						}

						const byDay = [];
						if (obj.BYDAY) {
							obj.BYDAY.forEach(d =>
								byDay.push(webix.i18n.calendar.dayFull[d])
							);
						}

						let text = `${_("Every")} ${obj.BYMONTHDAY || obj.BYSETPOS} ${
							obj.BYDAY ? byDay.join(", ") : ""
						}`;
						if (this._freq === "YEARLY")
							text += ` ${_("of")} ${
								webix.i18n.calendar.monthFull[obj.BYMONTH - 1]
							}`;
						return text;
					},
				},
				on: {
					onShow() {
						this.getList().filter(
							obj => this.$scope._freq === "YEARLY" || obj.id != 3
						);
					},
				},
			},
		};
	}

	/**
	 * Describes calculated constants of a date
	 * @typedef {Object} DateInfo
	 * @property {number} day - the date (day of month)
	 * @property {number} weekday - the number of a weekday (0-6)
	 * @property {number} month - the number of month (0-11)
	 * @property {number} weekNum - the number of a weekday within a month (e.g. 4th Friday)
	 */

	/**
	 * Sets pattern options for the dropdown list (monthly and yearly events)
	 * @param {Object} ev - values from the form
	 * @param {DateInfo} calculated - calculated constants of a date
	 */
	SetPatternOptions(ev, calculated) {
		this.PatternSelect.getList().parse([
			{
				id: 1,
				BYMONTH: ev.BYMONTH || calculated.month + 1,
				BYMONTHDAY: ev.BYMONTHDAY || calculated.day,
			},
			{
				id: 2,
				BYSETPOS: ev.BYSETPOS || calculated.weekNum,
				BYDAY: [calculated.weekday],
				BYMONTH: ev.BYMONTH || calculated.month + 1,
			},
			{
				id: 3,
				BYYEARDAY: ev.BYYEARDAY || calculated.yearday,
			},
		]);
		this.PatternSelect.refresh();
		this._freq = ev.FREQ;

		ev.rec_pattern = ev.BYSETPOS ? 2 : ev.BYYEARDAY ? 3 : 1;
	}

	/**
	 *
	 * @param {Object} values - values from the form
	 */
	GetRecPattern(values) {
		const data = webix.copy(
			this.PatternSelect.getList().getItem(values.rec_pattern)
		);

		if (this._freq === "MONTHLY") delete data.BYMONTH;
		if (data.BYDAY) data.BYDAY = data.BYDAY.map(d => WEEKDAYS[d]).join(",");
		delete data.id;

		return data;
	}

	// methods of radio for UNTIL

	/**
	 * Returns the config object for the radio with options for setting the end of repetition
	 * @param {function} _ - the translator method of the Locale plugin
	 * @returns {Object}
	 */
	CreateRadio(_) {
		const selectors = {
			width: 130,
			rows: [
				{
					localId: "untilDate",
					align: "middle",
					view: "align",
					body: {
						view: "datepicker",
						name: "untilDate",
						icons: false,
					},
				},
				{
					localId: "untilCount",
					view: "align",
					align: "bottom",
					body: {
						name: "untilCount",
						view: "counter",
						css: "webix_scheduler_counter",
						min: 1,
					},
				},
			],
		};

		return {
			rows: [
				{ view: "label", label: _("End repeat") },
				{
					cols: [
						{
							view: "radio",
							name: "until",
							localId: "until",
							vertical: true,
							options: [
								{ id: 1, value: _("never") },
								{ id: 2, value: _("date") },
								{ id: 3, value: _("after several occurrences") },
							],
							on: {
								onChange: v => this.ChangeSelectors(v),
							},
						},
						selectors,
					],
				},
			],
		};
	}

	/**
	 * Switches controls for dates or numbers for setting the end of repetition
	 * @param {(number|string)} v - the value of the radio that sets the type of repetition end (1,2,3)
	 */
	ChangeSelectors(v) {
		this.UntilDateContainer[v === "2" ? "show" : "hide"]();
		this.UntilNumContainer[v === "3" ? "show" : "hide"]();
	}

	/**
	 * Sets the values of controls that are used to choose the end of repetition
	 * @param {Object} values - values from the form
	 */
	SetUNTIL(values) {
		const start = this._StartDate;
		values.until = values.COUNT ? 3 : values.UNTIL ? 2 : 1;
		values.untilDate = values.UNTIL || webix.Date.add(start, 1, "year", true);
		values.untilCount = values.COUNT || this.CountNum(values);
		this.UntilDateCalendar.define({ minDate: start });
	}

	/**
	 * Calculates the number of occurrences based on the end date of repetition (UNTIL)
	 * @param {Object} values - values from the form
	 */
	CountNum(values) {
		let res = 0;

		if (values.UNTIL) {
			const consts = {};
			fillWdays(values, consts);
			let date = webix.Date.copy(this._StartDate);

			while (date <= values.UNTIL) {
				date = next(date, values, consts);
				res++;
			}
		} else {
			res = 3;
		}

		return res;
	}

	// subform methods

	/**
	 * Sets values into this form
	 * @param {Object} event - the event data object
	 * @param {DateInfo} calculated - calculated constants of a date
	 * @param {Boolean} silent - if true, the form is marked as 'saved' (containing no unsaved values)
	 */
	SetValues(event, calculated, silent) {
		if (!calculated) return this.Form.setValues(event, true);

		this._StartDate = event.start_date;
		let values = event.$recurring;

		if (!values) {
			values = { FREQ: "DAILY" };
		}

		if (!values.INTERVAL) values.INTERVAL = 1;
		if (!values.BYDAY) {
			values.BYDAY = WEEKDAYS[calculated.weekday];
		}

		// for a reset
		if (!values.EXDATE) values.EXDATE = [];

		this.SetUNTIL(values);
		this.SetBYDAY(values);
		this.SetPatternOptions(values, calculated);
		this.Form.setValues(values, true);
		if (silent) this.Form.setDirty();
	}

	/**
	 * Defines the object that stores the recurring pattern, for more details please refer to the iCalendar docs https://icalendar.org/iCalendar-RFC-5545/3-8-5-3-recurrence-rule.html
	 * @typedef {Object} Recurring
	 * @property {string} FREQ - the step of repetition: DAILY, WEEKLY, MONTHLY, YEARLY
	 * @property {(number|string)} INTERVAL - how many steps are between occurrences
	 * @property {string} BYDAY - week days, e.g. SU,MO,TU,WE,TH,FR,SA
	 * @property {(number|string)} BYMONTH - the number or a month (1-12)
	 * @property {(number|string)} BYMONTHDAY - the date (day of a month)
	 * @property {(number|string)} BYSETPOS - the number of the week day within a month
	 * @property {string} UNTIL - a date like 19970902T090000
	 * @property {(number|string)} COUNT - how many times the event repeats
	 * @property {string} EXDATE - string with 1 or more dates, on which the event does not repeat, e.g. 19960402T010000Z,19960403T010000Z,19960404T010000Z
	 */

	/**
	 * Collects recurrence pattern object from form values and returns it
	 * @returns {Recurring} the object that stores the recurring pattern
	 */
	GetValues() {
		let values = this.Form.getValues();

		let res = {
			FREQ: values.FREQ,
			INTERVAL: values.INTERVAL,
			UNTIL: values.until == 2 ? values.untilDate : this.GetUntil(values),
			COUNT: values.until == 3 ? values.untilCount : null,
			EXDATE: values.EXDATE || [],
		};

		if (values.FREQ === "WEEKLY") {
			res.BYDAY = this.GetBYDAY(values);
		} else if (values.FREQ === "MONTHLY" || values.FREQ === "YEARLY") {
			res = webix.extend(res, this.GetRecPattern(values));
		}

		this.Form.setDirty();

		return res;
	}

	/**
	 * Calculates the end of repetition as date based on COUNT
	 * @param {Object} values - values from the form
	 * @returns {Date}
	 */
	GetUntil(values) {
		if (values.until == 3) {
			const consts = {};
			fillWdays(values, consts);
			let date = webix.Date.copy(this._StartDate);
			for (let i = 1; i < values.untilCount; ++i) {
				date = next(date, values, consts);
			}
			date = webix.Date.add(date, 1, "day", true);
			this.Form.setValues({ untilDate: date }, true);
			return date;
		} else return null;
	}

	/**
	 * Collects all values and sends them to the master form for event update (via global app event)
	 * @param {string} mode - "user" means that the change was made via UI
	 */
	UpdateEvent(mode) {
		if (mode === "user") {
			const change = this.GetValues();
			this.app.callEvent("form:update:recurring", [{ $recurring: change }]);
			this.Form.setDirty();
		}
	}

	/**
	 * Checks if this form has changed values
	 * @returns {Boolean}
	 */
	IsDirty() {
		return this.Form.isDirty();
	}

	/**
	 * Shows this form
	 */
	Show() {
		this.getRoot().show();
	}

	/**
	 * Hides this form
	 */
	Hide() {
		this.getRoot().hide();
	}
}
