import UnitListView from "../common/unitlist";
import { shrinkTo, isToday } from "helpers/dates";

export default class AgendaView extends UnitListView {
	config() {
		const compact = this.getParam("compact", true);
		const ui = super.config();
		ui.css = "webix_scheduler_agenda";

		webix.extend(
			ui.type,
			{
				height: "auto",
				headerHeight: 50,
				classname: obj => this.TemplateCss(obj),
			},
			true
		);

		webix.extend(
			ui.on,
			{
				onItemRender: obj => {
					if (obj.$unit) this.CurrentUnit = obj.$unit;
				},
				onItemClick: (id, e, node) => {
					node = node.firstChild;
					this.ShowEvent(id, node);
					if (!compact) this.SelectEvent(node);
				},
			},
			true
		);

		return ui;
	}

	init() {
		super.init();

		this.Data = this.app.getService("local");
		const events = this.Data.events(true);

		this.on(this.State.$changes, "date", () => this.RefreshData());
		this.on(this.State.$changes, "active", () => this.RefreshData());
		this.on(this.State.$changes, "selected", v => {
			if (!v) this.SelectEvent(); //remove selection
		});
		this.on(this.app, "events:refresh", () => this.RefreshData());

		// updates made in full mode by form
		this.on(
			events.data,
			"onStoreUpdated",
			(_i, _o, mode) => mode && this.RefreshData()
		);
	}

	RefreshData() {
		let start = webix.Date.monthStart(this.State.date);
		let end = webix.Date.add(start, 1, "month", true);

		this.Data.getEvents(start, end).then(data => {
			this.data = { all: data, start: start, end: end };
			if (this.app) {
				this.List.clearAll();
				this.List.parse(data.map(shrinkTo(start, end)));
				this.ToggleOverlay();
			}
		});
	}

	SelectEvent(node) {
		if (node == this._selected) return;

		const name = "webix_agenda_selected";
		if (this._selected) webix.html.removeCss(this._selected, name);
		if (node) webix.html.addCss(node, name);

		this._selected = node;
	}

	TemplateHeader(datestring) {
		const date = new Date(datestring * 1);
		const today = isToday(date);

		const fstring = `%F <span class='webix_scheduler_monthday${
			today ? " webix_scheduler_today" : ""
		}'>%j</span><br><span class='webix_scheduler_dayofweek'>%l</span>`;

		return webix.Date.dateToStr(fstring)(date);
	}

	TemplateCss(obj) {
		const ids = this.List.getUnitList(this.CurrentUnit);

		let css = "webix_list_item";
		if (obj.id == ids[ids.length - 1]) css += " webix_event_last";
		if (obj.id == ids[0]) css += " webix_event_first";

		return css;
	}
}
