import MultiMoreView from "../common/multimore";
import SelectionMixin from "helpers/selection";
import EditRecurringMixin from "helpers/editrecurring";
import { dayEventColor } from "helpers/templates";
import { initDnD, locateEvent } from "helpers/dnd";
import { daysBetweenInclusive } from "helpers/dates";
import { getParams } from "helpers/recurring";
import CopyPasteMixin from "helpers/copypaste";

export default class MultiDayEventsView extends MultiMoreView {
	config() {
		const ui = super.config();

		ui.cols[1] = {
			view: "dataview",
			localId: "multiDayList",
			css: "webix_scheduler_multidays",
			scroll: false,
			height: 32,
			xCount: 7,
			yCount: 1,
			width: 0,
			type: {
				width: "auto",
				height: "auto",
				itemHeight: 36,
			},
			template: "",
			tooltip: {
				template: "",
			},
			onClick: {
				webix_scheduler_multiday_event: e => this.ShowEvent(e),
			},
		};

		return ui;
	}
	init() {
		this.State = this.app.getState();
		super.init();

		SelectionMixin(this, "multi");
		EditRecurringMixin(this);

		this.Events = new webix.DataCollection({});

		this.List.attachEvent("onAfterRender", () => this.RenderEvents());
		initDnD.call(this, this.List, "multiday");

		if (this.app.config.copypaste && !webix.env.mobile)
			CopyPasteMixin(this, this.List);
	}

	urlChange() {
		this.data = this.getParam("data");
		if (this.data) {
			this.ParseDays(this.data.multi);
		}
	}

	ParseDays(events) {
		if (!events.length) return;

		this.Events.clearAll();
		this.Events.parse(events);

		const days = [];
		for (let d = 0; d < 7; d++) {
			days.push({
				id: d + 1,
				date: webix.Date.add(this.data.start, d, "day", true),
			});
		}
		this.BreakEventsByDay(days);

		this.SetMoreIcon(
			this._maxEvents > this.maxVisibleLines ? this._expandState : null
		);

		this.List.blockEvent();
		this.List.parse(days);
		this.ResetListHeight();
		this.List.unblockEvent();

		this.RenderEvents();
	}

	BreakEventsByDay(data) {
		this.Days = [];
		const processed = {};
		this._maxEvents = 0;

		data.forEach((day, dayIndex) => {
			const today = day.date;
			this.Days.push([]);

			this.Events.data.each(ev => {
				if (!webix.isUndefined(processed[ev.id])) {
					if ((today <= ev.end_date && ev.all_day) || today < ev.end_date) {
						this.Days[dayIndex][processed[ev.id]] = "";
					} else delete processed[ev.id];
				} else if (
					ev.start_date < webix.Date.add(today, 1, "day", true) &&
					ev.end_date >= today
				) {
					let index = 0;
					// search for the first available index
					while (!webix.isUndefined(this.Days[dayIndex][index])) index++;

					processed[ev.id] = index;

					const config = {};
					config.es = ev.start_date;
					config.ee =
						!ev.all_day && !webix.Date.timePart(ev.end_date)
							? webix.Date.add(ev.end_date, -1, "day", true)
							: ev.end_date;

					config.length =
						1 +
						daysBetweenInclusive(
							(config.ls = new Date(Math.max(this.data.start, config.es))),
							(config.le = new Date(Math.min(this.data.end - 1, config.ee)))
						);

					ev.$config = config;
					this.Days[dayIndex][index] = ev;
				}
			});

			if (this.Days[dayIndex].length > this._maxEvents)
				this._maxEvents = this.Days[dayIndex].length;
		});
	}

	RenderEvents() {
		const allEvents = this.Events.count();

		if (!allEvents) return;

		const container = webix.html.create("div", {
			role: "presentation",
		});

		let html = "";
		let renderedEvents = 0;
		const toHide =
			this._expandState === "down" && this._maxEvents > this.maxVisibleLines;

		this.List.data.each((_day, dayIndex) => {
			const dayHTML = [];
			const visible = toHide
				? this.maxVisibleLines - 1
				: this.Days[dayIndex].length;

			for (let i = 0; i < visible; ++i) {
				dayHTML[i] = this.Days[dayIndex][i];

				if (dayHTML[i]) {
					dayHTML[i] = this.ToHTML(dayHTML[i], dayIndex, i);
					renderedEvents++;
				}
			}

			html += dayHTML.join("");
		});

		if (toHide) {
			html += this.MoreOption(allEvents - renderedEvents);
		}

		container.innerHTML = html;
		this._DataObj = this.List.$view.firstChild.appendChild(container);
		this.app.callEvent("events:rendered", []);
	}

	ToHTML(obj, cIndex, rIndex) {
		const style = dayEventColor(obj);
		const size = this.GetSizePosition(obj.$config.length, cIndex, rIndex);
		const css = this.GetStyle(obj, obj.$config);

		const _ = this.app.getService("locale")._;
		return `
			<div
				webix_tooltip="${obj.text || _("(No title)")}"
				webix_e_id="${obj.id}"
				class="${css}"
				style="${size} ${style}">
					${obj.text || _("(No title)")}
			</div>
		`;
	}

	GetStyle(obj, c) {
		const { start, end } = this.data;
		let css = "webix_scheduler_multiday_event";
		css +=
			this.State.selected &&
			(obj.id == this.State.selected.id || obj.id == this.State.selected.date)
				? " webix_scheduler_event_selected"
				: "";

		if (c.es < start && webix.Date.equal(start, webix.Date.dayStart(c.ls)))
			css += " webix_scheduler_event_break_left";
		if (
			c.ee >= end &&
			webix.Date.equal(
				webix.Date.add(end, -1, "day", true),
				webix.Date.dayStart(c.le)
			)
		)
			css += " webix_scheduler_event_break_right";

		css += obj.$past ? " webix_scheduler_past_event" : "";

		return css;
	}

	MoreOption(excess) {
		const _ = this.app.getService("locale")._;
		const size = this.GetSizePosition(
			this.List.config.xCount,
			0,
			this.maxVisibleLines - 1
		);

		return `<div webix_e_id="$wsh_multi_more" class="webix_scheduler_multiday_event webix_scheduler_multi_more" style="${size}">${excess} ${_(
			"more"
		)}</div>`;
	}

	GetSizePosition(length, cIndex, rIndex) {
		// numbers are for space between events and borders of component
		const width = length * this.List.type.width - 4;
		const height = this.List.type.itemHeight - 8;
		const left = this.List.type.width * cIndex + 1;
		const top = rIndex * this.List.type.itemHeight + 4;

		return `width:${width}px; height:${height}px; line-height:${height}px; left:${left}px; top:${top}px;`;
	}

	ShowEvent(e) {
		const event = locateEvent(e, this.List.$view);
		if (event.id) {
			if (event.id === "$wsh_multi_more") return this.ExpandData();

			const obj = this.Events.getItem(event.id);
			const sel = webix.extend(event, getParams(obj), true);
			this.State.selected = sel;
		}
		return false;
	}

	ExpandData() {
		if (!this._inAnimation) {
			this._inAnimation = true;

			this._expandState = "up";
			this.Animate("up");

			this.ResetListHeight(this._maxEvents);
		}
	}

	WrapData() {
		if (!this._inAnimation) {
			this._inAnimation = true;
			this._expandState = "down";

			const height =
				this.List.$height -
				(this._maxEvents - this.maxVisibleLines) * this.List.type.height;
			this.Animate("down", height, () => {
				this.ResetListHeight(this.maxVisibleLines);
			});
		}
	}

	ResetListHeight(count) {
		if (!count)
			count =
				this._expandState === "down"
					? Math.min(this._maxEvents, this.maxVisibleLines)
					: this._maxEvents;

		const height = count * this.List.type.itemHeight;
		if (this.List.type.height !== height) {
			this.List.type.height = height;
			this.List.define("height", height);
			this.List.resize();
		}
	}

	/**
	 * Returns the data object for an event
	 * @param {(string|number)} id - the ID of an event
	 * @returns {Object} - the event object
	 */
	GetEvent(id) {
		return this.Events.getItem(id);
	}

	/**
	 * Returns the target date for an event
	 * @param {(string|number)} id - the ID of a data item of the main view (corresponds to a day)
	 * @returns {Date}
	 */
	GetTargetDate(id) {
		return id ? this.List.getItem(id).date : this.State.date;
	}
}
