import UnitListView from "../common/unitlist";
import { isToday } from "helpers/dates";

export default class WeekEventsView extends UnitListView {
	config() {
		const ui = super.config();
		ui.css = "webix_scheduler_week";
		ui.type.headerHeight = 28;

		return ui;
	}

	urlChange() {
		this.data = this.getParam("data");
		if (this.data) {
			this.List.clearAll();
			this.List.parse(this.data.all);
			this.ToggleOverlay();
		}
	}

	TemplateHeader(datestring) {
		const date = new Date(datestring * 1);
		const today = isToday(date);

		const fstring = `%l, %F <span class='webix_scheduler_monthday${
			today ? " webix_scheduler_today" : ""
		}'>%j</span>`;

		return `<span class="webix_unit_header_inner">${webix.Date.dateToStr(
			fstring
		)(date)}</span>`;
	}
}
