import { JetView } from "webix-jet";
import getPalette from "helpers/palette";
import { getYearDay } from "helpers/dates";

import {
	WEEKDAYS,
	fillWdays,
	next,
	datesToWeekdays,
	WeekdaysToDates,
	clipSequence,
	cutOccurrence,
	separateSequence,
} from "helpers/recurring";
import { serialize } from "helpers/ical";
import RecurringFormView from "./recurringform";
import EditRecurringMixin from "helpers/editrecurring";

export default class EventFormView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		this.Compact = this.getParam("compact", true);

		const aSkin = webix.skin.$active;
		const toolbar = {
			view: "toolbar",
			borderless: true,
			padding: {
				left: aSkin.layoutPadding.form - (aSkin.inputHeight - 20) / 2,
				right: 14,
			},
			elements: [
				{
					view: "icon",
					icon: "wxi-close",
					tooltip: _("Close"),
					hotkey: "esc",
					click: () => this.Close(),
				},
				{ view: "label", label: _("Edit event") },
				{
					view: "button",
					width: 130,
					css: "webix_primary",
					value: _("Done"),
					hotkey: "Ctrl+Enter",
					click: () => this.Done(),
				},
			],
		};

		const form = {
			view: "form",
			localId: "form",
			scroll: true,
			borderless: true,
			padding: { right: 14 },
			elementsConfig: { labelPosition: "top" },
			elements: [
				{ view: "text", label: _("Event"), name: "text" },
				{
					margin: 8,
					rows: [
						this.GetDateTime("start", _, aSkin),
						this.GetDateTime("end", _, aSkin),
					],
				},
				{
					view: "checkbox",
					name: "all_day",
					labelPosition: "left",
					labelWidth: 0,
					labelRight: _("All Day"),
					on: {
						onChange: v => this.ToggleTimeControls(v),
					},
				},
				this.CreateCalendarColor(_),
				{
					view: "textarea",
					label: _("Notes"),
					name: "details",
					height: 150,
				},
				{},
			],
			on: {
				onChange: (v, o, m) => {
					// some fields need preprocessing
					const change = this.Form.getDirtyValues();
					if (Object.keys(change).length) this.PrepareChange(change);
					if (!this.Compact && m === "user")
						this.Wait(() => this.UpdateEvent(change));
				},
			},
		};

		if (this.app.config.recurring)
			form.elements.splice(3, 0, this.CreateRepeat(_, aSkin));

		if (this.app.config.units) {
			const index = this.app.config.recurring ? 5 : 4;
			form.elements.splice(index, 0, this.CreateUnitSelector(_));
		}

		if (this.app.config.timeline) {
			let index = this.app.config.recurring ? 5 : 4;
			if (this.app.config.units) index++;
			form.elements.splice(index, 0, this.CreateSectionSelector(_));
		}

		const config = {
			view: "proxy", // for borders
			body: {
				margin: 0,
				rows: [toolbar, form],
			},
		};

		return config;
	}

	init() {
		this.State = this.app.getState();
		this.Form = this.$$("form");
		this.Local = this.app.getService("local").events(true);
		this.Ops = this.app.getService("operations");

		// extending this class with common methods for editing of recurring events
		EditRecurringMixin(this);

		// most commonly called controls
		this.RecurringOptions = this.$$("recOption");
		this.StartTime = this.$$("startTime");
		this.EndTime = this.$$("endTime");

		this.on(this.State.$changes, "readonly", (_v, o) => {
			if (!webix.isUndefined(o)) this.Close();
		});

		this.on(this.app, "form:update:recurring", update => {
			// 'update' as imitated dirty value
			if (Object.keys(update).length) this.PrepareChange(update);
			this.UpdateEvent(update);
		});

		// parse lists that are not data dependable
		const local = this.app.getService("local");
		if (this.app.config.calendars) {
			this.Calendars = local.calendars(true);
			this.$$("calendar")
				.getList()
				.parse(this.Calendars);
		}

		if (this.app.config.units) {
			const waitUnits = local.units();
			const combo = this.Form.elements["units"];
			waitUnits.then(units => {
				combo.getList().parse(units);
				combo.refresh();
			});
		}

		if (this.app.config.timeline) {
			this.Sections = local.sections(true);
			this.$$("sections")
				.getList()
				.parse(this.Sections);
		}
	}

	ready() {
		this.SubForm = this.getSubView("recurringForm");
		this.on(this.State.$changes, "selected", v => {
			if (v) {
				this.ClearTempEvent(); // support double clicks and 'Create' at any moment
				this.Wait(() => this.FillForm(v));
			}
		});
	}

	destroy() {
		this.ClearTempEvent();
	}

	/**
	 * Returns the config controls for editing recurring patterns of events
	 * @param {function} _ - the method of the Locale plugin used to translate labels according to the current locale
	 * @param {Object} aSkin - stores sizes and colors of a currently active Webix skin
	 */
	CreateRepeat(_, aSkin) {
		const rows = [
			{ view: "label", label: _("Repeat"), height: aSkin.labelTopHeight },
			{
				margin: 4,
				cols: [
					{
						view: "richselect",
						localId: "recOption",
						name: "rec_option",
						suggest: {
							data: [
								{ id: "none", value: _("never") },
								{ id: "daily", value: _("daily") },
								{ id: "weekly" },
								{ id: "monthly" },
								{ id: "yearly" },
								{ id: "work", value: _("every working day") },
								{ id: "custom", value: _("custom") },
							],
						},
						on: {
							onChange: (v, o) => this.ToggleSubform(v, o),
						},
					},
				],
			},
		];

		if (this.Compact) {
			rows[1].cols.push({
				localId: "editCustomRec",
				view: "icon",
				icon: "wxi-pencil",
				tooltip: _("Change recurring pattern"),
				hidden: true,
				click: () => this.ShowSubForm(),
			});
		}

		rows.push({ $subview: RecurringFormView, name: "recurringForm" });

		return { localId: "repeatControls", rows: rows };
	}

	/**
	 * Hides or shows time selectors
	 * @param {number} v - the value of 'all day' checkbox (1 or 0)
	 */
	ToggleTimeControls(v) {
		if (v == 1) {
			this.StartTime.hide();
			this.EndTime.hide();
		} else {
			this.StartTime.show();
			this.EndTime.show();
		}
	}

	/**
	 * Shows or hides the subform for editing recurring pattern; the subform is shown when v is 'custom'
	 * @param {string} v - the new recurring pattern option
	 * @param {string} o  - the new recurring pattern option
	 */
	ToggleSubform(v, o) {
		if (v === "custom") {
			if (this.Compact) {
				this.$$("editCustomRec").show();
				if (o) this.ShowSubForm();
			} else {
				this.SubForm.Show();
			}
		} else {
			this.SubForm.Hide();
			if (this.Compact) this.$$("editCustomRec").hide();
		}
	}

	/**
	 * Returns the config of two controls: a selector of calendar and event color
	 * @param {function} _ - the method of the Locale plugin used to translate labels according to the current locale
	 */
	CreateCalendarColor(_) {
		const ui = {
			margin: 8,
			cols: [
				{
					view: "colorpicker",
					label: _("Color"),
					name: "color",
					value: "#1abc9c",
					suggest: {
						type: "colorboard",
						padding: 3,
						body: getPalette(),
					},
				},
			],
		};
		if (this.app.config.calendars) {
			ui.cols.unshift({
				view: "richselect",
				localId: "calendar",
				label: _("Calendar"),
				name: "calendar",
				css: "webix_scheduler_cal_color",
				options: {
					css: "webix_scheduler_cal_color_suggest",
					data: [],
					body: {
						tooltip: () => "",
						template: function(obj) {
							const active = obj.active * 1;
							const css = active ? "" : "class='webix_scheduler_cal_disabled'";
							const tooltip = active
								? ""
								: `webix_tooltip="${_("Inactive calendar")}"`;

							return `<div style="overflow: hidden;" ${tooltip} ${css}>${obj.text ||
								_("(no title)")}
								<span class="webix_scheduler_cal_marker" style="background-color:${obj.color};">
							</span></div>`;
						},
					},
				},
			});
		}
		return ui;
	}

	/**
	 * Returns the config of two controls: a selector of calendar and event color
	 * @param {function} _ - the method of the Locale plugin used to translate labels according to the current locale
	 */
	CreateUnitSelector(_) {
		const ui = {
			view: "multicombo",
			name: "units",
			label: _("Units"),
			options: [],
		};
		return ui;
	}

	/**
	 * Returns the config of controls for selecting date and time
	 * @param {string} type - "start" or "end"
	 * @param {function*} _ - the method of the Locale plugin used to translate labels according to the current locale
	 * @param {Object} aSkin - stores sizes and colors of a currently active Webix skin
	 */
	GetDateTime(type, _, aSkin) {
		return {
			rows: [
				{
					view: "label",
					height: aSkin.labelTopHeight,
					label: type === "start" ? _("Start") : _("End"),
				},
				{
					margin: 8,
					cols: [
						{
							view: "datepicker",
							name: `${type}_date`,
							suggest: {
								type: "calendar",
								icons: false,
							},
						},
						{
							view: "datepicker",
							localId: `${type}Time`,
							name: `${type}_time`,
							type: "time",
							format: "%H:%i",
						},
					],
				},
			],
		};
	}

	CreateSectionSelector(_) {
		return {
			localId: "sections",
			view: "richselect",
			label: _("Section"),
			name: "section",
			options: {
				data: [],
				body: {
					template: "#text#",
				},
			},
		};
	}

	// general form tasks

	/**
	 * Fills the form with relevant values for editing of a selected event or adding a new event
	 * @param {Object} ev - the current state.selected
	 */
	FillForm(ev) {
		this.Id = ev.id;

		this.Form.focus();
		this.Date = ev.date;
		if (typeof this.Date === "string") {
			this.Date = new Date(this.Date.slice(0, this.Date.indexOf("_")) * 1);
		}

		let obj;

		// new record or a new copy of a record
		if (ev.id == "0") {
			if (this.State.clipboard && this.State.clipboard.selected) {
				obj = this.State.clipboard.selected;
				this.$$("repeatControls").hide();
			} else {
				obj = this.NewEvent(ev);
			}

			if (obj.then) {
				return obj.then(res => {
					this.Local.add({ id: ev.id, ...webix.copy(res) });
					this.FillFormContinue(res);
				});
			} else {
				this.Local.add({ id: ev.id, ...webix.copy(obj) });
			}
		} else {
			obj = webix.copy(this.Local.getItem(ev.id));

			const mode = this.getParam("mode", true);
			if (mode) obj = this.AdaptObjToMode(mode, obj);
		}

		this.FillFormContinue(obj);
	}

	/**
	 * Fills the event object with relevant values according to the mode of editing
	 * @param {string} mode - mode in which a recurring event will be edited ("all", "next", "this")
	 * @param {Object} obj - event data
	 */
	AdaptObjToMode(mode, obj) {
		mode = this.ChangeMode(
			mode,
			obj,
			obj.recurring
				? obj.start_date
				: this.Local.getItem(obj.origin_id).start_date,
			obj.recurring ? this.State.selected.date : obj.start_date
		);
		this.setParam("mode", mode, true);

		if (mode === "all" || mode === "next") {
			if (mode === "all" && obj.origin_id) {
				obj = webix.copy(this.Local.getItem(obj.origin_id));
			}

			// if this is a "this" sub-event
			if (!obj.$recurring)
				obj.$recurring = webix.copy(
					this.Local.getItem(obj.origin_id).$recurring
				);

			if (obj.$recurring.COUNT || obj.$recurring.UNTIL) {
				// [FIXME] maybe need to store total UNTIL/COUNT on backend (mostly for dynamic loading)
				obj.$recurring = this.CorrectCountUntil(
					obj,
					mode,
					obj.start_date,
					this.Local
				);
			}
		}

		if (mode === "this" || mode === "next") {
			// split event sequence or cut out an occurrence
			if (this.State.selected.date)
				obj = { ...obj, ...separateSequence(obj, this.State.selected.date) };

			if (mode === "this") {
				obj.$recurring = null;
				obj.recurring = "";
				this.$$("repeatControls").hide();
			}
		}

		return obj;
	}

	/**
	 * Splits start and end dates into date and time parts and fills the form with values
	 * @param {Object} obj - the event data
	 */
	FillFormContinue(obj) {
		obj.start_time = webix.Date.copy(obj.start_date);
		obj.end_time = webix.Date.copy(obj.end_date);

		obj.start_date = webix.Date.datePart(obj.start_date);
		obj.end_date = webix.Date.datePart(obj.end_date);

		if (this.app.config.recurring) this.SetRecurring(obj);

		this.Form.setValues(obj);
	}

	/**
	 * Describes calculated constants of a date
	 * @typedef {Object} DateInfo
	 * @property {number} day - the date (day of month)
	 * @property {number} weekday - the number of a weekday (0-6)
	 * @property {number} month - the number of month (0-11)
	 * @property {number} weekNum - the number of a weekday within a month (e.g. 4th Friday)
	 */

	/**
	 * Calculates constants related to the start date of an event
	 * @param {Date} start - the start date of an event
	 * @returns {DateInfo}
	 */
	GetFromStartDate(start) {
		const res = {
			weekday: start.getDay(),
			day: start.getDate(),
			month: start.getMonth(),
			yearday: getYearDay(start),
		};
		res.weekNum = Math.floor(res.day / 7) + (res.day % 7 ? 1 : 0);
		return res;
	}

	/**
	 * Sets controls and their values for recurring patterns
	 * @param {Object} obj - the event data
	 */
	SetRecurring(obj) {
		const today = this.GetFromStartDate(obj.start_date);
		const roptions = this.ResetRecurringOptions(today);
		this.RecurringOptions.getList().parse(roptions);

		this.SubForm.SetValues(webix.copy(obj), today, true);

		if (!obj.$recurring) {
			obj.rec_option = "none";
		} else if (obj.$recurring.BYDAY === "MO,TU,WE,TH,FR") {
			obj.rec_option = "work";
		} else if (
			!obj.$recurring.UNTIL &&
			!obj.$recurring.COUNT &&
			(!obj.$recurring.INTERVAL || obj.$recurring.INTERVAL == 1) &&
			(obj.$recurring.FREQ === "DAILY" ||
				(obj.$recurring.FREQ === "WEEKLY" &&
					obj.$recurring.BYDAY == WEEKDAYS[today.weekday]) ||
				(obj.$recurring.FREQ === "MONTHLY" &&
					obj.$recurring.BYSETPOS &&
					obj.$recurring.BYSETPOS == today.weekNum &&
					obj.$recurring.BYDAY == WEEKDAYS[today.weekday]) ||
				(obj.$recurring.FREQ === "YEARLY" &&
					obj.$recurring.BYMONTH == today.month + 1 &&
					obj.$recurring.BYMONTHDAY == today.day))
		) {
			obj.rec_option = obj.$recurring.FREQ.toLowerCase();
		} else {
			obj.rec_option = "custom";
		}
	}

	/**
	 * Returns date-dependent options for recurring pattern select list
	 * @param {Date} start - the start date of an event
	 */
	ResetRecurringOptions(start) {
		const _ = this.app.getService("locale")._;
		return [
			{
				id: "weekly",
				value: `${_("weekly, every")} ${
					webix.i18n.calendar.dayFull[start.weekday]
				}`,
			},
			{
				id: "monthly",
				value: `${_("monthly, every")} ${start.weekNum} ${_("week on")} ${
					webix.i18n.calendar.dayFull[start.weekday]
				}`,
			},
			{
				id: "yearly",
				value: `${_("yearly, every")} ${
					webix.i18n.calendar.monthFull[start.month]
				} ${start.day}`,
			},
		];
	}

	/**
	 * Joins date and time into one Date object
	 * @param {Date} date - a date without a time part
	 * @param {Date} time - a date with a time part
	 */
	Join(date, time) {
		return webix.Date.add(date, webix.Date.timePart(time) / 60, "minute", true);
	}

	/**
	 * Returns an object with values for adding a new event
	 * @param {Object} params - current state.selected property
	 */
	NewEvent(params) {
		const start = this.GetStartDate();
		const end =
			this.State.selected.end_date || webix.Date.add(start, 1, "hour", true);

		const obj = {
			start_date: start,
			end_date: end,
			text: "",
			details: "",
		};
		if (this.app.config.recurring) obj.recurring = "";
		if (this.app.config.timeline) {
			obj.section = params.section || this.Sections.getFirstId();
		}

		if (this.app.config.calendars) {
			let active = this.State.active[0];
			if (!active) {
				const first = this.Calendars.getFirstId();
				const wait = first
					? this.Ops.updateCalendar(first, { active: 1 })
					: this.Ops.addCalendar();

				return wait.then(() => {
					obj.calendar = this.State.active[0];
					obj.$color = this.Calendars.getItem(obj.calendar).color;
					return obj;
				});
			}
			obj.calendar = active;
			obj.$color = this.Calendars.getItem(obj.calendar).color;
		} else obj.$color = "#01C2A5"; //same for default color in datacollection

		if (this.app.config.calendars && params.unit) {
			obj.units = params.unit;
		}

		return obj;
	}

	/**
	 * Returns a start date of a new object based on:
	 * 1) the origin of the double click of the user
	 * 2) the current date and time
	 */
	GetStartDate() {
		if (this.Date) {
			return webix.Date.copy(this.Date);
		} else {
			const date = new Date();
			const minutes = Math.ceil(date.getMinutes() / 30) * 30;
			const res = webix.Date.copy(this.State.date);
			res.setHours(date.getHours(), minutes);
			return res;
		}
	}

	/**
	 * Launches saving of changes made in the form
	 * @param {Object} change - an object with changed values
	 */
	UpdateEvent(change) {
		// clear 'dirty' flag so that next change will definitely be 'dirty'
		this.Form.setDirty();

		if (this.Id === "0") {
			const data = this.PrepareOut(change);
			this._inProgress = this.Ops.addEvent(data, true, this.Id);
		} else if (Object.keys(change).length) {
			this._inProgress = this.UpdateExisting(this.Id, change);
		}

		if (this._inProgress) {
			this.app.callEvent("backend:operation", [this._inProgress]);
			this._inProgress
				.then(id => {
					if (id) this.UpdateSelection(id);

					// make event visible if new dates are outside currently selected dates
					this.ChangeDate(change, this.Form.getValues());

					// continue editing in simple mode
					this.setParam("mode", "", true);
				})
				.finally(() => {
					this._inProgress = null;
				});
		}

		return this._inProgress || webix.promise.resolve();
	}

	/**
	 * Updates an existing event
	 * @param {(string|number)} sid - the ID of the event
	 * @param {Object} change - the new data
	 * @returns {Promise}
	 */
	UpdateExisting(sid, change) {
		let mode = this.getParam("mode", true);
		const obj = this.Local.getItem(sid);

		if ((mode === "this" && !this.HasOneOccurrence(obj)) || mode === "next") {
			// cut out occurrence or split event sequence and save as separate events
			change.origin_id = obj.origin_id || obj.id;
			const newEvent = this.PrepareOut(change);

			let recurring = "";
			if (mode === "this") {
				recurring = cutOccurrence(this.State.selected.date, obj);
			} else {
				let params;
				if (obj.recurring) {
					params = [this.State.selected.date, obj];
				} else {
					sid = obj.origin_id;
					params = [newEvent.start_date, this.Local.getItem(sid)];
				}
				recurring = clipSequence(...params);
			}

			return this.Ops.updateEvent(
				sid,
				{ recurring },
				mode,
				webix.Date.dayStart(newEvent.start_date),
				true
			).then(() => {
				if (change.rec_option === "none") this.State.selected.date = null;
				return this.Ops.addEvent(newEvent, true);
			});
		} else {
			// non-recurring and recurring in mode "all"

			if (mode === "all") {
				const recVals = this.Form.getValues().$recurring;
				if (recVals && webix.isUndefined(change.recurring)) {
					delete recVals.EXDATE;
					change.recurring = serialize(recVals); // plus all that's been updated in form silently in FillForm

					this.Form.setValues(
						{
							$recurring: recVals,
							recurring: change.recurring,
						},
						true
					);
					this.SubForm.SetValues({ EXDATE: null });
				}

				if (obj.origin_id) this.Id = this.State.selected.id = obj.origin_id;
			}

			return this.Ops.updateEvent(this.Id, change, mode, undefined, true).then(
				() => {
					if (change.rec_option === "none") this.State.selected.date = null;
				}
			);
		}
	}

	/**
	 * if event was saved as new, update selected silently to avoid triggering listeners
	 * @param {(string|number)} id - the ID of the event
	 */
	UpdateSelection(id) {
		this.State.selected.id = this.Id = id;
		const sdate = this.State.selected.date;
		if (sdate instanceof Date) {
			this.State.selected.date = null;
		} else if (sdate) {
			const fvals = this.Form.getValues();
			this.State.selected.date =
				this.Join(fvals.start_date, fvals.start_time).valueOf() + "_" + id;
		}
	}

	/**
	 * Processes changes made in the form and prepares values for saving to the server
	 * @param {Object} change - an object with changed values
	 */
	PrepareChange(change) {
		if (change.rec_option) this.ProcessRecurringOption(change);
		else if (!webix.isUndefined(change.$recurring)) {
			this.ProcessRecurringPattern(change);
		}

		this.ProcessDates(change);

		if (this.app.config.recurring && change.start_date)
			this.ApplyDatechangeToRecurring(change);

		this.ProcessText(change, "text");
		this.ProcessText(change, "details");

		this._changedValues = change;
	}

	/**
	 * Gets recurring pattern according to the new recurring option
	 * @param {Object} change - an object with changed values
	 */
	ProcessRecurringOption(change) {
		const fvals = this.Form.getValues();

		if (change.rec_option === "none") {
			change.recurring = "";
			change.series_end_date = "";
		} else if (change.rec_option === "custom") {
			if (fvals.$recurring) {
				this.SubForm.SetValues(
					{ ...fvals, ...change },
					this.GetFromStartDate(fvals.start_date)
				);
			}
			change.$recurring = this.SubForm.GetValues();
			this.ProcessRecurringPattern(change);

			if (!fvals.$recurring) {
				this.Form.setValues({ $recurring: change.$recurring }, true);
			}
		} else {
			this.GetRecurring(change.rec_option, change, fvals.start_date);
			change.series_end_date = "";
		}

		delete change.rec_option;
		delete change.$recurring;
	}

	/**
	 * Serializes recurring pattern object and synchronizes dates with the new pattern
	 * @param {Object} change - an object with changed values
	 */
	ProcessRecurringPattern(change) {
		const fvals = this.Form.getValues();
		const recurring = serialize(change.$recurring);
		const old = this.Local.getItem(this.State.selected.id);

		// check if switch from a preset to custom and back and the pattern is the same
		if (!old || recurring !== old.recurring) {
			change.recurring = recurring;
			change.series_end_date = change.$recurring.UNTIL;

			if (change.$recurring.BYDAY) {
				[change.start_date, change.end_date] = datesToWeekdays(
					fvals.start_date,
					fvals.end_date,
					change.$recurring
				);
			}

			this.Form.setValues(change, true);
		}
		delete change.$recurring;
	}

	/**
	 * Sums up dates and time depending on the changes in dates, time and all_day flag
	 * @param {Object} change - an object with changed values
	 */
	ProcessDates(change) {
		const fvals = this.Form.getValues();
		if (webix.isUndefined(change.all_day)) {
			if ((change.start_date || change.start_time) && fvals.all_day == 0) {
				change.start_date = this.Join(fvals.start_date, fvals.start_time);
			}
			if ((change.end_date || change.end_time) && fvals.all_day == 0) {
				change.end_date = this.Join(fvals.end_date, fvals.end_time);
			}
		} else if (change.all_day == 1) {
			change.start_date = fvals.start_date;
			change.end_date = fvals.end_date;
		} else if (change.all_day == 0) {
			change.start_date = this.Join(fvals.start_date, fvals.start_time);
			change.end_date = this.Join(fvals.end_date, fvals.end_time);
		}

		if (change.start_date || change.end_date) this.CorrectDateTime(change);
	}

	/**
	 * Corrects the unchanged field if start > end or end < start
	 * @param {Object} change - an object with changed values
	 */
	CorrectDateTime(change) {
		const fvals = this.Form.getValues();
		const old = this.Local.getItem(this.State.selected.id);
		const ev_length = (old.end_date - old.start_date) / 1000 / 60; // minutes

		const type = change.start_date ? "start" : "end";
		const opposite = type === "start" ? "end" : "start";

		const start = change.start_date || old.start_date;
		const end = change.end_date || old.end_date;

		if ((start >= end && !fvals.all_day) || start > end) {
			change[opposite + "_date"] = webix.Date.add(
				change[type + "_date"],
				(type === "start" ? 1 : -1) * (ev_length || 60),
				"minute",
				true
			);

			// apply also to form
			this.Form.setValues(
				{
					start_date: webix.Date.datePart(change.start_date, true),
					end_date: webix.Date.datePart(change.end_date, true),
					start_time: change.start_time || webix.Date.copy(change.start_date),
					end_time: change.end_time || webix.Date.copy(change.end_date),
				},
				true
			);
		}

		delete change.start_time;
		delete change.end_time;
	}

	/**
	 * Removes trailing spaces from input text
	 * @param {Object} change - an object with changed values
	 * @param {string} field - the name of a text field ('text', 'details')
	 */
	ProcessText(change, field) {
		if (!this.webix.isUndefined(change[field])) {
			change[field] = change[field].replace(/(<[^>]+>|^\s+|\s+$)/gi, "");
			const prev = this.Local.getItem(this.State.selected.id)[field] || "";
			if (change[field] === prev.trim()) delete change[field];
		}
	}

	/**
	 * Synchronizes recurring pattern with new start date
	 * @param {Object} change - an object with changed values
	 */
	ApplyDatechangeToRecurring(change) {
		const today = this.GetFromStartDate(change.start_date);
		// if date was changed after subform change, ignore
		const fvals = this.Form.getValues();
		if (!change.recurring && fvals.$recurring) {
			if (fvals.rec_option === "custom") {
				const recurring = fvals.$recurring;

				if (recurring.BYYEARDAY) {
					recurring.BYYEARDAY = today.yearday;
				}
				if (recurring.BYMONTH) {
					recurring.BYMONTH = today.month + 1;
				}
				if (recurring.BYMONTHDAY) {
					recurring.BYMONTHDAY = today.day;
				}
				if (recurring.BYSETPOS) {
					recurring.BYSETPOS = today.weekNum;
				}
				if (recurring.BYDAY) {
					recurring.BYDAY = WeekdaysToDates(
						change.start_date,
						fvals.$recurring
					);
				}
				if (recurring.UNTIL && recurring.UNTIL < change.start_date) {
					const oldStart = this.Local.getItem(this.State.selected.id)
						.start_date;
					const length = (fvals.$recurring.UNTIL - oldStart) / 1000 / 60; // minutes
					recurring.UNTIL = webix.Date.add(
						change.start_date,
						length,
						"minute",
						true
					);
				}
				change.recurring = serialize(recurring);

				this.SubForm.SetValues({ ...fvals, ...change }, today);
			} else {
				this.GetRecurring(fvals.rec_option, change, fvals.start_date);
				delete change.$recurring;
			}
		}
		this.RecurringOptions.getList().parse(this.ResetRecurringOptions(today));
		this.RecurringOptions.refresh();
	}

	/**
	 * Assigns recurring pattern according to a preset
	 * @param {string} r the ID of a recurring pattern option
	 * @param {Object} change - an object with changed values
	 * @param {Date} start - the start date of the event
	 */
	GetRecurring(r, change, start) {
		let $recurring = this.GetRecurringPreset(r, start);

		if (r === "work" && change.start_date) {
			const BYDAY = WeekdaysToDates(change.start_date, $recurring);
			if ($recurring.BYDAY !== BYDAY) {
				$recurring.BYDAY = BYDAY;
				r = "custom";
			}
		}

		const recurring = serialize($recurring);
		const old = this.Local.getItem(this.State.selected.id);

		// check if switch from a preset to custom and back and the pattern is the same
		if (!old || recurring !== old.recurring) {
			change.$recurring = $recurring;
			change.recurring = recurring;
			this.Form.setValues(
				{
					$recurring: change.$recurring,
					recurring,
					rec_option: r,
				},
				true
			);
		}
	}

	/**
	 * Gets a recurring pattern object for a pattern preset based on the start date of the event
	 * @param {string} r - a recurring option (accepted values: "daily", "weekly", "work", "monthly", "yearly")
	 * @param {Date} start - the start date of an event
	 * @returns {Object} - $recurring
	 */
	GetRecurringPreset(r, start) {
		const today = this.GetFromStartDate(start);
		switch (r) {
			case "daily":
				return { FREQ: "DAILY", INTERVAL: 1 };
			case "weekly":
				return {
					FREQ: "WEEKLY",
					INTERVAL: 1,
					BYDAY: WEEKDAYS[today.weekday],
				};
			case "work":
				return {
					FREQ: "WEEKLY",
					INTERVAL: 1,
					BYDAY: "MO,TU,WE,TH,FR",
				};
			case "monthly":
				return {
					FREQ: "MONTHLY",
					INTERVAL: 1,
					BYSETPOS: today.weekNum,
					BYDAY: WEEKDAYS[today.weekday],
				};
			case "yearly":
				return {
					FREQ: "YEARLY",
					INTERVAL: 1,
					BYMONTH: today.month + 1,
					BYMONTHDAY: today.day,
				};
			default:
				return { FREQ: "DAILY", INTERVAL: 1 };
		}
	}

	/**
	 * Switches Scheduler to another date if the event after change is no longer visible
	 * @param {Object} dirty - an object with changed values
	 * @param {Object} fvals - an object with all current form values
	 */
	ChangeDate(dirty, fvals) {
		if (dirty) {
			// start date of non-repeating event changed or repeating event became non-repeating
			if ((dirty.start_date && !fvals.recurring) || dirty.recurring === "") {
				this.State.selected.date = null;
				this.UpdateStateDate(dirty.start_date || fvals.start_date);
			} else if (dirty.start_date || dirty.recurring) {
				const date = this.GetClosestOccurrenceDate(dirty, fvals);
				this.State.selected.date =
					date.valueOf() + "_" + this.State.selected.id;
				this.UpdateStateDate(date);
			}
		}
	}

	/**
	 * Returns the next closest occurrence date when dates or recurring pattern of an event was changed
	 * @param {Object} dirty - an object with changed data
	 * @param {Object} fvals - an object of all event form values
	 * @returns {Date} the date of the next closest occurrence
	 */
	GetClosestOccurrenceDate(dirty, fvals) {
		let date = dirty.start_date
			? webix.Date.copy(dirty.start_date)
			: this.Join(fvals.start_date, fvals.start_time);
		let end = webix.Date.dayStart(
			this.State.selected.date
				? new Date(this.State.selected.date.split("_")[0] * 1)
				: date
		);
		if (fvals.series_end_date) {
			end = new Date(
				Math.min(webix.Date.add(fvals.series_end_date, -1, "day", true), end)
			);
		}

		const consts = {};
		fillWdays(fvals.$recurring, consts);
		let temp = webix.Date.copy(date);
		while (webix.Date.dayStart(temp) < end) {
			temp = next(temp, fvals.$recurring, consts);
			if (Math.abs(end - temp) < Math.abs(end - date)) {
				date = webix.Date.copy(temp);
			}
		}
		return date;
	}

	/**
	 * Updates state.date according to state.mode
	 * @param {Date} date - new state.date
	 */
	UpdateStateDate(date) {
		date = webix.Date.dayStart(date);
		const state = this.State;
		if (
			state.mode === "day" ||
			state.mode === "units" ||
			(state.mode === "timeline" && state.timelineMode === "day")
		) {
			const sdate = webix.Date.dayStart(state.date);
			if (!webix.Date.equal(date, sdate)) {
				state.date = webix.Date.dayStart(date);
			}
		} else {
			const { start, end } = this.GetModeBounds();
			if (date < start || date >= end) {
				state.date = webix.Date.dayStart(date);
			}
		}
	}

	/**
	 * Returns start and end dates of a currently displayed period
	 */
	GetModeBounds() {
		const state = this.State;
		let mode = state.mode;
		if (mode === "timeline") mode = state.timelineMode;
		else if (mode === "agenda") mode = "month";
		let start = webix.Date[mode + "Start"](state.date);
		let end = webix.Date.add(start, 1, mode, true);

		if (state.mode === "month") {
			start = webix.Date.weekStart(start);
			const weeks = Math.ceil((end - start) / (24 * 60 * 60 * 1000) / 7);
			end = webix.Date.add(start, weeks, "week", true);
		}

		return { start, end };
	}

	/**
	 * Unites changed and unchanged fields into 1 object for saving newly added events
	 * @param {Object} change - an object with changed values
	 */
	PrepareOut(change) {
		let obj = this.Form.getValues();

		if (!obj.all_day) {
			obj.start_date = this.Join(obj.start_date, obj.start_time);
			obj.end_date = this.Join(obj.end_date, obj.end_time);
		}

		obj = webix.extend(obj, change || {}, true);

		if (!(change && change.recurring) && obj.$recurring) {
			obj.$recurring.EXDATE = [];
			obj.recurring = serialize(obj.$recurring);
		}

		if (!obj.origin_id) obj.origin_id = 0;

		// go backend wants a number
		if (this.app.config.timeline && !obj.section) obj.section = 0;

		return obj;
	}

	/**
	 * Removes a temporary local event
	 */
	ClearTempEvent() {
		const sid = this.Id;
		if (sid === "0" && this.Local.exists(sid)) {
			this.Local.remove(sid);
		}
	}

	/**
	 * Closes the form and either the whole right panel with it or returns to Info
	 * @param {boolean} close - if true, the right panel is closed
	 */
	Back(close) {
		this.Form.clear();

		if (close || this.State.selected.id === "0") {
			this.State.selected = null;
		} else {
			const path = this.Compact ? "event.formpopup/" : "../";
			this.show(path + "event.info", { target: "edit" });
		}
	}

	/**
	 * Closes the right panel
	 */
	Close() {
		if (this._inProgress) {
			this._inProgress.then(() => {
				this.Back(true);
			});
		} else {
			const dirty =
				this.Form.isDirty() || (this.Subform && this.SubForm.IsDirty());
			if (this.Compact && dirty) {
				const _ = this.app.getService("locale")._;
				webix
					.confirm({
						text: _("Save changes?"),
						container: this.app.getRoot().$view,
					})
					.then(() => this.Done(true))
					.catch(() => this.Back(true));
			} else this.Back(true);
		}
	}

	/**
	 * Closes the form
	 * @param {boolean} close
	 */
	Done(close) {
		if (this._inProgress) {
			this._inProgress.then(() => {
				this.Back();
			});
		} else if (this.State.selected.id === "0" || this.Compact) {
			let change = {};
			if (this.Compact) {
				if (this._changedValues) change = this._changedValues;

				if (this.SubForm.IsDirty()) {
					change.$recurring = this.SubForm.GetValues();
					change.series_end_date = change.$recurring.UNTIL;
					delete change.rec_option;
				}
			}
			this.UpdateEvent(change).then(() => this.Back(close));
		} else {
			this.Back();
		}
	}

	/**
	 * Waits until some asynchronous operation finishes (if there's any) and then executes 'handler'
	 * @param {function} handler - the code that will be executed
	 */
	Wait(handler) {
		if (this._inProgress) {
			this._inProgress.then(handler);
		} else {
			handler.call();
		}
	}

	// compact

	/**
	 * Shows a subform with controls for editing recurring pattern in a window
	 */
	ShowSubForm() {
		this.SubForm.Show();
		const _ = this.app.getService("locale")._;
		const subform = this.SubForm.getRoot();

		const aSkin = webix.skin.$active;
		webix.fullscreen.set(subform, {
			head: {
				view: "toolbar",
				padding: {
					left: aSkin.layoutPadding.form - (aSkin.inputHeight - 20) / 2,
					right: 14,
				},
				elements: [
					{ view: "icon", icon: "shi-back", click: () => this.HideSubPop() },
					{ view: "label", label: _("Change recurring pattern") },
					{
						view: "button",
						value: _("Done"),
						width: 130,
						css: "webix_primary",
						click: () => this.HideSubPop(),
					},
				],
			},
		});

		const win = subform.getTopParentView();
		win.define({ css: "webix_scheduler_subform_popup" });
	}

	/**
	 * Hides the window with the subform
	 */
	HideSubPop() {
		this.SubForm.Hide();
		webix.fullscreen.exit();
	}
}
