import BaseView from "./baseview";

export default class ChartView extends BaseView {
	config() {
		const _ = (this._ = this.app.getService("locale")._);

		const labelField = {
			rows: [
				{ view: "label", label: _("Labels column") },
				{
					view: "richselect",
					name: "label",
					options: [],
				},
			],
		};

		const valueField = {
			rows: [
				{ view: "label", label: _("Values column") },
				{
					view: "richselect",
					name: "value",
					options: [],
				},
			],
		};

		const colorField = {
			rows: [
				{ view: "label", label: _("Color column") },
				{
					view: "richselect",
					name: "color",
					options: [],
				},
			],
		};

		return {
			view: "form",
			scroll: false,
			padding: { top: webix.skin.$active.layoutPadding.toolbar },
			rows: [valueField, labelField, colorField, {}],
		};
	}

	init() {
		this.State = this.getParam("state", true);
		this.Local = this.app.getService("local");

		let fields = (this.Fields = (
			this.State.publicFields || this.State.fields
		).map(a => ({
			...a,
		})));
		let valueFields = fields.filter(
			field => field.type == "number" && !field.key
		);
		if (!valueFields.length)
			valueFields = fields.filter(field => field.type == "number");
		const els = this.getRoot().elements;

		const lc = els.label;
		lc.getList().parse(fields);
		lc.getList().type.template = obj => this.ListTemplate(obj);
		lc.setValue(this.State.meta.label || fields[0].id);

		const vc = els.value;
		vc.getList().parse(valueFields);
		vc.getList().type.template = obj => this.ListTemplate(obj);
		vc.setValue(this.State.meta.value || valueFields[0].id);

		const cc = els.color;
		cc.getList().parse(valueFields);
		cc.getList().type.template = obj => this.ListTemplate(obj);
		cc.setValue(this.State.meta.color || valueFields[0].id);

		this.getRoot().attachEvent("onChange", () => this.OnChange());
		if (!this.State.meta.value) this.OnChange();
	}

	OnChange() {
		this.Apply({ ...this.State.meta, ...this.getRoot().getValues() });
	}

	Apply(meta) {
		const check = [meta.value, meta.label, meta.color];
		const columns = this.Fields.filter(a => check.indexOf(a.id) !== -1);

		this.State.$batch({
			meta,
			columns,
		});
	}

	ListTemplate(obj) {
		return (
			obj.name +
			(obj.model
				? "<span class='webix_rpt_sources_path'>" + obj.model + "</span>"
				: "")
		);
	}
}
