webix.protoUI(
	{
		name: "groupselect",
		$cssName: "text webix_rpt_groupselect",
		defaults: {
			selectIcon: true,
			modifiers: {},
		},
		renderModifier(id) {
			const mid = this._modifiers[id];
			if (mid) {
				const option = this.config.modifiers.find(a => a.id === mid);
				return (
					"<div class='webix_rpt_date_selector'>" +
					(option ? option.value : "") +
					(this.config.selectIcon
						? "<span class='webix_icon wxi-angle-down'></span>"
						: "") +
					"</div> <span class='webix_rpt_groupselect_sep'> | </span> "
				);
			}
			return "";
		},
		$renderTag(text, width, height, value) {
			let content = this.renderModifier(value);
			content +=
				"<span>" +
				text +
				"</span><span class='webix_multicombo_delete webix_rpt_groupselect_delete' role='button' aria-label='" +
				webix.i18n.aria.removeItem +
				"'></span>";
			return (
				"<li class='webix_multicombo_value' style='line-height:" +
				height +
				"px;' optvalue='" +
				webix.template.escape(value) +
				"'>" +
				content +
				"</li>"
			);
		},
		$init() {
			this._modifiers = {};
			webix.extend(this.on_click, {
				webix_rpt_date_selector: function(e, id, node) {
					let value = webix.html.locate(e, "optvalue");
					this.callEvent("onModifierClick", [value, e, node]);
					return false;
				},
			});
		},
		setValue(value) {
			if (value) {
				if (typeof value !== "string" && typeof value[0] !== "string") {
					this._modifiers = {};
					value = value.map(a => {
						this._modifiers[a.id] = a.mod || null;
						return a.id;
					});
				}
				webix.ui.multicombo.prototype.setValue.call(this, value);
			} else {
				this._modifiers = {};
			}

			return webix.ui.multicombo.prototype.setValue.call(this, value);
		},
		getModifier(id) {
			return this._modifiers[id];
		},
		setModifier(id, value, silent) {
			this._modifiers[id] = value;
			if (!silent) this.callEvent("onChange", []);
			this.refresh();
		},
		// [FIXME] bug in multicombo repainting during view replacing
		$setSize: function(x, y) {
			var config = this.config;

			const [px, py] = this._custom_last_size || [0, 0];
			if (x == px && y == py) return;
			this._custom_last_size = [x, y];

			if (webix.ui.view.prototype.$setSize.call(this, x, y)) {
				if (!x || !y) return;
				if (config.labelPosition == "top") {
					config.labelWidth = 0;
				}
				this.render();
			}
		},
	},
	webix.ui.multicombo
);
