import { JetView } from "webix-jet";

export default class SummaryPopupView extends JetView {
	constructor(app, masterName) {
		super(app);
		this.Name = masterName;
	}
	config() {
		return {
			view: "popup",
			body: {
				view: "list",
				css: "webix_rpt_popup_menu",
				width: 160,
				autoheight: true,
				template: obj => this.GetTemplate(obj),
				click: id => {
					this.ClickHandler(id);
					this.getRoot().close();
				},
				data: this.GetOptions(),
			},
		};
	}

	ClickHandler(id) {
		this.getParentView().ApplyAction(id);
	}
	Show(node, data, index) {
		this.SetActionStates(data, index);
		this.getRoot().show(node);
	}
	GetOptions() {
		const _ = this.app.getService("locale")._;
		return [{ id: "delete", value: _("Delete"), icon: "rpi-delete" }];
	}
	SetActionStates(data) {
		const popup = this.getRoot();
		const list = popup.getBody();
		list.data.each(action => {
			if (data.length == 1) list.disableItem(action.id);
			else list.enableItem(action.id);
		});
	}
	GetTemplate(obj) {
		return `<span class='webix_icon ${obj.icon}'></span> ${obj.value}`;
	}
}
