import { JetView } from "webix-jet";
import Toolbar from "./common/toolbar";

export default class TableView extends JetView {
	config() {
		const page = {
			type: "wide",
			margin: 5,
			cols: [{}],
		};

		return { rows: [Toolbar, page] };
	}

	init() {
		this.State = this.getParam("state", true);
		this.Local = this.app.getService("local");
	}
	ready() {
		const parts = this.getRoot().getChildViews();
		this.Bar = parts[0];
		this.Host = parts[1];

		this.on(this.State.$changes, "module", mod => this.Show(mod));
		this.on(this.app, "exportModule", mode => this.Export(mode));
	}

	Show(mod) {
		const host = this.Host;
		if (mod && mod.meta.value && mod.type === "heatmap") {
			const config = this.GetChartConfig(mod);
			if (!config.view) return;
			this.Bar.show();
			this.GetChartData(mod).then(([data, options]) => {
				config.data = this.FormatLabels(webix.copy(data), options);
				if (data.length && mod.meta.color)
					config.type.cssClass = this.CssFactory(data, mod.meta.color);

				webix.ui([config], host);
				this.Chart = host.getChildViews()[0];
			});
		} else {
			webix.ui([{ template: " " }], host);
			this.Bar.hide();
			this.Chart = null;
		}
	}

	GetChartConfig(mod) {
		const { meta, columns } = mod;

		const obj = {
			view: "treemap",
			css: "webix_rpt",
			value: obj => obj[meta.value],
			tooltip: this.TooltipFactory(meta, columns),
			type: {},
		};

		if (meta.label) obj.type.template = obj => obj[meta.label];

		return obj;
	}

	GetChartData(mod) {
		return Promise.all([
			this.Local.getData(
				this.Local.getDataConfig(mod),
				this.State.mode == "edit"
			),
			this.Local.getOptions(mod.columns),
		]);
	}

	CssFactory(data, prop) {
		var min = Infinity;
		var max = -Infinity;

		for (var i = 0; i < data.length; i++) {
			const v = data[i][prop];
			if (v > max) max = v * 1;
			if (v < min) min = v * 1;
		}

		const step = (max - min) / 4;
		const bases = [max - step, max - step * 2, max - step * 3, min];

		return function(obj) {
			const v = obj[prop];
			for (var i = 0; i < bases.length; i++) if (v >= bases[i]) return "l" + i;
		};
	}

	TooltipFactory(obj, columns) {
		const valueColumn = columns.find(a => a.id === obj.value);
		const colorColumn = columns.find(a => a.id === obj.color);
		if (!valueColumn || !colorColumn) return "";
		const props = [obj.value, valueColumn.name];
		if (obj.color && obj.color != obj.value)
			props.push(obj.color, colorColumn.name);

		return function(obj) {
			let out = "";
			for (let i = 0; i < props.length; i += 2) {
				out += (i ? ", " : "") + props[i + 1] + ": " + obj[props[i]];
			}

			return out;
		};
	}
	/**
	 * Exports chart to png image or PDF
	 * @param {String} mode - export mode ( "png" or PDF "table"/"image"/"all"),
	 */
	Export(mode) {
		if (this.Chart) {
			if (mode == "png") webix.toPNG(this.Chart);
			else if (mode == "pdf") {
				let params = { autowidth: true, display: "image" };
				webix.toPDF(this.Chart, params);
			}
		}
	}
	FormatLabels(data, options) {
		const labelColumn = this.State.module.meta.label;
		const labelOptions =
			labelColumn && options.length
				? options.find(op => op.field == labelColumn)
				: null;

		if (labelOptions)
			data.forEach(item => {
				const option = labelOptions.data.find(o => o.id == item[labelColumn]);
				item[labelColumn] = option ? option.value : item[labelColumn];
			});
		else {
			const field = this.State.module.fields.find(
				field => field.id == labelColumn
			);
			if (field && field.type == "date") {
				data.forEach(item => {
					item[labelColumn] = webix.i18n.dateFormatStr(
						new Date(item[labelColumn])
					);
				});
			}
		}
		return data;
	}
}
