import { JetView } from "webix-jet";
import MonthListView from "./list";
import CalendarView from "./compact";
import MonthEventsView from "./events";
import MonthHeaderView from "./header";

export default class MonthView extends JetView {
	config() {
		const compact = this.getParam("compact", true);
		const views = compact
			? [CalendarView, MonthListView]
			: [MonthHeaderView, MonthEventsView];

		return {
			rows: views,
		};
	}
	init() {
		this.Data = this.app.getService("local");
		const state = this.app.getState();
		this.on(state.$changes, "date", value => this.RefreshData(value));
		this.on(state.$changes, "active", () => this.RefreshData(state.date));
		this.on(this.app, "events:refresh", () => this.RefreshData(state.date));

		// updates made in form
		const events = this.app.getService("local").events(true);
		this.on(
			events.data,
			"onStoreUpdated",
			(_i, _o, mode) => mode && this.RefreshData(state.date)
		);
	}
	RefreshData(value) {
		let start = webix.Date.monthStart(value);
		let end = webix.Date.add(start, 1, "month", true);

		start = webix.Date.weekStart(
			new Date(start.getFullYear(), start.getMonth(), 1)
		);
		const weeks = Math.ceil((end - start) / (24 * 60 * 60 * 1000) / 7);
		end = webix.Date.add(start, weeks, "week", true);

		this.Data.getEvents(start, end).then(data => {
			if (this.app) this.setParam("data", { start, weeks, data }, true);
		});
	}
}
