import { JetView } from "webix-jet";
import { events, timeStart } from "helpers/templates";
import { isMultiDay } from "helpers/dates";
import { getParams } from "helpers/recurring";

export default class UnitListView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;

		return {
			view: "unitlist",
			localId: "list",
			uniteBy: () => "custom",
			type: {
				template: events(_),
				templateHeader: obj => this.TemplateHeader(obj),
				timeStart: obj => this.TimeStart(obj),
				height: 50,
			},
			on: {
				onUnits: () => this.SetUnits(),
				onItemClick: (id, node) => this.ShowEvent(id, node),
			},
		};
	}

	init() {
		this.State = this.app.getState();
		this.List = this.$$("list");
		webix.extend(this.List, webix.OverlayBox);
	}

	ShowEvent(id, node) {
		const obj = this.List.getItem(id);
		const sel = getParams(obj);
		sel.node = node;
		this.State.selected = sel;
	}

	ToggleOverlay() {
		if (!this.List.count()) {
			const _ = this.app.getService("locale")._;
			this.List.showOverlay(_("No Events"));
		} else {
			this.List.hideOverlay();
		}
	}

	SetUnits() {
		let units = this.List.units;
		const data = this.data.all;

		// ignore clearAll
		if (Object.keys(units).length) {
			this.List.units = {};
			let start = this.data.start;
			const end = this.data.end;

			while (start < end) {
				const next = webix.Date.add(start, 1, "day", true);

				let result = data
					.filter(
						d =>
							d.start_date < next &&
							(d.end_date > start || (d.end_date >= start && d.all_day))
					)
					.map(ev => ev.id);
				if (result.length) {
					this.List.units[start.valueOf()] = result;
				}

				start = webix.Date.add(start, 1, "day", true);
			}
		}
	}

	TimeStart(ev) {
		const _ = this.app.getService("locale")._;
		if (isMultiDay(ev)) return _("All Day");
		else return timeStart(ev.start_date, ev.end_date);
	}
}
