import { JetView } from "webix-jet";
import MultidayEventList from "./multiday";
import DayEventsView from "./events";
import HourscaleView from "../common/hourscale";
import { isMultiDay } from "helpers/dates";

export default class DayView extends JetView {
	config() {
		const header = {
			view: "template",
			localId: "header",
			css: "webix_scheduler_day_header",
			height: 32,
			template: obj => {
				return obj.date ? webix.Date.dateToStr("%l")(obj.date) : "";
			},
		};

		const config = {
			css: "webix_scheduler_day",
			rows: [
				header,
				{
					localId: "multi",
					hidden: true,
					cols: [MultidayEventList],
				},
				{
					localId: "scroll",
					view: "scrollview",
					css: "webix_scheduler_day_scroll",
					body: {
						cols: [HourscaleView, DayEventsView],
					},
				},
			],
		};

		return config;
	}

	init() {
		const state = this.app.getState();
		this.on(state.$changes, "date", value => this.RefreshData(value));
		this.on(state.$changes, "active", () => this.RefreshData(state.date));
		this.on(this.app, "events:refresh", () => this.RefreshData(state.date));

		// updates made in form
		const events = this.app.getService("local").events(true);
		this.on(
			events.data,
			"onStoreUpdated",
			(_i, _o, mode) => mode && this.RefreshData(state.date)
		);

		this.on(state.$changes, "selected", v => {
			if (v && !state.clipboard) this.ScrollScale(v);
		});
	}

	RefreshData(value) {
		this.$$("header").setValues({ date: value });

		this.GetDay(value).then(data => {
			// this.app will be null if compact trigger was called in main.js
			if (this.app) this.setParam("data", data, true);

			if (data.multi.length) {
				this.$$("multi").show();
			} else this.$$("multi").hide();
		});
	}

	GetDay(date) {
		const end = webix.Date.add(date, 1, "day", true);
		return this.app
			.getService("local")
			.getEvents(date, end)
			.then(evs => {
				const multi = [],
					single = [];
				evs.forEach(ev => {
					if (isMultiDay(ev)) {
						multi.push(ev);
					} else {
						single.push(ev);
					}
				});
				return { multi, single };
			});
	}

	ScrollScale(v) {
		if (v.id === "0" && !v.date) {
			setTimeout(() => {
				const y = (new Date().getHours() + 2) * 40;
				this.$$("scroll").scrollTo(0, y);
			}, 100);
		}
	}
}
