import { JetView } from "webix-jet";
import { createState, ignoreInitial } from "jet-restate";
import Summaries from "./summary";
import "../../../helpers/groupselect";
import DatePopupView from "./datepopup";

export default class OtherSettingsView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const groupBy = {
			margin: 5,
			rows: [
				{
					autoheight: true,
					borderless: true,
					template: _("Specify columns to group by and output(s)"),
					css: "webix_rpt_description",
				},
				{
					view: "groupselect",
					localId: "groupBy",
					modifiers: this.GetDateOptions(),
					options: [],
					on: {
						onChange: () => this.ChangeGroupBy(),
						onModifierClick: (id, e, node) =>
							this.ShowModifierPopup(id, e, node),
					},
				},
			],
		};

		return {
			view: "form",
			borderless: true,
			padding: 0,
			elements: [
				{
					localId: "groupByLayout",
					margin: webix.skin.$active.layoutMargin.form,
					rows: [groupBy, Summaries],
				},
			],
		};
	}

	init() {
		this.State = this.getParam("state", true);

		const groupState = this.State.group || {};
		this.GroupState = createState({
			by: groupState.by || null,
			columns: groupState.columns || null,
		});

		this.LoadColumns();

		this.setParam("group", this.GroupState);
		this.on(this.GroupState.$changes, "*", ignoreInitial(() => this.Apply()));
	}

	ChangeGroupBy() {
		const group = this.$$("groupBy");
		const list = group.getPopup().getList();
		let arr = group.getValue();
		arr = arr ? (typeof arr === "string" ? arr.split(",") : arr) : [];

		this.GroupState.by = arr.map(a => {
			let mod = group.getModifier(a);
			if (list.getItem(a).type === "date" && !mod) {
				mod = "year";
				group.setModifier(a, mod, true);
			}

			const obj = { id: a };
			if (mod) obj.mod = mod;

			return obj;
		});
	}

	FilterGroupByColumns(column) {
		return column.id != "id";
	}

	LoadColumns() {
		const columns = this.State.fields.map(a => ({
			id: a.id,
			value: a.name,
			type: a.type,
			model: a.model,
		}));
		const groupBy = this.$$("groupBy");
		const list = groupBy.getPopup().getList();
		list.type.template = obj => this.ListTemplate(obj);
		list.parse(columns);

		this.$$("groupBy").setValue(this.GroupState.by);
	}

	ListTemplate(obj) {
		return (
			obj.value +
			(obj.model
				? "<span class='webix_rpt_sources_path'>" + obj.model + "</span>"
				: "")
		);
	}

	ShowModifierPopup(id, e, node) {
		if (!this.DatePopup)
			this.DatePopup = this.ui(new DatePopupView(this.app, this.$$("groupBy")));
		webix.delay(() => this.DatePopup.Show(node, id));
	}

	GetDateOptions() {
		const _ = this.app.getService("locale")._;
		return [
			{ id: "day", value: _("Day") },
			{ id: "month", value: _("Month") },
			{ id: "year", value: _("Year") },
			{ id: "yearmonth", value: _("Year") + "/" + _("Month") },
			{
				id: "yearmonthday",
				value: _("Year") + "/" + _("Month") + "/" + _("Day"),
			},
		];
	}
	Apply() {
		const s = this.GroupState;
		this.State.group = {
			by: s.by || null,
			columns: s.columns || [],
		};
	}
}
