import { JetView } from "webix-jet";
import NavBarView from "views/bars/nav";
import DateView from "views/bars/date";
import AddView from "views/bars/add";
import NavPopupView from "views/bars/navpopup";

export default class TopBarView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		this.Compact = this.getParam("compact", true);

		const todayIcon = {
			view: "icon",
			icon: "wxi-calendar",
			batch: "compact",
			tooltip: _("Today"),
			click: () => this.SetToday(),
		};
		const todayBtn = {
			view: "align",
			align: "middle",
			batch: "full",
			body: {
				width: 80,
				view: "button",
				css: "webix_transparent",
				value: _("Today"),
				click: () => this.SetToday(),
			},
		};

		const ui = {
			view: "toolbar",
			css: "webix_scheduler_toolbar webix_dark",
			responsive: true,
			paddingY: 0,
			paddingX: 4,
			height: webix.skin.$active.tabbarHeight + 10,
			visibleBatch: this.Compact ? "compact" : "full",
			cols: [
				{
					view: "icon",
					icon: "shi-menu",
					click: () => this.ShowNav(),
					hidden: !this.Compact && !this.app.config.calendars,
					responsiveCell: false,
				},
				{
					view: "proxy",
					localId: "add",
					borderless: true,
					body: AddView,
					batch: "full",
					//[ToDO] makes sence to hide it on medium screens to allows smaller compactWidth
					//in this case click-to-add behaviour for modes must be implemenetd
					responsiveCell: false,
				},
				{},
				{
					view: "proxy",
					borderless: true,
					body: DateView,
					responsiveCell: false,
				},
				{},
				todayBtn,
				{ width: 12 },
				NavBarView,
				todayIcon,
			],
		};

		return ui;
	}
	init() {
		this.State = this.app.getState();

		if (this.Compact) this.NavPopup = this.ui(NavPopupView);

		this.on(this.State.$changes, "readonly", value => {
			this.ToggleAdd(value);
		});
	}
	SetToday() {
		this.State.date = webix.Date.dayStart(new Date());
	}
	ShowNav() {
		if (this.Compact) this.NavPopup.Show();
		else this.app.callEvent("show:panel");
	}
	ToggleAdd(value) {
		const add = this.$$("add");
		if (value) add.hide();
		else if (!this.Compact) add.show();
	}
}
