webix.protoUI(
	{
		name: "text-search",
		$cssName: "search",
		defaults: {
			closeIcon: "wxi-close",
			template: function(obj, common) {
				var text = common.$renderInput(obj);
				if (obj.badge || obj.badge === 0)
					text = text.replace(
						new RegExp("</div>$"),
						"<span class='webix_badge'>" + obj.badge + "</span></div>"
					);
				return text;
			},
		},

		on_click: {
			webix_input_icon: function(e) {
				const css = e.target.className;
				this.getInputNode().focus();

				if (css.indexOf(this.config.closeIcon) > -1) {
					this.setValue("");
					this.callEvent("onCloseIconClick", [e]);
					this.hideCloseIcon();
				} else {
					this.callEvent("onSearchIconClick", [e]);
				}
			},
		},
		$init() {
			this.attachEvent("onTimedKeyPress", () => this._onSearch());
			this.attachEvent("onChange", () => this._onSearch());
		},
		_onSearch() {
			const v = this.getValue();
			if (v) this.showCloseIcon();
			else this.hideCloseIcon();

			this.callEvent("onSearchChange", [v]);
		},
		setIcon(icon) {
			const nodes = this.$view.getElementsByClassName("webix_input_icon");
			if (nodes.length) nodes[0].className = "webix_input_icon " + icon;
		},
		showCloseIcon() {
			this.setIcon(this.config.closeIcon);
		},
		hideCloseIcon() {
			this.setIcon(this.config.icon);
		},
		setBadge(value) {
			let node = this.$view.querySelector(".webix_badge");
			if (value || value === 0) {
				if (!node) {
					node = webix.html.create(
						"SPAN",
						{ class: "webix_badge" },
						"" + value
					);
					this.$view.querySelector(".webix_el_box").appendChild(node);
				} else node.innerHTML = "" + value;
			} else if (node) webix.html.remove(node);
			this.config.badge = value;
		},
	},
	webix.ui.search
);
