import { JetView } from "webix-jet";
import YearCalendarView from "./calendar";

export default class YearView extends JetView {
	config() {
		return {
			view: "scrollview",
			body: {
				padding: 20,
				view: "flexlayout",
				css: "webix_scheduler_year",
				cols: this.GetCalendars(),
			},
		};
	}

	/**
	 * Creates 12 calendars for each month of the current year
	 * @returns {Array} an array of calendar views
	 */
	GetCalendars() {
		const calendars = [];
		for (let i = 0; i < 12; i++) {
			calendars.push(
				new (this.app.dynamic(YearCalendarView))(this.app, { month: i })
			);
		}
		return calendars;
	}
}
