export function shrinkTo(start, end) {
	return ev => {
		const before = ev.start_date <= start;
		const after = ev.end_date >= end;

		if (before || after) {
			const t = webix.copy(ev);
			if (before) t.start_date = start;
			if (after) t.end_date = end;
			return t;
		}

		return ev;
	};
}

export function isToday(date) {
	return webix.Date.equal(
		webix.Date.datePart(date),
		webix.Date.datePart(new Date())
	);
}

export function isMultiDay(ev) {
	return (
		ev.all_day ||
		(ev.start_date.getDate() != ev.end_date.getDate() &&
			ev.end_date - ev.start_date >= 1000 * 60 * 60 * 24 * 1)
	);
}

/**
 * Returns the difference between dates
 * @param {Date} start - date 1
 * @param {Date} end - date 2
 * @returns {number} difference between dates in days
 */
export function daysBetweenInclusive(start, end) {
	const millisecondsPerDay = 24 * 60 * 60 * 1000;
	return Math.round(
		(webix.Date.dayStart(end) - webix.Date.dayStart(start)) / millisecondsPerDay
	);
}

/**
 * Checks if the dates are the same
 * @param {Date} adate
 * @param {Date} bdate
 * @returns {Boolean}
 */
export function isSameDate(adate, bdate) {
	return webix.Date.equal(
		webix.Date.dayStart(adate),
		webix.Date.dayStart(bdate)
	);
}

export function getYearDay(date) {
	const start = webix.Date.yearStart(date);
	return (date - start) / 1000 / 60 / 60 / 24 + 1;
}
