import { JetView } from "webix-jet";
import getPalette from "helpers/palette";

export default class SidePopupView extends JetView {
	config() {
		const compact = this.getParam("compact", true);
		const _ = this.app.getService("locale")._;

		const save = {
			view: "button",
			value: _("Save"),
			align: "right",
			inputWidth: 100,
			css: "webix_primary",
			click: () => this.Save(),
		};

		const aSkin = webix.skin.$active;
		const compactBar = {
			view: "toolbar",
			padding: {
				left: aSkin.layoutPadding.form - (aSkin.inputHeight - 20) / 2,
				right: aSkin.layoutPadding.form,
			},
			elements: [
				{ view: "icon", icon: "wxi-close", click: () => this.getRoot().hide() },
				{},
				save,
			],
		};

		const controls = [
			{
				view: "button",
				localId: "removeBtn",
				value: _("Delete"),
				css: "webix_danger webix_scheduler_danger",
				hidden: true,
				align: compact ? "center" : "left",
				inputWidth: compact ? 330 : 100,
				click: () => this.Delete(),
			},
		];

		if (!compact) controls.push(save);

		const ui = this.app.getService("jet-win").updateConfig({
			view: "window",
			width: 400,
			close: true,
			borderless: compact,
			fullscreen: compact,
			css: "webix_scheduler_cal_popup",
			head: compact ? compactBar : _("Edit calendar"),
			body: {
				view: "form",
				localId: "calendars:form",
				elements: [
					{ view: "text", label: _("Title"), name: "text" },
					{
						view: "colorpicker",
						label: _("Color"),
						name: "color",
						suggest: {
							type: "colorboard",
							padding: 3,
							body: getPalette(),
						},
					},
					{ view: "checkbox", label: _("Active"), name: "active" },
					{ cols: controls },
				],
			},
			on: {
				onHide: () => {
					this.Form.clear();
					this.$$("removeBtn").hide();
				},
			},
		});

		if (compact) ui.body.elements.splice(3, 0, {});

		return ui;
	}
	init() {
		this.Cals = this.app.getService("local").calendars(true);
		this.Ops = this.app.getService("operations");
		this.Form = this.$$("calendars:form");
	}

	Show(id) {
		if (id) {
			const obj = this.Cals.getItem(id);
			this.Form.setValues(obj);
			this.$$("removeBtn").show();
		} else {
			this.Form.setValues({
				color: "#997CEB",
				active: 1,
			});
		}
		this.getRoot().show();
		this.Form.focus();
	}

	Save() {
		const obj = this.Form.getValues();
		if (obj.id) this.Ops.updateCalendar(obj.id, obj);
		else this.Ops.addCalendar(obj);
		this.getRoot().hide();
	}

	Delete() {
		const obj = this.Form.getValues();
		const _ = this.app.getService("locale")._;

		webix
			.confirm({
				text: _("Do you really want to remove this calendar?"),
				container: this.app.getRoot().$view,
			})
			.then(() => {
				this.Ops.removeCalendar(obj.id);
				this.getRoot().hide();
			});
	}
}
