import { JetView } from "webix-jet";
import { templateStart } from "helpers/templates";

export default class MonthHeaderView extends JetView {
	config() {
		return {
			view: "dataview",
			css: "webix_scheduler_calendar_header",
			xCount: 7,
			yCount: 1,
			width: 0,
			height: 32,
			scroll: false,
			type: {
				height: "auto",
				width: "auto",
				templateStart,
			},
		};
	}

	init(view) {
		view.type.master = view;
		view.parse(this.GetHeaderData());
	}

	GetHeaderData() {
		const data = [];
		const k = webix.Date.startOnMonday ? 1 : 0;

		for (let i = 0; i < 7; i++)
			data.push({ value: webix.i18n.calendar.dayShort[(k + i) % 7] });

		data[data.length - 1]["right"] = true;
		return data;
	}
}
