import { JetView } from "webix-jet";

export default class SummaryPopupView extends JetView {
	constructor(app, master) {
		super(app);
		this.Master = master;
	}
	config() {
		const _ = this.app.getService("locale")._;
		return {
			view: "popup",
			autoheight: true,
			fitMaster: true,
			body: {
				rows: [
					{
						padding: { top: 5, bottom: 5, left: 10, right: 10 },
						cols: [
							{
								view: "text",
								localId: "search",
								placeholder: _("Search"),
							},
						],
					},
					{
						view: "list",
						scroll: true,
						yCount: 5,
						autoheight: true,
						select: true,
						type: {
							template: obj => this.GetTemplate(obj),
						},
						on: {
							onItemClick: id => {
								const item = this.List.getItem(id);
								this.AddColumn(item);
								this.View.hide();
							},
							onEnter: () => {
								const item = this.List.getSelectedItem();
								if (item) {
									this.AddColumn(item);
									webix.delay(() => this.View.hide());
								}
							},
						},
					},
				],
			},
		};
	}

	init() {
		this.View = this.getRoot();
		const views = this.View.getBody().getChildViews();
		this.State = this.getParam("state", true);
		this.BucketState = this.getParam("buckets", true);
		this.Text = views[0].getChildViews()[0];
		this.List = views[1];
		this.Text.attachEvent("onTimedKeyPress", () => {
			this.Find(this.Text.getValue());
		});
	}
	AddColumn(column) {
		const t = [...this.BucketState.buckets];
		t.push({ column: column.id, options: [] });
		this.BucketState.buckets = t;
	}
	Show(node) {
		node = node.firstChild.firstChild;
		this.View.config.width = node.offsetWidth - 2;
		this.View.adjust();
		this.View.show(node);
		this.Text.focus();
		this.List.clearAll();
		this.List.parse(this.GetColumns());
	}
	GetColumns() {
		let columns = this.State.fields;
		if (this.State.publicFields)
			columns = columns.filter(a =>
				this.State.publicFields.find(b => b.id == a.id)
			);
		return columns.map(a => ({
			id: a.id,
			value: a.name,
			type: a.type,
			model: a.model,
			disabled:
				this.BucketState.buckets &&
				this.BucketState.buckets.find(b => b.column == a.id),
		}));
	}

	GetTemplate(obj) {
		return (
			obj.value +
			(obj.model
				? "<span class='webix_rpt_sources_path'>" + obj.model + "</span>"
				: "")
		);
	}

	Find(value) {
		value = value.toLowerCase();
		if (value)
			this.List.filter(item => item.value.toLowerCase().indexOf(value) > -1);
		else this.List.filter("");
	}
}
