const icalFormat = webix.Date.dateToStr("%Y%m%dT000000Z");

export function parse(string) {
	//FREQ=DAILY;INTERVAL=2
	const parts = string.split(";");
	const config = {};
	for (let i = 0; i < parts.length; i++) {
		const [code, name] = parts[i].split("=");
		config[code] = name;
	}

	if (config.UNTIL) config.UNTIL = strToDate(config.UNTIL);
	if (config.EXDATE) {
		config.EXDATE = config.EXDATE.split(",").map(d => strToDate(d));
	}

	return config;
}

function strToDate(str) {
	return new Date(
		str.substr(0, 4) * 1,
		str.substr(4, 2) * 1 - 1,
		str.substr(6, 2) * 1
	);
}

export function serialize(config) {
	const str = [];
	for (let key in config) {
		let value = config[key];
		if (key === "UNTIL") value = icalFormat(value);

		if (key === "EXDATE") {
			value = value.map(d => icalFormat(d));
			value = value.join(",");
		}
		if (value) str.push(`${key}=${value}`);
	}

	return str.join(";");
}
