import { JetView } from "webix-jet";
import { createState, ignoreInitial } from "jet-restate";
import "../../../helpers/groupselect";
import BucketView from "./bucket";
import BucketPopup from "./bucketpopup";

export default class BucketSettingsView extends JetView {
	config() {
		const _ = (this._ = this.app.getService("locale")._);
		this.Views = {};
		const columns = {
			margin: 5,
			rows: [
				{
					autoheight: true,
					borderless: true,
					template: _("Specify columns and their data buckets"),
					css: "webix_rpt_description",
				},
				{
					view: "button",
					icon: "wxi-plus",
					localId: "addColumn",

					label: _("Add column"),
					click: () => this.ShowColumns(),
				},
			],
		};
		const buckets = {
			view: "datatable",
			css: "webix_rpt_columns_list webix_rpt_bcols",
			localId: "table",
			borderless: true,
			autoheight: true,
			header: false,
			select: false,
			subview: (obj, target) => {
				const buckets = createState({
					id: obj.id,
					options: obj.options,
				});
				this.on(
					buckets.$changes,
					"*",
					ignoreInitial(() => {
						this.Update(obj.id, buckets);
					})
				);

				const sub = (this.Views[obj.id] = new BucketView(
					this.app,
					buckets,
					this.State
				));
				this.ui(sub, { container: target });
				return sub.getRoot();
			},
			columns: [
				{
					fillspace: true,
					template: (item, common) => this.RowTemplate(item, common),
				},
			],
			on: {
				onItemClick: id => this.ToggleSub(id),
				onAfterRender: () => {
					if (this._hasFocus) {
						const el = this._hasFocus;
						this._hasFocus = null;

						if (el && document.contains(el) && el !== document.activeElement)
							el.focus();
					}
				},
				onSubViewOpen: id => this.HideAllEditors(id),
				onSubViewClose: () => this.ResetEditor(),
			},
			onClick: {
				webix_rpt_bucket_delete: (e, id) => {
					webix
						.confirm({
							title: _("Delete"),
							ok: _("Delete"),
							cancel: _("Cancel"),
							text: _("Are you sure to delete?"),
						})
						.then(() => this.RemoveColumn(id));
					return false;
				},
			},
		};

		return {
			view: "form",
			borderless: true,
			padding: 0,
			elements: [
				{
					margin: webix.skin.$active.layoutMargin.form,
					rows: [columns, buckets],
				},
			],
		};
	}

	init() {
		this.State = this.getParam("state", true);
		this.Table = this.$$("table");
		const buckets = this.State.buckets || [];

		this.BucketState = createState({ buckets, newColumn: null });
		this.LoadBuckets();
		this.setParam("buckets", this.BucketState);
		this.on(
			this.BucketState.$changes,
			"*",
			ignoreInitial((v, o) => this.Changed(v, o))
		);
		this.on(this.app, "onAction", (name, action, id) => {
			if (this.getRoot() && name == "bucket" && action == "resize")
				this.Table.refresh(id);
		});
	}
	ShowColumns() {
		if (!this.ColumnList || !this.ColumnList.$view)
			this.ColumnList = this.ui(BucketPopup, this.State);
		this.ColumnList.Show(this.$$("addColumn").$view);
	}

	LoadBuckets() {
		const tableData = this.BucketState.buckets
			? this.BucketState.buckets.map(a => {
					const field = this.State.fields.find(f => f.id == a.column);
					return {
						id: a.column,
						name: field.name,
						model: field.model || null,
						options: a.options,
					};
			  })
			: [];
		this.Table.clearAll();
		this.Table.parse(tableData);
		if (!tableData.length) this.Table.hide();
	}
	AddColumn(id) {
		if (this.Table.config.hidden) this.Table.show();
		const field = this.State.fields.find(f => f.id == id);
		this.Table.add({
			id,
			name: field.name,
			model: field.model || null,
			options: [],
		});
	}
	RemoveColumn(id) {
		this.Table.remove(id);
		const buckets = [...this.BucketState.buckets];
		const i = buckets.findIndex(a => a.column == id);
		buckets.splice(i, 1);
		this.BucketState.buckets = buckets;
		if (!buckets.length) this.Table.hide();
		if (this._ActiveEditor && String(id) == String(this._ActiveEditor))
			this.ResetEditor();
		this.Views[id].destructor();
	}

	ListTemplate(obj) {
		return (
			obj.value +
			(obj.model
				? "<span class='webix_rpt_sources_path'>" + obj.model + "</span>"
				: "")
		);
	}

	Changed(newV, oldV) {
		if (newV.length > oldV.length) {
			const id = newV[newV.length - 1].column;
			this.AddColumn(id);
			this.Table.openSub(id);
		}
		const buckets = this.BucketState.buckets.filter(b => b.options.length);
		this.State.buckets = buckets;
	}

	RowTemplate(item, common) {
		const html = `<div class='webix_rpt_config_row'>
			<span class='webix_rpt_buckets_num'>
				<span class='webix_rpt_bucket_sublabel'>${this._("buckets")}: </span>
					${item.options.length}
				</span>
				<div class='webix_rpt_config_row_name'>
					<span class='webix_rpt_bucket_sublabel'>${this._("column")}: </span>
					${item.name}${
			item.model
				? `<span class='webix_rpt_sources_path'>${item.model}</span>`
				: ""
		}
				</div>
				<span class='webix_icon rpi-delete webix_rpt_bucket_delete'></span>
				${common.subrow(item, common)}
		
		</div>`;

		return html;
	}

	HideAllEditors(id) {
		const prevId = this._ActiveEditor;
		if (prevId && this.Table.getItem(prevId)) this.Table.closeSub(prevId);

		this._ActiveEditor = id;
		if (id) this.FocusEditor();
	}
	FocusEditor() {
		this.app.callEvent("onAction", ["bucket", "focus", this._ActiveEditor]);
	}

	ResetEditor() {
		this._ActiveEditor = null;
	}

	ToggleSub(id) {
		var row = this.Table.getItem(id);
		if (row.$subopen) this.Table.closeSub(id);
		else this.Table.openSub(id);
	}
	Update(id, data) {
		this._hasFocus = document.activeElement;
		const buckets = [...this.BucketState.buckets];
		const column = this.BucketState.buckets.find(b => b.column == id);
		column.options = data.options;
		this.BucketState.buckets = buckets;
		this.Table.updateItem(id, { options: column.options });
	}
}
