import { JetView } from "webix-jet";
import PopupView from "./popup";
import EditorView from "./editor";

export default class SideView extends JetView {
	config() {
		this.Compact = this.getParam("compact", true);
		const _ = this.app.getService("locale")._;
		const state = this.app.getState();

		const calendar = {
			view: "calendar",
			localId: "calendar",
			height: 276,
			monthSelect: false,
			on: {
				onAfterDateSelect: date => {
					state.date = webix.Date.dayStart(date);
				},
			},
		};

		const list = {
			view: "list",
			localId: "calendars",
			type: this.ListType(_, state),
			borderless: true,
			autoheight: true,
			yCount: 5,
			editable: true,
			editValue: "text",
			editaction: "custom",
			editor: "text",
			css: "webix_scheduler_cal_list",
			tooltip: () => "",
			onClick: {
				webix_scheduler_cal_edit: (e, id) => this.ShowPopup(e, id),
				webix_scheduler_cal_title: (e, id) => this.ToggleCalendar(id),
				webix_scheduler_active: (e, id) => this.ToggleCalendar(id),
			},
			on: {
				onBeforeEditStop: (v, editor) => this.SetTitle(editor.id, v.value),
			},
		};

		const rows = [
			{ maxHeight: 100 },
			list,
			{
				view: "button",
				localId: "add",
				value: _("Add calendar"),
				hidden: !state.readonly,
				inputWidth: 190,
				align: "center",
				click: () => this.AddCalendar(),
			},
			{},
		];

		let side = {
			type: "form",
			padding: { top: 8 },
			rows: rows,
		};

		if (!this.Compact) {
			list.on.onAfterScroll = () => this.Popup.Hide();
			side = {
				view: "scrollview",
				body: {
					type: "clean",
					rows: [calendar, side],
				},
			};
		}

		return side;
	}

	init() {
		this.Cals = this.$$("calendars");
		this.Data = this.app.getService("local");
		this.Ops = this.app.getService("operations");

		this.Popup = this.Compact ? this.ui(EditorView) : this.ui(PopupView);
		webix.extend(this.Cals, webix.EditAbility);

		this.Data.calendars().then(data => this.Cals.parse(data));

		const state = this.app.getState();

		if (!this.Compact) {
			this.on(state.$changes, "date", value => {
				this.$$("calendar").setValue(value);
			});
			this.on(this.app, "side:popup:hide", () => {
				this.Cals.unselectAll();
			});
		}

		this.on(state.$changes, "readonly", value => this.ToggleState(value));
	}

	ListType(_, state) {
		return {
			checkbox: function(obj) {
				const icon =
					obj.active * 1 ? "wxi-checkbox-marked" : "wxi-checkbox-blank";
				return `<span style="color:${obj.color};" 
					role="checkbox" tabindex="-1" 
					aria-checked="${obj.active ? "true" : "false"}" 
					class="webix_icon webix_scheduler_active ${icon}">
				</span>`;
			},
			template: (obj, common) => {
				const edit = `
					<span
						webix_tooltip="${_("Edit calendar")}" 
						class="webix_icon wxi-dots webix_scheduler_cal_edit 
							${webix.env.touch ? " webix_scheduler_cal_visible" : ""}
						">
					</span>
				`;

				return `
					${common.checkbox(obj)}
					<span class="webix_scheduler_cal_title">${obj.text || _("(no title)")}</span>
					${state.readonly ? "" : edit}
				`;
			},
		};
	}

	AddCalendar() {
		const _ = this.app.getService("locale")._;
		this.Ops.addCalendar().then(id => {
			this.Cals.edit(id);
			this.Cals.getEditor()
				.getInputNode()
				.setAttribute("placeholder", _("(no title)"));
		});
	}

	ShowPopup(e, id) {
		if (!this.Compact) {
			this.Cals.select(id);

			const color = this.Cals.getItem(id).color;
			this.Popup.Show(id, this.Cals.getItemNode(id), color);
		} else this.Popup.Show(id);
	}

	ToggleCalendar(id) {
		const obj = this.Cals.getItem(id);
		this.Ops.updateCalendar(id, { active: obj.active ^ 1 });
	}

	ToggleState(value) {
		const add = this.$$("add");
		if (value) add.hide();
		else add.show();

		this.Cals.refresh();
	}

	SetTitle(id, text) {
		this.Ops.updateCalendar(id, { text }).then(
			() => this.EditStopSilent(),
			() => this.EditStopSilent(true)
		);
		return false;
	}

	EditStopSilent(fail) {
		this.Cals.blockEvent();
		if (fail) this.Cals.editCancel();
		else this.Cals.editStop();
		this.Cals.unblockEvent();
	}
}
