import { JetView } from "webix-jet";
import { bind } from "jet-restate-bind";

export default class ColumnPropertiesView extends JetView {
	constructor(app, data) {
		super(app);
		this.Column = data;
	}

	config() {
		const _ = this.app.getService("locale")._;
		const inputs = [
			{
				labelAlign: "right",
				label: _("Title"),
				view: "text",
				name: "name",
			},
			{
				labelAlign: "right",
				label: _("Filter"),
				view: "richselect",
				name: "header",
				options: [
					{ id: "none", value: _("No filter") },
					{ id: "text", value: _("Text") },
					{ id: "select", value: _("Select") },
				],
			},
		];

		return {
			view: "form",
			borderless: true,
			elementsConfig: {
				labelWidth: 120,
			},
			rows: inputs,
		};
	}

	init() {
		bind(this.Column, "*", this.getRoot());
	}
}
