import { JetView } from "webix-jet";

export default class DateView extends JetView {
	constructor(app, master) {
		super(app);
		this.Master = master;
	}
	config() {
		return {
			view: "popup",
			body: {
				view: "list",
				width: 160,
				autoheight: true,
				css: "webix_rpt_grouppopup",
				template: obj => this.GetPopupTemplate(obj),
				data: this.Master.config.modifiers,
				click: id => {
					this.Master.setModifier(this.ActiveGroup, id);
					this.getRoot().hide();
				},
			},
		};
	}
	$init() {
		this.ActiveGroup = null;
	}
	GetPopupTemplate(obj) {
		const isSelected = obj.id == this.Master.getModifier(this.ActiveGroup);
		return `${obj.value} <span class='webix_icon wxi-radiobox-${
			isSelected ? "marked" : "blank"
		}'></span>`;
	}
	Show(node, id) {
		this.ActiveGroup = id;
		this.getRoot().show(node, { x: -10 });
		this.getRoot()
			.getBody()
			.refresh();
	}
}
