import { createState, ignoreInitial } from "jet-restate";
import ColorPopup from "./colorpopup";
import BaseView from "./baseview";
import "../../../helpers/rsearch";
import SeriesProperties from "./chartseries";
import AxisProperties from "./axis";
//import LegendProperties from "./legend";

export default class ChartView extends BaseView {
	config() {
		const _ = (this._ = this.app.getService("locale")._);
		this.Charts = this.app.getService("charts");
		this.Local = this.app.getService("local");
		this._ActiveEditor = {};
		const type = {
			rows: [
				{ view: "label", label: this._("Chart type") },
				{
					view: "richselect",
					localId: "type",
					options: this.Charts.getTypes(),
					on: {
						onChange: v => (this.ChartState.chartType = v),
					},
				},
			],
		};

		const labelField = {
			rows: [
				{ view: "label", label: _("X axis column") },
				{
					view: "richselect",
					localId: "labelColumn",
					options: [],
					on: {
						onChange: v => this.ChangeLabelField(v),
					},
				},
			],
		};

		const valueFields = {
			view: "datatable",
			css: "webix_rpt_columns_list",
			localId: "fields",
			borderless: true,
			autoheight: true,
			header: false,
			select: false,
			editable: true,
			subview: (obj, target) => {
				if (!this.SubState) this.SubState = {};
				const series = (this.SubState[obj.id] = createState({
					name: obj.meta.name,
					color: obj.meta.color,
					type: obj.meta.type || null,
					markerType: obj.meta.markerType || null,
					fillMarker: obj.meta.fillMarker || null,
				}));
				this.on(
					series.$changes,
					"*",
					ignoreInitial(() => {
						this.UpdateSeries(obj.id, series);
					})
				);

				const sub = new SeriesProperties(this.app, series, this.ChartState);
				this.ui(sub, { container: target });
				return sub.getRoot();
			},
			columns: [
				{
					fillspace: true,
					template: (item, common) => this.RowTemplate(item, common),
				},
			],
			drag: "order",
			on: {
				onItemDblClick: function(id) {
					this.openSub(id);
				},
				onAfterRender: () => {
					if (this._hasFocus) {
						const el = this._hasFocus;
						this._hasFocus = null;

						if (el && document.contains(el) && el !== document.activeElement)
							el.focus();
					}
				},
				onAfterDrop: () => this.SaveOrder(),
				onSubViewOpen: id => this.HideAllEditors(id),
				"data->onClearAll": () => this.ResetEditor(),
				onSubViewClose: () => this.ResetEditor(),
			},
			onClick: {
				webix_rpt_chart_color: (e, id) => this.ShowColorPopup(e, id),
				"action-visible": (e, id) => this.HideField(id),
				"action-hidden": (e, id) => this.ShowField(id),
				webix_rpt_config_row_name: (e, id) => this.ToggleSub(id),
			},
		};
		const axisFields = {
			rows: [
				{ view: "label", label: _("Axes") },

				{
					view: "datatable",
					css: "webix_rpt_columns_list",
					localId: "axisFields",
					borderless: true,
					autoheight: true,
					header: false,
					select: false,
					subview: (obj, target) => {
						const axis = createState({ id: obj.id, ...obj.meta });
						this.on(
							axis.$changes,
							"*",
							ignoreInitial(() => {
								this.UpdateAxis(obj.id, axis);
							})
						);

						const sub = new AxisProperties(this.app, axis, this.ChartState);
						this.ui(sub, { container: target });
						return sub.getRoot();
					},
					columns: [
						{
							fillspace: true,
							template: (item, common) => this.AxisRowTemplate(item, common),
						},
					],

					on: {
						onItemClick: id => this.ToggleSub(id, "axisFields"),
						onAfterRender: () => {
							if (this._hasFocus) {
								const el = this._hasFocus;
								this._hasFocus = null;

								if (
									el &&
									document.contains(el) &&
									el !== document.activeElement
								)
									el.focus();
							}
						},
						onSubViewOpen: id => this.HideAllEditors(id, "axisFields"),
						onSubViewClose: () => this.ResetEditor("axisFields"),
					},
				},
			],
		};

		const legendFields = {
			rows: [
				{ view: "label", label: _("Legend") },
				{
					view: "radio",
					localId: "legendPosition",
					options: [
						{ id: "bottom", value: _("Bottom") },
						{ id: "right", value: _("Right") },
						{ id: "none", value: _("None") },
					],
					on: {
						onChange: v => this.ChangeLegend(v),
					},
				},
			],
		};
		return {
			view: "form",
			scroll: "auto",
			css: "webix_rpt_chart_config",
			padding: { top: webix.skin.$active.layoutPadding.toolbar },
			rows: [
				type,
				labelField,
				{
					localId: "dataInputs",
					rows: this.GetDataInputs(),
				},
				valueFields,
				axisFields,
				legendFields,
				{},
			],
		};
	}

	init() {
		this.State = this.getParam("state", true);
		this.Types = this.Charts.getTypes();

		let fields = [];
		(this.State.publicFields || this.State.fields).forEach(a => {
			fields.push({ ...a });
		});

		// options with all fields
		this.Fields = fields
			.filter(field => {
				return !field.key;
			})
			.sort((a, b) => (a.type < b.type ? 1 : -1))
			.map(a => ({ id: a.id, value: a.name, type: a.type }));

		// Options with numeric fields
		this.ValueFields = fields
			.filter(field => {
				return field.type == "number" && !field.key;
			})
			.map(a => ({ id: a.id, value: a.name, type: a.type, model: a.model }));

		this.ChartState = createState(this.GetInitState());

		this.on(this.ChartState.$changes, "*", () => {
			this.UpdateModuleState();
		});
		this.on(this.ChartState.$changes, "chartType", (type, prev) => {
			this.ApplyChartType(type, prev);
		});
		this.on(this.ChartState.$changes, "seriesFrom", (value, prev) => {
			this.ApplySeriesFrom(value, prev);
		});

		this.on(
			this.ChartState.$changes,
			"baseColumn",
			ignoreInitial(() => {
				this.GenerateSeries();
			})
		);
		this.on(
			this.ChartState.$changes,
			"dataColumn",
			ignoreInitial(() => {
				this.GenerateSeries();
			})
		);

		this.on(this.ChartState.$changes, "updatedSeries", v => {
			if (v) {
				this.$$("fields").refresh(v);
				if (this.SubState && this.SubState[v])
					this.SubState[v].color = this.ChartState.series.find(
						s => s.id == v
					).meta.color;
				this.ChartState.updatedSeries = null;
			}
		});

		this.$$("type").setValue(this.ChartState.chartType);

		this.$$("labelColumn")
			.getList()
			.parse(this.Fields);
		this.$$("labelColumn").setValue(this.ChartState.labelColumn);

		this.$$("seriesFrom").setValue(this.ChartState.seriesFrom);

		this.$$("baseColumn")
			.getList()
			.parse(this.Fields);
		this.$$("baseColumn").setValue(this.ChartState.baseColumn);

		this.$$("dataColumn")
			.getList()
			.parse(this.ValueFields);
		this.$$("dataColumn").setValue(this.ChartState.dataColumn);

		this.$$("legendPosition").setValue(this.ChartState.legendPosition);
	}

	ChangeLabelField(label) {
		this.ChartState.labelColumn = label;
	}
	ChangeLegend(v) {
		this.ChartState.legendPosition = v;
	}

	GetInitState() {
		const meta = this.State.meta.chart || {};
		return {
			chartType: meta.chartType || this.Charts.type,
			labelColumn: meta.labelColumn || null,
			updatedSeries: null,
			seriesFrom: meta.seriesFrom || "columns",
			baseColumn: meta.baseColumn || null,
			dataColumn: meta.dataColumn || null,
			series: meta.series || null,
			legend: meta.legend || { layout: "x" },
			axises: meta.axises || null,
			legendPosition: meta.legendPosition || "bottom",
		};
	}

	/*
	 * Called when chart type changed.
	 * */
	ApplyChartType(type, prevType) {
		this.ShowAxisInputs(type);
		this.RenderSeries(prevType);
	}

	/*
	 * Renders chart series. Loads init state values
	 * or generates new series depending on selected type
	 * */
	RenderSeries(prevType) {
		const series = this.ChartState.series;
		if (series && typeof prevType == "undefined") this.LoadSeries(series);
		else this.GenerateSeries();
	}

	// Loads axis data
	ShowAxisInputs(type) {
		const isAxises = this.Charts.noAxisSupport.indexOf(type);
		if (isAxises) {
			if (!this.ChartState.axises) {
				this.ChartState.axises = {
					x: {
						title: "",
						color: "#edeff0",
						lineColor: "#edeff0",
						lines: true,
						verticalLabels: true,
					},
					y: {
						title: "",
						color: "#edeff0",
						lineColor: "#edeff0",
						lines: true,
						logarithmic: false,
					},
				};
			}
			if (!this.$$("axisFields").data.count())
				this.$$("axisFields").parse([
					{ id: "x", meta: { ...this.ChartState.axises.x } },
					{ id: "y", meta: { ...this.ChartState.axises.y } },
				]);
		} else {
			this.ChartState.axises = null;
			this.$$("axisFields").clearAll();
		}
	}
	GetDataInputs() {
		return [
			{
				rows: [
					{ view: "label", label: this._("Data series") },
					{
						cols: [
							{
								rows: [
									{
										view: "label",
										label: this._("Extract series from"),
										css: "webix_rpt_subtitle",
									},
									{
										view: "radio",
										width: 200,
										localId: "seriesFrom",
										options: [
											{ id: "columns", value: this._("columns") },
											{ id: "rows", value: this._("rows") },
										],
										on: {
											onChange: v => (this.ChartState.seriesFrom = v),
										},
									},
								],
							},
							{
								hidden: true,
								localId: "baseColumnLayout",
								margin: webix.skin.$active.layoutMargin.form,
								cols: [
									{
										rows: [
											{
												view: "label",
												label: this._("Keys column"),
												css: "webix_rpt_subtitle",
											},
											{
												view: "richselect",

												css: "webix_rpt_subtitle",
												localId: "baseColumn",
												options: [],
												labelPosition: "top",
												on: {
													onChange: v => (this.ChartState.baseColumn = v),
												},
											},
										],
									},
									{
										rows: [
											{
												view: "label",
												label: this._("Values column"),
												css: "webix_rpt_subtitle",
											},
											{
												view: "richselect",
												css: "webix_rpdataColumnt_subtitle",
												localId: "dataColumn",
												options: [],
												on: {
													onChange: v => (this.ChartState.dataColumn = v),
												},
											},
										],
									},
								],
							},
						],
					},
				],
			},
		];
	}

	/*
	 * Generates series and loads these data
	 * params:
	 * - an array of fields/ keys options
	 * */
	ApplySeriesKeys(sourceArray) {
		const data = this.Charts.getSeries(sourceArray, this.ChartState.type);
		this.ChartState.series = data;
		this.LoadSeries(data);
	}

	LoadSeries(data) {
		if (!data.length) this.$$("fields").hide();
		else this.$$("fields").show();

		this.$$("fields").clearAll();
		this.$$("fields").parse(data);
	}
	/*
	 * Gets row keys (requires data loading)
	 * params:
	 * - the field / column id where keys need to be found
	 * Returns a promise
	 * */
	GetRowKeys(base) {
		if (base && this.State.columns.length) {
			let keys = [];
			return this.GetData(base).then(([data, baseOptions]) => {
				data.forEach(row => {
					const key = row[base];
					if (keys.indexOf(key) == -1) keys.push(key);
				});

				if (baseOptions)
					return keys.map(key => {
						const keyOption =
							baseOptions.data.find(option => option.id == key) || null;
						return { id: key, value: keyOption ? keyOption.value : key };
					});
				return keys.map(key => ({ id: key, value: key }));
			});
		} else return Promise.resolve([]);
	}

	GenerateSeries() {
		const from = this.ChartState.seriesFrom;
		if (from == "columns") this.ApplySeriesKeys([...this.ValueFields], null);
		else {
			this.ChartState.dataColumn = this.$$("dataColumn").getValue();
			const base = (this.ChartState.baseColumn = this.$$(
				"baseColumn"
			).getValue());
			this.GetRowKeys(base).then(arr => this.ApplySeriesKeys(arr, base));
		}
	}

	ApplySeriesFrom(v, prevValue) {
		if (this.$$("baseColumnLayout")) {
			if (v == "columns") {
				this.$$("baseColumnLayout").hide();
				this.$$("baseColumn").setValue(null);
			} else this.$$("baseColumnLayout").show();
			if (prevValue) {
				this.GenerateSeries();
			}
		}
	}

	UpdateSeries(id, update) {
		this._hasFocus = document.activeElement;
		const series = [...this.ChartState.series];
		const ind = series.findIndex(a => a.id == id);
		Object.assign(series[ind].meta, update);
		this.ChartState.series = series;
		this.$$("fields").refresh(id);
	}

	UpdateAxis(id, update) {
		this._hasFocus = document.activeElement;
		let axises = { ...this.ChartState.axises };
		Object.assign(axises[id], update);
		this.ChartState.axises = axises;
	}

	RowTemplate(item, common) {
		let html =
			"<div class='webix_rpt_config_row'><div class='webix_icon rpi-drag-vertical'></div>";
		html +=
			"<span class='webix_icon " +
			(item.show
				? "action-visible rpi-eye"
				: "action-hidden rpi-eye-off-outline") +
			"'></span>";
		html +=
			"<div class='webix_rpt_chart_color' style='background:" +
			item.meta.color +
			"'> </div>";
		html +=
			"<div class='webix_rpt_config_row_name'>" +
			item.name +
			(item.model
				? "<span class='webix_rpt_sources_path'>" + item.model + "</span>"
				: "") +
			"</div>";
		html += common.subrow(item, common);
		return html + "</div>";
	}

	GetData(baseColumn) {
		const appState = this.app.getParam("state");
		let moduleConfig = this.Local.getDataConfig(appState.module);
		moduleConfig.columns = this.State.columns.map(c => c.id);
		let waitData = [this.Local.getData(moduleConfig)];
		if (baseColumn) waitData.push(this.Local.getOptionsList(baseColumn));
		return Promise.all(waitData);
	}

	ShowColorPopup(e, id) {
		if (!this.ColorPopup || !this.ColorPopup.$view)
			this.ColorPopup = this.ui(new ColorPopup(this.app, this.ChartState));
		this.ActiveSeries = id;
		this.ColorPopup.Show(e.target, id);
	}

	SaveOrder() {
		const table = this.$$("fields");
		const columns = [];
		table.data.each(a => {
			if (a.show)
				columns.push(
					(this.State.publicFields || this.State.fields).find(
						b => b.id === a.id
					)
				);
		});
		this.ChartState.series = table.serialize();
	}

	UpdateModuleState() {
		const state = this.ChartState;
		const columns = [];

		if (state.labelColumn) this.AddColumn(columns, state.labelColumn);
		if (state.seriesFrom == "columns") {
			if (state.series)
				state.series.forEach(a => {
					if (a.show) this.AddColumn(columns, a.id);
				});
		} else {
			if (state.baseColumn) this.AddColumn(columns, state.baseColumn);
			if (state.dataColumn) this.AddColumn(columns, state.dataColumn);
		}
		this.State.$batch({
			columns,
			meta: { ...this.State.meta, chart: this.ChartState },
		});
	}

	AddColumn(columns, id) {
		const col = (this.State.publicFields || this.State.fields).find(
			b => b.id === id
		);
		if (col) columns.push(col);
	}

	AxisRowTemplate(item, common) {
		let html =
			"<div class='webix_rpt_config_row'>" +
			this._(item.id == "x" ? "X Axis" : "Y Axis");
		html += common.subrow(item, common);
		return html + "</div>";
	}
}
