import { getDimmedColor, getDimmedFont } from "./colors";

export function marker(obj) {
	const color = obj.color || obj.$color;
	return `<div class="webix_event_marker"><div class="webix_event_marker_inner" style="background-color:${
		obj.$past ? getDimmedColor(color) : color
	};"></div></div>`;
}

export function events(_) {
	return function(obj, common) {
		return `<div class="webix_event_overall ${
			obj.$past ? "webix_scheduler_past_event" : ""
		}"><div class="webix_event_time">${common.timeStart(obj)}</div>${marker(
			obj,
			common
		)}<div class="webix_event_text">${obj.text || _("(No title)")}</div></div>`;
	};
}

export function dayEventColor(obj) {
	const color = obj.color || obj.$color;
	return `border-color:${
		obj.$past ? getDimmedColor(obj.$color) : obj.$color
	}; background-color:${obj.$past ? getDimmedColor(color) : color}; color:${
		obj.$past ? getDimmedFont() : obj.$textColor
	};`;
}

export function timeStart(start, end) {
	return `<div class="start">${webix.i18n.timeFormatStr(
		start
	)}</div><div class="end">${webix.i18n.timeFormatStr(end)}</div>`;
}

function fill(obj, common, size) {
	const v = common.master;
	return (
		v["$" + size] -
		(v.config[size == "width" ? "xCount" : "yCount"] - 1) * common[size]
	);
}

export function templateStart(obj, common, marks) {
	let { width, height } = common;
	if (obj.right) width = fill(obj, common, "width");
	if (obj.bottom) height = fill(obj, common, "height");
	return (
		"<div " +
		/*@attr*/ "webix_l_id" +
		`="${obj.id}" class="${common.classname(obj, common, marks)}" ${common.aria(
			obj,
			common,
			marks
		)} style="width:${width}px; height:${height}px; float:left; overflow:hidden;">`
	);
}
