import { JetView } from "webix-jet";
import "../../helpers/rsearch";
import ExportMenu from "./export";

export default class ToolbarView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const bar = {
			view: "form",
			localId: "toolbar",
			height: webix.skin.$active.barHeight + 6,
			hidden: true,
			padding: { left: 10, right: 10, top: 6, bottom: 6 },
			margin: 10,
			cols: [
				{
					view: "button",
					type: "icon",
					width: 140,
					label: _("Export"),
					icon: "rpi-export",
					click: viewId => this.ShowExportPopup(viewId),
				},
				{},

				{
					view: "text-search",
					localId: "search",
					placeholder: _("Type to search"),
					maxWidth: 600,
					minWidth: 200,
				},
			],
		};
		return bar;
	}

	ready() {
		this.State = this.getParam("state", true);
		this.$$("search").attachEvent(
			"onSearchChange",
			v => (this.State.search = v)
		);
		this.on(this.app, "onRecordsSearch", (value, result) => {
			if (value) this.$$("search").setBadge(result.length);
			else this.$$("search").setBadge(null);
		});
	}
	/**
	 * Shows menu
	 * @param {String} viewId - webix view id
	 */
	ShowExportPopup(viewId) {
		if (!this.ExportMenu || !this.ExportMenu.$view)
			this.ExportMenu = this.ui(ExportMenu);
		const node = webix.$$(viewId);
		this.ExportMenu.Show(node.$view);
	}
}
