import { JetView } from "webix-jet";

export default class MenuPopup extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		return {
			view: "popup",
			padding: 0,
			point: false,
			body: {
				view: "list",
				borderless: true,
				css: "webix_rpt_popup_menu",
				template: obj =>
					`<span class='webix_icon ${obj.icon}'></span>${obj.value}`,
				width: 160,
				autoheight: true,
				data: [
					{
						value: _("Edit"),
						id: "edit",
						icon: "rpi-square-edit-outline",
					},
					{
						value: _("Create copy"),
						id: "copy",
						icon: "rpi-content-copy",
					},
					{
						value: _("Delete"),
						id: "delete",
						icon: "rpi-delete",
					},
				],
				on: {
					onItemClick: id => {
						this.app.callEvent("onMenuAction", [this.ID, id]);
						this.getRoot().hide();
					},
				},
			},
		};
	}
	init() {}

	Show(trg, id) {
		this.ID = id;
		webix.delay(() => {
			if (this.getRoot()) this.getRoot().show(trg);
		});
	}
}
