import { JetView } from "webix-jet";

export default class EventActionPopup extends JetView {
	config() {
		const _ = this.app.getService("locale")._;

		const buttons = {
			margin: webix.skin.$active.layoutMargin.form,
			cols: [
				{
					view: "button",
					value: _("Cancel"),
					hotkey: "esc",
					click: () => this.Cancel(),
				},
				{
					view: "button",
					value: _("Apply"),
					css: "webix_primary",
					hotkey: "enter",
					click: () => this.Done(),
				},
			],
		};

		return this.app.getService("jet-win").updateConfig({
			view: "window",
			head: _("Edit recurring event"),
			autoheight: true,
			css: "webix_scheduler_action_popup",
			body: {
				type: "form",
				padding: { top: 0 },
				width: 340,
				rows: [
					{
						view: "radio",
						localId: "option",
						vertical: true,
						options: [
							{ id: "this", value: _("This event") },
							{ id: "next", value: _("This event and the following") },
							{ id: "all", value: _("All events") },
						],
					},
					buttons,
				],
			},
			on: {
				onHide: () => (this.Result = null),
			},
		});
	}

	init(view) {
		this.Root = view;
	}

	Show(promise) {
		this.Result = promise;
		this.$$("option").setValue("this");
		this.Root.show();
	}

	Done() {
		const id = this.$$("option").getValue();
		if (this.Result) this.Result.resolve(id);
		this.Root.hide();
	}

	Cancel() {
		if (this.Result) this.Result.reject();
		this.Root.hide();
	}
}
