import { JetView } from "webix-jet";
import MoreWindowView from "./more";
import SelectionMixin from "helpers/selection";
import EditRecurringMixin from "helpers/editrecurring";
import { marker, dayEventColor, templateStart } from "helpers/templates";
import { initDnD, locateEvent } from "helpers/dnd";
import { daysBetweenInclusive, isToday } from "helpers/dates";
import { getParams } from "helpers/recurring";
import CopyPasteMixin from "helpers/copypaste";

export default class MonthEventsView extends JetView {
	config() {
		return {
			view: "dataview",
			css: "webix_scheduler_calendar",
			prerender: true,
			xCount: 7,
			yCount: 5,
			width: 0,
			height: 0,
			scroll: false,
			tooltip: {
				template: "",
			},
			onClick: {
				webix_cal_date: (_ev, id) => this.ShowDay(id),
				webix_scheduler_month_event: ev => this.ShowEvent(ev),
				webix_scheduler_month_event_single: ev => this.ShowEvent(ev),
				webix_scheduler_more: (_ev, id) => this.ShowMore(id),
			},
			onDblClick: {
				webix_cal_day: (_e, id) => this.ShowNew(id),
			},
			type: {
				height: "auto",
				width: "auto",
				template: (o, c) => this.CalendarTemplate(o, c),
				templateStart,
				headerHeight: 32,
				eventHeight: 28,
				padding: 8,
			},
		};
	}

	init(view) {
		this.State = this.app.getState();
		this.Calendar = view;
		view.type.master = this.Calendar;

		SelectionMixin(this, "month");
		EditRecurringMixin(this);

		this.Events = new webix.DataCollection({});
		this.MoreWindow = this.ui(
			new (this.app.dynamic(MoreWindowView))(this.app, {
				events: this.Events,
			})
		);

		view.attachEvent("onAfterRender", () => this.RenderEvents());
		initDnD.call(this, this.Calendar, "month");

		if (this.app.config.copypaste && !webix.env.mobile)
			CopyPasteMixin(this, view);
	}

	urlChange() {
		this.data = this.getParam("data", true); // IE hack
		if (this.data) this.RefreshData();
	}

	CalendarTemplate(obj) {
		const _ = this.app.getService("locale")._;

		let html = `<span class="webix_cal_date">${obj.date.getDate()}</span>`;
		if (obj.$more && obj.$more.length) {
			html += `<div class="webix_scheduler_more">${obj.$more.length} ${_(
				"more"
			)}</div>`;
		}
		return html;
	}

	GetDayCss(day, month) {
		let css = "webix_cal_day";
		if (isToday(day)) css += " webix_cal_today";
		if (day.getMonth() != month) css += " webix_cal_outside";
		else css += " " + (webix.Date.isHoliday(day) || "");
		return css;
	}

	GetMonthData() {
		const data = [];
		const { start, weeks } = this.data;
		const month = webix.Date.add(start, 1, "week", true).getMonth();

		let day = webix.Date.copy(start);
		for (let i = 0; i < weeks * 7; i++) {
			const obj = {
				date: webix.Date.copy(day),
				$css: this.GetDayCss(day, month),
			};

			if ((i + 1) % 7 === 0) obj.right = true;
			if (i >= (weeks - 1) * 7) obj.bottom = true;

			data.push(obj);
			webix.Date.add(day, 1, "day");
		}
		return data;
	}

	RenderEvents() {
		// No Events, No Render
		if (!this.Events.count() || !this.Calendar.count()) return;

		const container = webix.html.create("div", {
			role: "presentation",
			class: "webix_scheduler_month_events",
		});

		const { start } = this.data;
		const type = this.Calendar.type;
		const maxLines = Math.floor(
			(type.height - type.headerHeight + type.padding - 2) /
				(type.eventHeight + type.padding)
		);

		let wStart = webix.Date.copy(start);
		let wEnd = webix.Date.add(wStart, 1, "week", true);

		let html = "";
		let cache = {};
		const me = this.GetTargetDate(this.Calendar.getLastId());
		const ms = this.GetTargetDate(this.Calendar.getFirstId());
		this.Calendar.data.each(function(day, dayIndex) {
			// next week
			if (day.date >= wEnd) {
				cache = {};
				wStart = wEnd;
				wEnd = webix.Date.add(wStart, 1, "week", true);
			}

			const more = [];
			const dayHTML = [];
			this.Events.data.each(function(ev) {
				const id = ev.id;

				// will not be this week
				if (
					ev.start_date >= wEnd ||
					ev.end_date < wStart ||
					(!ev.all_day && webix.Date.equal(ev.end_date, wStart))
				)
					return;

				// if already processed - check and skip
				if (!webix.isUndefined(cache[id])) {
					if (
						(day.date <= ev.end_date && ev.all_day) ||
						day.date < ev.end_date
					) {
						if (cache[id] >= maxLines) {
							more.push(id);
						} else dayHTML[cache[id]] = "";
					} else delete cache[id];
					return;
				}

				// today
				if (
					ev.start_date < webix.Date.add(day.date, 1, "day", true) &&
					ev.end_date >= day.date
				) {
					let index = 0;
					// search for the first available index
					while (!webix.isUndefined(dayHTML[index])) index++;

					cache[id] = index;
					if (index >= maxLines) return more.push(id);

					const config = { ms, me };
					config.single = this.IsSingleDay(ev);

					config.length = 1;
					if (!config.single) {
						config.es = ev.start_date;
						config.ee =
							!ev.all_day && !webix.Date.timePart(ev.end_date)
								? webix.Date.add(ev.end_date, -1, "day", true)
								: ev.end_date;

						config.length += daysBetweenInclusive(
							(config.ls = new Date(Math.max(wStart, config.es))),
							(config.le = new Date(Math.min(wEnd - 1, config.ee)))
						);
					}
					config.index = dayIndex;
					dayHTML[index] = this.ToHTML(ev, index, config);
				}
			}, this);

			const prevLength = day.$more && day.$more.length;
			day.$more = more;
			if (prevLength != more.length) this.Calendar.render(day.id, day, "paint");

			html += dayHTML.join("");
		}, this);

		container.innerHTML = html;
		this._DataObj = this.Calendar.$view.firstChild.appendChild(container);
		this.app.callEvent("events:rendered", []);
	}

	IsSingleDay(ev) {
		if (ev.all_day)
			return !daysBetweenInclusive(
				ev.start_date,
				webix.Date.add(ev.end_date, 1, "day", true)
			);

		let len = daysBetweenInclusive(ev.start_date, ev.end_date);

		if (
			len === 1 &&
			webix.Date.timePart(ev.start_date) &&
			!webix.Date.timePart(ev.end_date)
		)
			len = 0;

		return !len;
	}

	ToHTML(obj, index, c) {
		const type = this.Calendar.type;
		const style = c.single ? "" : dayEventColor(obj);
		const width = c.length * type.width - type.padding;
		const height = type.eventHeight;
		const pos = this.GetPosition(type, index, c.index);

		let extra = "";
		if (c.single) {
			extra =
				marker(obj) +
				`<span class="webix_event_time">${webix.i18n.timeFormatStr(
					obj.start_date
				)}</span>`;
		}
		const css = this.GetStyle(obj, c);

		const _ = this.app.getService("locale")._;
		return `
			<div
				webix_tooltip="${obj.text}"
				webix_e_id="${obj.id}"
				class="${css}"
				style="height:${height}px;line-height:${height}px; width:${width}px; ${pos +
			style}">
					${extra + (obj.text || _("(No title)"))}
			</div>
		`;
	}

	GetStyle(obj, c) {
		let css = `webix_scheduler_month_event${c.single ? "_single" : ""}`;
		css +=
			this.State.selected &&
			(obj.id == this.State.selected.id || obj.id == this.State.selected.date)
				? " webix_scheduler_event_selected"
				: "";

		css += obj.$past ? " webix_scheduler_past_event" : "";

		if (!c.single) {
			if (c.es < c.ms && webix.Date.equal(c.ms, webix.Date.dayStart(c.ls)))
				css += " webix_scheduler_event_break_left";
			else if (
				c.ee >= webix.Date.add(c.me, 1, "day", true) &&
				webix.Date.equal(c.me, webix.Date.dayStart(c.le))
			)
				css += " webix_scheduler_event_break_right";
		}
		return css;
	}

	GetPosition(type, index, dayIndex) {
		const row = Math.floor(dayIndex / this.Calendar.config.xCount);
		const col = dayIndex % this.Calendar.config.xCount;

		return `
			top:${row * type.height +
				type.headerHeight +
				index * (type.eventHeight + type.padding)}px;
			left:${col * type.width}px;
		`;
	}

	RefreshData() {
		const { data, weeks } = this.data;

		this.Events.clearAll();
		this.Events.parse(data);
		this.Events.sort(function(a, b) {
			const start_a = webix.Date.dayStart(a.start_date);
			const start_b = webix.Date.dayStart(b.start_date);

			if (webix.Date.equal(start_a, start_b)) {
				const days_a = daysBetweenInclusive(a.start_date, a.end_date);
				const days_b = daysBetweenInclusive(b.start_date, b.end_date);
				return days_b - days_a || a.start_date - b.start_date;
			}
			return start_a - start_b;
		});

		const monthData = this.GetMonthData();

		this.Calendar.clearAll();
		if (this.Calendar.config.yCount != weeks) {
			this.Calendar.define("yCount", weeks);
			this.Calendar.resize();
		}
		this.Calendar.parse(monthData);
	}

	ShowMore(id) {
		this.MoreWindow.ShowWindow(
			id,
			this.Calendar.getItemNode(id),
			this.Calendar.getItem(id).$more || []
		);
		return false;
	}

	ShowEvent(e) {
		const event = locateEvent(e, this.Calendar.$view);
		if (event.id && event.id != "0") {
			const obj = this.Events.getItem(event.id);
			const sel = webix.extend(event, getParams(obj), true);
			this.State.selected = sel;
		}
		return false;
	}

	ShowNew(id) {
		const state = this.State;
		if (!state.readonly && !state.clipboard) {
			const date = this.GetTargetDate(id);
			state.selected = { id: "0", date };
		}
	}

	ShowDay(id) {
		const state = this.app.getState();

		state.$batch({
			date: this.GetTargetDate(id),
			mode: "day",
		});

		return false;
	}

	/**
	 * Returns the target date for an event
	 * @param {(string|number)} id - the ID of the day in the calendar
	 * @returns
	 */
	GetTargetDate(id) {
		return id ? this.Calendar.getItem(id).date : this.State.date;
	}

	/**
	 * Returns the data object of an event
	 * @param {(string|number)} id - the ID of the event
	 * @returns {Object}
	 */
	GetEvent(id) {
		return this.Events.getItem(id);
	}

	/**
	 * Updates selected in global reactive state
	 * @param {(string|number)} id - the ID of the event
	 * @param {Object} newEvent - the data of the new event that should be selected
	 */
	UpdateSelection(id, newEvent) {
		if (this.State.selected) {
			newEvent.id = id;
			this.State.selected = getParams(newEvent);
		}
	}
}
