import { JetView } from "webix-jet";

export default class MultiMoreView extends JetView {
	config() {
		const more = {
			view: "template",
			localId: "more",
			width: 50,
			css: "webix_scheduler_multi_space",
			tooltip: () => "",
			onClick: {
				"wxi-angle-down": () => this.ExpandData(),
				"wxi-angle-up": () => this.WrapData(),
			},
		};

		return {
			cols: [more, {}],
		};
	}

	init() {
		this.List = this.$$("multiDayList");
		this.MoreIcon = this.$$("more");

		this._expandState = "down";
		this.maxVisibleLines = 3;
	}

	/**
	 *
	 * @param {*} mode
	 */
	SetMoreIcon(mode) {
		if (!mode) {
			this.MoreIcon.setHTML("");
		} else {
			const tooltip = mode === "up" ? "Collapse" : "Expand";
			const _ = this.app.getService("locale")._;
			this.MoreIcon.setHTML(
				`<div class="webix_scheduler_more_icon webix_icon wxi-angle-${mode}" webix_tooltip="${_(
					`${tooltip} all-day events`
				)}"><div>`
			);
		}
	}

	/**
	 * Sets new height and delay for animation
	 * @param {string} mode - "up" or "down"
	 * @param {number} height
	 * @param {function} handler
	 */
	Animate(mode, height, handler) {
		const delay = 150;
		this.List.$view.style.transition = `height ${delay}ms`;
		if (height) this.List.$view.style.height = `${height}px`;

		setTimeout(() => {
			if (handler) handler();

			this.List.$view.style.transition = "";
			this._inAnimation = false;
			this.SetMoreIcon(mode);
		}, delay);
	}
}
