import { JetView } from "webix-jet";
import SelectionMixin from "helpers/selection";
import { dayEventColor } from "helpers/templates";
import { getParams } from "helpers/recurring";
import CopyPasteMixin from "helpers/copypaste";
import { initDnD } from "helpers/dnd";
import EditRecurringMixin from "helpers/editrecurring";

export default class UnitMultidayList extends JetView {
	constructor(app, config) {
		super(app);

		if (config) {
			this.Unit = config.unit;
		}
	}

	config() {
		return {
			view: "list",
			localId: "multiDayList",
			autoheight: true,
			scroll: false,
			css: "webix_scheduler_multilist",
			type: {
				height: 36,
				template: (obj, common) => this.EventTemplate(obj, common),
			},
			onClick: {
				webix_scheduler_multiday_event: (e, id, node) =>
					this.ShowEvent(id, node),
			},
		};
	}

	init() {
		this.State = this.app.getState();
		this.List = this.$$("multiDayList");

		this._expandState = "down";
		this.maxVisibleLines = 3;

		this._DataObj = this.List.$view.firstChild;
		SelectionMixin(this, "multi", true);

		this._moreBtn = {
			id: "$wsh_multi_more",
			$css: "webix_scheduler_multi_more",
		};

		this.on(this.app, "units:multi:state", state => {
			if (state !== this._expandState) {
				state === "down" ? this.WrapData() : this.ExpandData();
			}
		});

		initDnD.call(this, this.List, "multidayUnits");
		EditRecurringMixin(this);

		if (this.app.config.copypaste && !webix.env.mobile)
			CopyPasteMixin(this, this.List, { multi: true });
	}

	urlChange() {
		const data = this.getParam("data");
		if (data) {
			const copy = webix.copy(data.multi);
			const multi = this.FilterByUnits(copy);
			this.LimitData(multi);
			this.List.clearAll();
			this.List.parse(multi);
		}
	}

	EventTemplate(obj, common) {
		const height = common.height - 8;
		const size = `height:${height}px; line-height:${height}px; width:calc(100% - 5px);`;
		const color = obj.id !== "$wsh_multi_more" ? dayEventColor(obj) : "";
		const css = this.GetStyle(obj);

		const _ = this.app.getService("locale")._;
		return `
			<div
				class="${css}"
				style="${size} ${color}">
					${obj.text || _("(No title)")}
			</div>
		`;
	}

	GetStyle(obj) {
		let css = "webix_scheduler_multiday_event";
		if (obj.id == "$wsh_multi_more") return css;

		const date = this.State.date;
		const end =
			!obj.all_day && !webix.Date.timePart(obj.end_date)
				? webix.Date.add(obj.end_date, -1, "day", true)
				: obj.end_date;

		css +=
			this.State.selected &&
			(obj.id == this.State.selected.id || obj.id == this.State.selected.date)
				? " webix_scheduler_event_selected"
				: "";

		css += obj.$past ? " webix_scheduler_past_event" : "";

		if (obj.start_date < date) css += " webix_scheduler_event_break_left";
		if (end >= webix.Date.add(date, 1, "day", true))
			css += " webix_scheduler_event_break_right";

		return css;
	}

	LimitData(data) {
		delete this._reserve;
		delete this._reserveIds;
		if (data.length > this.maxVisibleLines) {
			const excess = data.length - 2;
			const _ = this.app.getService("locale")._;
			this._moreBtn.text = `${excess} ${_("more")}`;

			if (this._expandState === "down") {
				this._reserve = data.splice(2, excess, this._moreBtn);
			} else {
				this._reserve = data.slice(2);
			}

			this._reserveIds = this._reserve.map(d => d.id);

			this.app.callEvent("units:multi:state", [this._expandState, false]);
		}
	}

	WrapData() {
		if (!this._inAnimation) {
			this._expandState = "down";

			if (this._reserveIds) {
				this._inAnimation = true;
				const height =
					this.List.$height -
					(this._reserveIds.length - 1) * this.List.type.height;
				this.Animate("down", height, () => {
					this.List.add(this._moreBtn);
					this.List.remove(this._reserveIds);
				});
			}
		}
	}

	ExpandData() {
		const expandable = this.List.exists("$wsh_multi_more");
		if (!this._inAnimation) {
			this._expandState = "up";
			if (expandable) {
				this._inAnimation = true;
				this.Animate("up");

				this.List.remove("$wsh_multi_more");
				this.List.parse(this._reserve);
			}
		}
	}

	Animate(mode, height, handler) {
		this.List.$view.style.transition = "height 150ms";
		if (height) this.List.$view.style.height = `${height}px`;

		setTimeout(() => {
			if (handler) handler();

			this.List.$view.style.transition = "";
			this._inAnimation = false;
		}, 150);
	}

	ShowEvent(id, node) {
		if (id === "$wsh_multi_more") {
			this.app.callEvent("units:multi:state", ["up", true]);
		} else {
			const obj = this.List.getItem(id);
			const sel = getParams(obj);
			sel.node = node;
			this.State.selected = sel;
		}
		return false;
	}

	FilterByUnits(multiEvents) {
		return multiEvents.filter(obj => {
			if (obj.units) {
				const units = obj.units.split(",");
				const unitID = this.Unit.id + "";
				return units.indexOf(unitID) !== -1;
			}
			return false;
		});
	}

	/**
	 * Returns the event object by its ID
	 * @param {(string|number)} id - the ID of the event
	 * @returns {Object}
	 */
	GetEvent(id) {
		return this.List.getItem(id);
	}

	/**
	 * Returns the target date for the event
	 * @returns {Date}
	 */
	GetTargetDate() {
		return this.State.date;
	}
}
