import { JetView } from "webix-jet";
import { isToday } from "helpers/dates";

export default class WeekHeaderView extends JetView {
	config() {
		return {
			type: "clean",
			cols: [
				{ borderless: false, width: 51 },
				{
					view: "dataview",
					localId: "header",
					css: "webix_scheduler_day_header",
					xCount: 7,
					yCount: 1,
					width: 0,
					height: 32,
					scroll: false,
					type: {
						height: "auto",
						width: "auto",
					},
					template: obj => {
						return `<span class="webix_scheduler_weekday">${obj.day}</span> <span class="webix_scheduler_monthday">${obj.date}</span>`;
					},
				},
			],
		};
	}

	urlChange() {
		this.data = this.getParam("data");
		if (this.data) {
			this.RefreshData();
		}
	}

	RefreshData() {
		let start = this.data.start;
		const data = [];
		while (start < this.data.end) {
			const obj = {
				day: webix.i18n.calendar.dayShort[start.getDay()],
				date: start.getDate(),
			};
			if (isToday(start)) {
				obj.$css = "webix_scheduler_list_today";
			}
			data.push(obj);
			start = webix.Date.add(start, 1, "day", true);
		}

		const header = this.$$("header");
		header.clearAll();
		header.parse(data);
	}
}
