import { JetView } from "webix-jet";

export default class UnitsHeaderView extends JetView {
	config() {
		return {
			type: "clean",
			cols: [
				{ borderless: false, width: 51 },
				{
					view: "dataview",
					localId: "header",
					css: "webix_scheduler_day_header",
					xCount: 1,
					yCount: 1,
					width: 0,
					height: 32,
					scroll: false,
					type: {
						height: "auto",
						width: "auto",
					},
					template: obj => obj.value,
				},
			],
		};
	}

	init() {
		this.Header = this.$$("header");

		this.Units = this.app.getService("local").units();

		this.Units.then(unitsCache => {
			this.Header.sync(unitsCache, () => {
				this.Header.define({ xCount: unitsCache.count() });
				this.Header.resize();
			});
		});
	}
}
