import { JetView } from "webix-jet";
import EditorView from "./editor";
import getPalette from "helpers/palette";
import { waitVisible } from "@xbs/jet-helpers";

export default class SidePopupView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const ui = {
			view: "popup",
			relative: "right",
			body: {
				rows: [
					webix.extend(getPalette(), {
						localId: "colors",
						on: {
							onItemClick: val => this.UpdateColor(val),
						},
					}),
					{
						css: "webix_scheduler_settings",
						template: _("Settings"),
						height: webix.skin.$active.inputHeight,
						onClick: {
							webix_template: () => this.ShowEditor(),
						},
					},
				],
			},
			on: {
				onHide: () => this.app.callEvent("side:popup:hide", []),
			},
		};

		return ui;
	}

	init() {
		waitVisible(this.getRoot()).then(() => {
			this.Editor = this.ui(EditorView);
		});
	}

	Show(id, node, color) {
		this.Cid = id;
		this.getRoot().show(node);
		this.$$("colors").setValue(color);
	}

	ShowEditor() {
		this.Editor.Show(this.Cid);
		this.getRoot().hide();
	}

	UpdateColor(val) {
		this.app.getService("operations").updateCalendar(this.Cid, {
			color: val,
		});
		this.getRoot().hide();
	}

	Hide() {
		this.getRoot().hide();
	}
}
