import { ignoreInitial } from "jet-restate";
import { JetView } from "webix-jet";
import MoreWindowView from "../month/more";

export default class YearCalendarView extends JetView {
	constructor(app, config) {
		super(app, config);

		this._month = config.month;
	}

	config() {
		this.DayEvents = {};

		return {
			view: "calendar",
			localId: "calendar",
			events: day => this.MarkEvent(day),
			icons: false,
			navigation: false,
			calendarHeader: "%F",
			skipEmptyWeeks: true,
			borderless: true,
			onClick: {
				webix_cal_day_with_event: e => this.ShowEvents(e),
				webix_cal_month: () => this.ShowMonth(),
			},
			onDblClick: {
				webix_cal_day: e => this.ShowNew(e),
			},
			on: {
				onChange: (date, mode) => mode === "user" && this.SetStateDate(date),
			},
		};
	}

	init() {
		this.Data = this.app.getService("local");
		this.Calendar = this.getRoot();
		this.Events = new webix.DataCollection({});
		const state = (this.State = this.app.getState());

		this.on(state.$changes, "date", date => this.ManageDateChange(date));

		this.on(
			state.$changes,
			"active",
			ignoreInitial(() => this.RefreshData(this.GetMonthStart(state.date)))
		);
		this.on(this.app, "events:refresh", () =>
			this.RefreshData(this.GetMonthStart(state.date))
		);

		// updates made in form
		const events = this.app.getService("local").events(true);
		this.on(
			events.data,
			"onStoreUpdated",
			(_i, _o, mode) => mode && this.RefreshData(this.GetMonthStart(state.date))
		);

		this.Popup = this.ui(
			new (this.app.dynamic(MoreWindowView))(this.app, {
				events: this.Events,
				drag: false,
			})
		);
	}

	/**
	 * Sets the date in the global reactive state
	 * @param {Date} date
	 */
	SetStateDate(date) {
		this.State.date = webix.Date.dayStart(date);
	}

	/**
	 * Switches the calendar to a new month and gets events for it
	 * @param {Date} date
	 */
	ManageDateChange(date) {
		if (!this._date || this._date.getFullYear() != date.getFullYear()) {
			let monthStart = (this.MonthStart = this.GetMonthStart(date));
			this.Calendar.showCalendar(monthStart);
			this.RefreshData(monthStart);
		}
		if (this._month === date.getMonth()) this.Calendar.setValue(date);
		else if (this.Calendar.getValue()) this.Calendar.setValue();
		this._date = date;
	}

	/**
	 * Gets the date at the start of the current month
	 * @param {Date} date
	 * @returns {Date}
	 */
	GetMonthStart(date) {
		return new Date(date.getFullYear(), this._month, 1);
	}

	/**
	 * Gets events for the given month
	 * @param {Date} monthStart - the start of the month
	 */
	RefreshData(monthStart) {
		this.DayEvents = {};
		const monthEnd = webix.Date.add(monthStart, 1, "month", true);

		this.Data.getEvents(monthStart, monthEnd).then(data => {
			this.Events.clearAll();
			this.Events.parse(data);
			while (monthStart < monthEnd) {
				let next = webix.Date.add(monthStart, 1, "day", true);
				data.forEach(obj => {
					if (
						(obj.all_day
							? obj.end_date >= monthStart
							: obj.end_date > monthStart) &&
						obj.start_date < next
					) {
						const dateKey = monthStart.valueOf();
						if (!this.DayEvents[dateKey]) this.DayEvents[dateKey] = [];
						this.DayEvents[dateKey].push(obj.id);
					}
				});
				monthStart = next;
			}
			this.Calendar.refresh();
		});
	}

	/**
	 * Adds dots to days with events
	 * @param {Date} day - the date
	 * @returns {string} the name of the CSS class
	 */
	MarkEvent(day) {
		let css = webix.Date.isHoliday(day) || "";
		if (this.DayEvents[day.valueOf()]) css += " webix_cal_day_with_event";
		return css;
	}

	/**
	 * Returns a timestamp of the day that was clicked
	 * @param {MouseEvent} e - the mouse event
	 * @returns {number|null} the timestamp or null
	 */
	GetDay(e) {
		const day = e.target.innerHTML;
		if (day) return new Date(this.MonthStart).setDate(day * 1);
		return null;
	}

	/**
	 * Shows a popup with a list of events for the day
	 * @param {MouseEvent} e - the mouse event
	 */
	ShowEvents(e) {
		const date = this.GetDay(e);
		if (date) {
			const data = this.DayEvents[date];
			this.Popup.ShowWindow(date, e.target.parentNode, data || []);
		}
	}

	/**
	 * Opens this day and opens the form with data for a new event
	 * @param {MouseEvent} e - the mouse event
	 */
	ShowNew(e) {
		const state = this.State;
		if (!state.readonly && !state.clipboard) {
			const date = this.GetDay(e);
			if (date) {
				// no $batch because view needs to be ready
				state.mode = "day";
				state.selected = { id: "0", date };
			}
		}
	}

	/**
	 * Opens the month view for the selected month
	 */
	ShowMonth() {
		this.State.$batch({
			mode: "month",
			date: this.MonthStart,
		});
	}
}
