/* eslint-disable indent */
/* eslint-disable no-mixed-spaces-and-tabs */
import { JetView } from "webix-jet";
import { FREQUENCE, WEEKDAYS, separateSequence } from "../../helpers/recurring";
import { cleanEventData } from "helpers/copypaste";

import EventActionMenu from "./actionmenu";

export default class EventView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;

		const aSkin = webix.skin.$active;
		const bar = {
			view: "toolbar",
			borderless: true,
			padding: {
				left: aSkin.layoutPadding.form - (aSkin.inputHeight - 20) / 2,
				right: 14,
			},
			elements: [
				{
					view: "icon",
					icon: "wxi-close",
					hotkey: "esc",
					click: () => this.Back(),
				},
				{},
				{
					width: 130,
					view: "button",
					localId: "edit",
					label: _("Edit"),
					css: "webix_primary",
					click: function() {
						this.$scope.StartAction("Edit", this.$view);
					},
				},
			],
		};

		if (this.app.config.copypaste) {
			bar.elements.splice(-1, 0, {
				view: "icon",
				icon: "shi-content-copy",
				tooltip: _("Copy event"),
				click: () => this.CopyEvent(),
			});
		}

		const info = {
			localId: "text",
			view: "template",
			css: "webix_scheduler_info",
			template: obj => (obj.start_date ? this.InfoTemplate(obj) : ""),
			borderless: true,
		};

		const deleteButton = {
			view: "button",
			localId: "remove",
			label: _("Delete event"),
			css: "webix_danger webix_scheduler_danger",
			align: "center",
			inputWidth: 330,
			click: function() {
				this.$scope.StartAction("Delete", this.$view);
			},
		};

		return {
			view: "proxy", // for borders
			body: {
				margin: 0,
				rows: [
					bar,
					{
						type: "form",
						padding: { right: 14 + 2 },
						borderless: true,
						rows: [info, deleteButton],
					},
				],
			},
		};
	}

	init() {
		this.State = this.app.getState();
		this.UnitsLocal = this.app.getService("local").units(true);

		this.on(this.State.$changes, "readonly", v => {
			const action = v ? "hide" : "show";
			this.$$("edit")[action]();
			this.$$("remove")[action]();
		});

		const template = this.$$("text");

		this.on(this.State.$changes, "selected", v => {
			if (v) {
				this.EventObj = this.app
					.getService("local")
					.events(true)
					.getItem(v.id);
				template.setValues(this.EventObj);
			}
		});

		this.Menu = this.ui(EventActionMenu);

		if (this.app.config.units) {
			const waitUnits = this.app.getService("local").units();
			waitUnits.then(() => {
				template.refresh();
			});
		}

		if (this.app.config.timeline) {
			const local = this.app.getService("local");
			local.sections().then(data => {
				this.Sections = data;
				template.refresh();
			});
		}
	}

	StartAction(mode, node) {
		if (
			this.app.config.recurring &&
			(typeof this.State.selected.date === "string" || this.EventObj.origin_id)
		) {
			this.Menu.Show(node).then(o => {
				if (mode === "Delete") this.DeleteEvent(o);
				else this.EditEvent(o);
			});
		} else {
			if (mode === "Delete") this.DeleteEvent();
			else this.EditEvent();
		}
	}

	EditEvent(o) {
		const compact = this.getParam("compact", true);
		const path = compact ? "event.formpopup/" : "../";
		this.show(path + "event.form", { target: "edit", params: { mode: o } });
	}

	DeleteEvent(mode) {
		const _ = this.app.getService("locale")._;
		webix
			.confirm({
				title: _("Delete event"),
				text: _("The event will be deleted permanently, are you sure?"),
				container: this.app.getRoot().$view,
			})
			.then(() => {
				const obj = this.EventObj;
				let sdate = "";
				if (mode && mode !== "all") {
					if (this.State.selected) sdate = this.State.selected.date;
					else if (obj.$id) sdate = obj.id;
				}
				this.app
					.getService("operations")
					.removeEvent(this.EventObj, mode, sdate);
				this.Back(true);
			});
	}

	/** Sets selected of the global reactive state to open form for copy creation */
	CopyEvent() {
		const data = webix.copy(this.EventObj);
		cleanEventData.call(this, data);
		this.State.$batch({
			selected: { id: "0" },
			clipboard: { mode: "copy", selected: data },
		});
	}

	/**
	 * Closes the info panel
	 * @param {Boolean} removal - indicates that the panel was closed after an event was removed
	 */
	Back(removal) {
		if (removal) {
			const clip = this.State.clipboard;
			if (
				clip &&
				(clip.selected.id === this.EventObj.id ||
					clip.selected.id.indexOf("_" + this.EventObj.id) > -1)
			) {
				this.State.clipboard = null;
			}
		}

		this.State.selected = null;
	}

	InfoTemplate(obj) {
		const _ = this.app.getService("locale")._;
		if (this.app.config.recurring && this.State.selected.date) {
			obj = { ...obj, ...separateSequence(obj, this.State.selected.date) };
		}
		const [start, end] = [obj.start_date, obj.end_date];

		const oneday = webix.Date.equal(
			webix.Date.datePart(start, true),
			webix.Date.datePart(end, true)
		);

		const allday =
			webix.Date.equal(webix.Date.datePart(start, true), start) &&
			webix.Date.equal(webix.Date.datePart(end, true), end) &&
			obj.all_day;

		const time = webix.i18n.timeFormatStr;
		const date = webix.i18n.longDateFormatStr;

		const title = `<div class="webix_scheduler_event_title">${obj.text ||
			_("(No title)")}</div>`;

		const startDate = date(start);
		const endDate = date(end);
		const startTime = time(start);
		const endTime = time(end);

		let fromTo = "";
		if (oneday) {
			fromTo = `<div class="webix_scheduler_event_text">${startDate}</div>`;
			if (!allday)
				fromTo += `<div class="webix_scheduler_event_text">${_(
					"from"
				)} ${startTime} ${_("to")} ${endTime}</div>`;
		} else {
			fromTo = `<div class="webix_scheduler_event_text">${_("from")} ${
				!allday ? startTime : ""
			} ${startDate}</div><div class="webix_scheduler_event_text">${_("to")} ${
				!allday ? endTime : ""
			} ${endDate}</div>`;
		}

		const recurring =
			this.app.config.recurring && obj.recurring
				? this.GetRecurringText(obj, _)
				: "";

		let units = "";
		if (this.app.config.units) {
			units += `<div class="webix_scheduler_event_units_title">${_(
				"Assigned to units"
			)}:</div><div class="webix_scheduler_event_units">`;
			if (obj.units) {
				const unitIDs = obj.units.split(",");
				unitIDs.forEach((id, index) => {
					const isLast = index === unitIDs.length - 1;
					const unitObj = this.UnitsLocal.getItem(id);
					const unitValue =
						unitObj && unitObj.value ? unitObj.value : _("Unknown unit");
					units += `<div class="webix_scheduler_event_unit_item">${unitValue}${
						isLast ? "" : ","
					}</div>`;
				});
			} else {
				units += _("No units");
			}
			units += "</div>";
		}

		const section =
			this.app.config.timeline && this.Sections ? this.GetSection(obj, _) : "";

		let details = "";
		if (obj.details)
			details = `<div class="webix_scheduler_event_details_title">${_(
				"Notes"
			)}</div>
				<div class="webix_scheduler_event_text">${obj.details.replace(
					/(?:\r\n|\r|\n)/g,
					"<br>"
				)}</div>`;

		return `${title}
			<div class='webix_scheduler_event_from_to'>${fromTo}</div>
			<div class='webix_scheduler_event_recurring_pattern'>${recurring}</div>${units}${section}${details}`;
	}

	GetRecurringText(obj, _) {
		const mode = obj.$recurring;
		const interval = mode.INTERVAL && mode.INTERVAL > 1 ? mode.INTERVAL : "";

		let pattern = "";
		if (mode.BYDAY) {
			pattern = ` ${_("on")} ${mode.BYSETPOS || ""} ${mode.BYDAY.split(",")
				.map(d => webix.i18n.calendar.dayFull[WEEKDAYS.indexOf(d)])
				.join(", ")} ${
				mode.BYMONTH
					? `${_("of")} ${webix.i18n.calendar.monthFull[mode.BYMONTH - 1]}`
					: ""
			}`;
		} else if (mode.BYMONTHDAY) {
			pattern =
				(mode.BYMONTHDAY ? ` ${_("on")} ` : "") +
				(mode.BYMONTH
					? `${mode.BYSETPOS ? _("of") : ""} ${
							webix.i18n.calendar.monthFull[mode.BYMONTH - 1]
					  }`
					: "") +
				(mode.BYMONTHDAY ? ` ${mode.BYMONTHDAY}` : "");
		} else if (mode.BYYEARDAY) {
			pattern = `${_("day")} ${mode.BYYEARDAY}`;
		}

		return `${_("Repeats each")} ${interval} ${_(
			FREQUENCE[mode.FREQ] + (interval ? "s" : "")
		)} ${pattern}${
			mode.COUNT
				? `, ${mode.COUNT} ${_("times")}`
				: mode.UNTIL
				? ` ${_("till")} ${webix.i18n.longDateFormatStr(mode.UNTIL)}`
				: ""
		}`;
	}

	/**
	 * Returns a string with HTML for the info with the section
	 * @param {Object} obj - data of an event
	 * @param {function} _ - the translator function of the Locale plugin
	 * @returns {string} a string with HTML for the info with the section
	 */
	GetSection(obj, _) {
		const section = this.Sections.getItem(obj.section);
		if (section)
			return `<div class="webix_scheduler_section_tpl"><span class="webix_scheduler_section_title">${_(
				"Section"
			)}:</span> ${section.text}</div>`;
		return "";
	}
}
