import { serialize, parse } from "helpers/ical";
export const WEEKDAYS = ["SU", "MO", "TU", "WE", "TH", "FR", "SA"];

export const FREQUENCE = {
	DAILY: "day",
	WEEKLY: "week",
	MONTHLY: "month",
	YEARLY: "year",
};

/**
 * Splits each recurring event into a series of events and returns the resulting array
 * @param {Date} sdate - the start date
 * @param {Date} edate - the end date
 * @param {Object} ev - the event data object
 * @returns {Array} with recurring events split into occurrences
 */
export function explodeEvent(sdate, edate, ev) {
	const mode = ev.$recurring;

	const consts = {};
	const res = [];

	// when we use week-days in recurring mode, convert structure
	// list of days => list of steps to the next allowed day
	fillWdays(mode, consts);

	const xdate = new Date(ev.start_date);
	const length = ev.end_date - ev.start_date;

	// occurrences before active window
	let count = 1;
	while (
		(sdate - xdate >= (length || 1) &&
			(!mode.UNTIL || xdate < mode.UNTIL) &&
			(!mode.COUNT || count <= mode.COUNT)) ||
		findDate(mode.EXDATE, xdate)
	) {
		++count;
		next(xdate, mode, consts);
	}

	// occurrences inside of window
	while (
		edate > xdate &&
		(!mode.UNTIL || xdate < mode.UNTIL) &&
		(!mode.COUNT || count <= mode.COUNT)
	) {
		// make copy of recurring event
		if (!findDate(mode.EXDATE, xdate)) {
			const copy = webix.copy(ev);

			xdate.setHours(ev.start_date.getHours());
			copy.start_date = new Date(xdate);
			copy.end_date = new Date(xdate * 1 + length);

			copy.$id = ev.id;
			copy.id = copy.start_date.valueOf() + "_" + ev.id;
			res.push(copy);
		}
		count++;

		next(xdate, mode, consts);
	}
	return res;
}

// IE support
/**
 * Finds a date in an array of dates
 * @param {Array} arr - all dates
 * @param {Date} date - the date to be checked
 * @returns {Boolean}
 */
function findDate(arr, date) {
	if (!arr) return false;

	for (let i = 0; i < arr.length; ++i) {
		if (webix.Date.equal(webix.Date.dayStart(date), arr[i])) return true;
	}
	return false;
}

/**
 * Defines the object that stores the recurring pattern, for more details please refer to the iCalendar docs https://icalendar.org/iCalendar-RFC-5545/3-8-5-3-recurrence-rule.html
 * @typedef {Object} Recurring
 * @property {string} FREQ - the step of repetition: DAILY, WEEKLY, MONTHLY, YEARLY
 * @property {(number|string)} INTERVAL - how many steps are between occurrences
 * @property {string} BYDAY - week days, e.g. SU,MO,TU,WE,TH,FR,SA
 * @property {(number|string)} BYMONTH - the number or a month (1-12)
 * @property {(number|string)} BYMONTHDAY - the date (day of a month)
 * @property {(number|string)} BYSETPOS - the number of the week day within a month
 * @property {string} UNTIL - a date like 19970902T090000
 * @property {(number|string)} COUNT - how many times the event repeats
 * @property {string} EXDATE - string with 1 or more dates, on which the event does not repeat, e.g. 19960402T010000Z,19960403T010000Z,19960404T010000Z
 */

/**
 * Fills the list of steps to the next allowed day for recurring events that repeat by certain week days
 * @param {Recurring} mode - the object with the recurring pattern
 * @param {Object} consts - (for events that have BYDAY in their pattern) stores wdays (Array) - the list of steps to the next allowed day
 */
export function fillWdays(mode, consts) {
	if (mode.BYDAY) {
		const diff = (consts.wdays = [0, 0, 0, 0, 0, 0, 0]);
		const line = mode.BYDAY.split(",")
			.map(a => WEEKDAYS.indexOf(a))
			.sort();

		// for each allowed day, fill range with steps
		for (let i = 0; i < line.length; i++) {
			const s = line[i];
			const e = line[i + 1] || line[0] + 7;

			for (let j = s; j < e; j++) {
				diff[j % 7] = e - j;
			}
		}
	}
}

/**
 * Calculates the date of the next occurrence of a repeating event
 * @param {Date} date - the current date
 * @param {Recurring} mode - the object with the recurring pattern
 * @param {Object} consts - (for events that have BYDAY in their pattern) stores wdays (Array) - the list of steps to the next allowed day
 */
export function next(date, mode, consts) {
	let interval = (mode.INTERVAL || 1) * 1;

	let now, step;
	switch (mode.FREQ) {
		case "DAILY":
			date.setDate(date.getDate() + interval);
			break;
		case "WEEKLY":
			now = date.getDay();
			step = consts.wdays ? consts.wdays[now] : 7;
			if (step + now > 6 && interval > 1) {
				// when new day is on the different week
				// add week interval as well
				step += 7 * (interval - 1);
			}
			date.setDate(date.getDate() + step);
			break;
		case "YEARLY": {
			if (mode.BYYEARDAY) {
				date.setFullYear(date.getFullYear() + interval);
				date.setMonth(0);
				date.setDate(1);
				date.setDate(mode.BYYEARDAY);
				break;
			}
			interval *= 12;
			// falls through
		}
		/*eslint no-fallthrough: "error"*/
		case "MONTHLY":
			now = date.getMonth();
			date.setDate(1);
			date.setMonth(now + interval);
			if (mode.BYMONTHDAY) {
				date.setDate(mode.BYMONTHDAY);
				// when date is outside of month bounds, switch to last day of month
				if ((now + interval) % 12 != date.getMonth()) date.setDate(0);
			} else if (mode.BYSETPOS) {
				// go to last day of month before one in question
				date.setDate(0);
				// find the matching day
				for (let i = 0; i < mode.BYSETPOS; i++) {
					step = consts.wdays[date.getDay()];
					date.setDate(date.getDate() + step);
				}
			}
			break;
	}

	return date;
}

/**
 * Defines the result of the check if a date fits a BYDAY pattern
 * @typedef {Object} weekdayCheckResult
 * @property {Boolean} result true if the date fits the BYDAY pattern, false otherwise
 * @property {Date} start - the copy of the start date
 * @property {Array} days - BYDAY pattern as an array of strings
 * @property {string} oldDay - the week day of the start date
 */
/**
 * Checks if the start day fits the recurring BYDAY pattern
 * @param {Date} sdate - the start date of an event
 * @param {Recurring} mode - the object with the recurring pattern
 * @returns {weekdayCheckResult} the result of the check
 */
function checkDay(sdate, mode) {
	const start = new Date(sdate);
	const days = mode.BYDAY.split(",");
	const oldDay = WEEKDAYS[start.getDay()];
	const result = days.indexOf(oldDay) !== -1;

	return { result, start, days, oldDay };
}

/**
 * Synchronizes the start and end dates to the BYDAY recurring pattern
 * @param {Date} sdate
 * @param {Date} edate
 * @param {Recurring} mode - the object with the recurring pattern
 * @return {Array} - the start and end dates of an event
 */
export function datesToWeekdays(sdate, edate, mode) {
	const { result, start, days } = checkDay(sdate, mode);
	if (result) {
		return [sdate, edate];
	}

	const diff = WEEKDAYS.indexOf(days[0]) - start.getDay();
	start.setDate(start.getDate() + diff);

	const end = new Date(edate);
	end.setDate(end.getDate() + diff);

	return [start, end];
}

/**
 * Synchronizes BYDAY recurring pattern with the date of an event
 * @param {Date} sdate - the start date of an event
 * @param {Recurring} mode - the object with the recurring pattern
 * @returns {string} with the new BYDAY pattern
 */
export function WeekdaysToDates(sdate, mode) {
	let { result, days, oldDay } = checkDay(sdate, mode);
	if (result) {
		return mode.BYDAY;
	}

	if (mode.FREQ === "WEEKLY") {
		days.splice(WEEKDAYS.indexOf(days[0]), 0, oldDay);
	} else {
		days = [oldDay];
	}
	return days.join(",");
}

/**
 * Returns selection parameters for the global state
 * @param {Object} obj - the event data object
 * @returns {Object} with selection parameters
 */
export function getParams(obj) {
	return { id: obj.$id || obj.id, date: obj.$recurring ? obj.id : null };
}

/**
 * Defines the selection.date parameter of the global state
 * @typedef {(Date|string)} SDate - a Date object or the ID of an event occurrence that consists of a timestamp and the event ID from DB, separated with _
 */

/**
 * Gets a date from the selection.date parameter of the global state
 * @param {(SDate)} sdate
 * @returns {Date}
 */
export function extractDate(sdate) {
	return webix.Date.dayStart(
		sdate.getDate ? sdate : new Date(sdate.split("_")[0] * 1)
	);
}

/**
 * Updates the end of a recurring event sequence
 * @param {SDate} sdate
 * @param {Object} ev - the event data object
 * @param {Boolean} block
 * @returns {string} - serialized recurrence pattern
 */
export function clipSequence(sdate, ev, block) {
	const date = extractDate(sdate);

	if (!block || (!ev.$recurring.UNTIL || ev.$recurring.UNTIL > date))
		ev.$recurring.UNTIL = date;

	if (ev.$recurring.COUNT) delete ev.$recurring.COUNT;

	if (ev.$recurring.EXDATE) {
		ev.$recurring.EXDATE = ev.$recurring.EXDATE.filter(d => d < date);
	}

	return serialize(ev.$recurring);
}

/**
 * Removes the specified date from the recurrence pattern
 * @param {SDate} sdate
 * @param {Object} ev - the event data object
 * @returns {string} - serialized recurrence pattern
 */
export function cutOccurrence(sdate, ev) {
	const date = extractDate(sdate);

	if (!ev.$recurring.EXDATE) ev.$recurring.EXDATE = [];
	ev.$recurring.EXDATE.push(date);

	return serialize(ev.$recurring);
}

/**
 * Gets the start and end dates for a particular occurrence from a series
 * @param {Object} obj - the event data object
 * @param {SDate} sdate
 * @returns {Object} with start_date and end_date
 */
export function separateSequence(obj, sdate) {
	const start_date =
		typeof sdate === "string"
			? new Date(sdate.split("_")[0] * 1)
			: new Date(sdate);
	const len = (obj.end_date - obj.start_date) / 1000 / 60; // mins
	const end_date = webix.Date.add(start_date, len, "minute", true);
	return { start_date, end_date };
}

/**
 * Checks if this is the first occurrence of a series
 * @param {SDate} sdate
 * @param {Date} evStart
 * @returns {Boolean}
 */
export function isFirstOccurrence(sdate, evStart) {
	// editing 'this' event as next
	if (!sdate) return false;

	const od = sdate.getDate ? sdate : new Date(sdate.split("_")[0] * 1);
	return od <= evStart;
}

/**
 * Corrects the edit mode for specific cases (depending on which occurrence was selected)
 * @param {Object} obj - the event data object
 * @param {string} mode - "all", "next", "this"
 * @returns {string}
 */
export function correctMode(obj, mode) {
	if (!obj.origin_id || !obj.recurring) {
		mode = "all";
	} else {
		// if this is 1st occ in 'next' which is the last part of 'all'
		if (this.app.getService("local").isLastPart(obj)) {
			mode = "";
		}
	}
	return mode;
}

const dayLength = 86400000;
/**
 * Moves a recurrence pattern if a recurring event was moved to another date
 * @param {Recurring} rec
 * @param {Object} newEvent - the new event data object
 * @param {Object} oldEvent - the old event data object
 * @returns {Recurring}
 */
export function movePattern(rec, newEvent, oldEvent) {
	let days =
		(webix.Date.dayStart(newEvent.start_date) -
			webix.Date.dayStart(oldEvent.start_date)) /
		dayLength;

	if (days) {
		if (rec.UNTIL) {
			const until = webix.Date.add(rec.UNTIL, days, "day", true);

			if (until > newEvent.end_date) rec.UNTIL = until;
			else
				rec.UNTIL = webix.Date.dayStart(
					webix.Date.add(newEvent.end_date, 1, "day", true)
				);
		}

		// day of start_date changed and pattern doesn't have in anymore
		if (rec.BYDAY) {
			days %= 7;
			if (days < 0) days += 7;
			rec.BYDAY = rec.BYDAY.split(",")
				.map(d => WEEKDAYS[(WEEKDAYS.indexOf(d) + days) % 7])
				.join(",");
		}

		// in case day of start_date changed in monthly or yearly
		if (rec.BYMONTHDAY) {
			const nmd = newEvent.start_date.getDate();
			if (nmd != rec.BYMONTHDAY) {
				rec.BYMONTHDAY = nmd;
			}
		}
	}

	// week number of start_date changed in yearly or monthly
	if (rec.BYSETPOS) {
		const nwn = getWeekNum(newEvent.start_date);
		if (rec.BYSETPOS != nwn) rec.BYSETPOS = nwn;
	}

	// month of start_date changed in a yearly event
	if (rec.BYMONTH) {
		const nm = newEvent.start_date.getMonth() + 1;
		if (nm != rec.BYMONTH) rec.BYMONTH = nm;
	}

	return rec;
}

/**
 * Returns the number of a week day within a month (e.g. 3rd Wednesday)
 * @param {Date} date
 * @returns {number}
 */
export function getWeekNum(date) {
	const day = date.getDate();
	return Math.floor(day / 7) + (day % 7 ? 1 : 0);
}

/**
 * Fills incomplete recurring patterns and ensures that they are consistent with the dates of events
 * @param {Object} ev - data object of an event
 */
export function checkPattern(ev) {
	const mode = ev.$recurring || parse(ev.recurring);

	// when exact date not configured, fill recurring config
	if (mode.FREQ === "WEEKLY" && !mode.BYDAY) {
		mode.BYDAY = WEEKDAYS[ev.start_date.getDay()];
		return;
	}

	if (
		(mode.FREQ === "MONTHLY" || mode.FREQ === "YEARLY") &&
		!mode.BYSETPOS &&
		!mode.BYMONTHDAY &&
		!mode.BYYEARDAY
	) {
		mode.BYMONTHDAY = ev.start_date.getDate();
		return;
	}

	if (mode.FREQ === "YEARLY" && !mode.BYMONTH && !mode.BYYEARDAY) {
		mode.BYMONTH = ev.start_date.getMonth();
		return;
	}

	// in case config is wrong, fix it
	if (mode.BYDAY) mode.BYDAY = WeekdaysToDates(ev.start_date, mode);
	if (mode.BYMONTHDAY) mode.BYMONTHDAY = ev.start_date.getDate();
	if (mode.BYMONTH) mode.BYMONTH = ev.start_date.getMonth() + 1;
	if (mode.BYSETPOS) mode.BYSETPOS = getWeekNum(ev.start_date);

	ev.recurring = serialize(mode);
}
