import { JetView } from "webix-jet";
import Popup from "./summarypopup";

export default class Summaries extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const labels = {
			margin: webix.skin.$active.layoutMargin.form,
			cols: [
				{
					view: "label",
					label: _("Function"),
					width: 100,
					css: "webix_rpt_subtitle",
				},
				{
					view: "label",
					label: _("Column"),
					css: "webix_rpt_subtitle",
				},
				{
					view: "label",
					label: _("New column name"),
					css: "webix_rpt_subtitle",
				},
				{
					width: 38,
				},
			],
		};

		const inputs = {
			margin: webix.skin.$active.layoutMargin.form,
			localId: "summaries",
			view: "form",
			type: "clean",
			css: "webix_rpt_summary_form",
			elements: [],
		};

		return {
			borderless: true,
			type: "clean",

			rows: [
				{
					cols: [{ view: "label", label: _("Group summaries") }],
				},
				labels,
				inputs,
				{
					height: webix.skin.$active.layoutMargin.form,
				},
				{
					view: "button",
					icon: "wxi-plus",
					type: "icon",
					label: _("Add column"),
					width: 210,
					align: "left",
					click: () => this.AddNewAggregation(),
				},
			],
		};
	}

	init() {
		this.State = this.getParam("state", true);
		this.GroupState = this.getParam("group", true);

		this.SummaryColumns = this.State.fields
			.filter(column => {
				return (
					(column.type == "number" || column.type == "date") && !column.key
				);
			})
			.sort((a, b) => (a.type < b.type ? 1 : -1))
			.map(a => ({ id: a.id, value: a.name, type: a.type }));

		this.CreateForm();
		this.on(this.GroupState.$changes, "by", () => this.ValidateFormData());
	}

	CreateForm() {
		const aggregate = this.GroupState.columns;
		if (aggregate && aggregate.length) this.LoadAggregations(aggregate);
		else this.AddNewAggregation();

		if (!this.SummaryColumns.length) {
			this.ApplyOneRowMode();
		}
	}
	ApplyOneRowMode() {
		const rows = this.getRoot().getChildViews();
		rows[3].hide();
		rows[4].hide();
	}

	AddNewAggregation() {
		this.AddAggregation();
		this.Changed();
	}

	LoadAggregations(data) {
		data.forEach(summary => {
			this.AddAggregation(summary);
		});
		this.Changed();
	}

	AddAggregation(aggregation) {
		aggregation = aggregation || this.GetDefaultFormData();
		if (!aggregation) return false;
		const formId = aggregation.id || "form" + webix.uid();
		this.$$("summaries").addView(this.GetRowForm(formId, aggregation));
	}

	GetRowForm(id, data) {
		let cols = [
			{
				width: 100,
				view: "richselect",
				name: "op",
				value: data.op || null,
				options: this.GetOperations(),
				on: {
					onChange: v => this.Changed(id, "op", v),
				},
			},
			{
				view: "richselect",
				name: "id",
				disabled: data.op == "count",
				value: data.id || null,
				options: this.SummaryColumns,
				on: {
					onChange: v => this.Changed(id, "id", v),
				},
			},
			{
				view: "text",
				value: data.name || null,
				name: "name",
				on: {
					onTimedKeypress: v => this.Changed(id, "name", v),
				},
			},
		];

		cols.push({
			view: "icon",
			icon: "wxi-dots",
			click: (id, ev) => this.ShowPopup(id, ev),
		});

		return {
			view: "form",
			localId: id,
			type: "clean",
			borderless: true,
			margin: webix.skin.$active.layoutMargin.form,
			rules: {
				id: value => {
					const groupBy = this.GroupState.by;
					return !groupBy || groupBy.indexOf(value) < 0;
				},
			},
			cols: cols,
		};
	}

	GetDefaultFormData() {
		const data = this.GetData();
		const columns = this.SummaryColumns;
		const operations = this.GetOperations();
		if (!columns.length) {
			const op = !data.length ? operations.find(op => op.id == "count") : null;
			return op
				? {
						op: "count",
						id: "",
						name: this.NewColumnTemplate(op),
						type: "number",
				  }
				: null;
		} else {
			for (let i = 0; i < operations.length; i++) {
				for (let j = 0; j < columns.length; j++) {
					const op = operations[i];
					if (
						!data.find(
							row =>
								row.op == op.id && (op.id == "count" || row.id == columns[j].id)
						)
					)
						return {
							op: op.id,
							id: op.id != "count" ? columns[j].id : null,
							name: this.NewColumnTemplate(op, columns[j]),
							type: columns[j].type,
						};
				}
			}
		}
		return null;
	}

	NewColumnTemplate(operation, column) {
		return operation.id == "count"
			? operation.value
			: operation.value + " " + column.value;
	}

	ShowPopup(id, ev) {
		if (!this.Popup || !this.Popup.$view)
			this.Popup = this.ui(new Popup(this.app, "summary"));
		const form = webix.$$(id).getParentView();
		this.ActiveForm = form;
		this.Popup.Show(
			ev.target,
			this.GetData(),
			form.getParentView().index(form)
		);
	}

	ApplyAction(id) {
		if (id == "delete") this.DeleteAggregation();
	}

	DeleteAggregation(form) {
		form = form || this.ActiveForm;
		const formList = form.getParentView();
		if (formList.getChildViews().length > 1) formList.removeView(form);
		else form.setValues({});
		this.Changed();
	}

	GetOperations() {
		const _ = this.app.getService("locale")._;
		return [
			{ id: "sum", value: _("Sum") },
			{ id: "count", value: _("Count") },
			{ id: "avg", value: _("Average") },
			{ id: "max", value: _("Max") },
			{ id: "min", value: _("Min") },
		];
	}

	GetData() {
		let data = [];
		this.ForEachFormRow(form => {
			data.push(form.getValues());
		});
		return data;
	}

	ForEachFormRow(handler) {
		this.$$("summaries")
			.getChildViews()
			.forEach(form => {
				if (form && form.elements) {
					handler(form);
				}
			});
	}

	ValidateFormData() {
		let valid = true;
		this.ForEachFormRow(form => (valid = form.validate() && valid));
		return valid;
	}

	ChangeNewColumnName(formId) {
		const form = this.$$(formId).elements;
		const oId = form.op.getValue();
		const cId = form.id.getValue();
		const operations = this.GetOperations();

		if (oId) {
			if (oId == "count")
				form.name.setValue(
					this.NewColumnTemplate(operations.find(o => o.id == oId))
				);
			else if (cId)
				form.name.setValue(
					this.NewColumnTemplate(
						operations.find(o => o.id == oId),
						this.SummaryColumns.find(c => c.id == cId)
					)
				);
		}
	}

	Changed(formId, name, value) {
		if (name == "op" || name == "id") this.ChangeNewColumnName(formId);
		if (name == "op" && value == "count") {
			this.$$(formId).elements.id.setValue(null);
			this.$$(formId).elements.id.disable();
		} else {
			if (formId && name) this.$$(formId).elements.id.enable();
		}

		this.ValidateFormData();
		const columns = this.GetData();
		columns.forEach(
			a =>
				(a.type = (
					this.SummaryColumns.find(b => b.id == a.id) || { type: "number" }
				).type)
		);
		this.GroupState.columns = columns;
	}
}
