import { JetView } from "webix-jet";

export default class HourscaleView extends JetView {
	config() {
		return {
			view: "list",
			borderless: true,
			localId: "scale",
			css: "webix_scheduler_scale",
			type: {
				height: 42,
				heightSize: (obj, common) => {
					const ind = this.List.getIndexById(obj.id);
					return common.height - (ind === 0 ? 17 : 0) + "px";
				},
			},
			scroll: false,
			autoheight: true,
			width: 51,
			template: obj => this.HourScaleItem(obj),
		};
	}

	init() {
		this.List = this.$$("scale");
		this.ParseHours();
	}

	ParseHours() {
		const data = [];
		for (let h = 0; h < 24; h++) {
			data.push({ id: h + "" });
		}
		this.List.parse(data);
	}

	HourScaleItem(obj) {
		if (this.List.getIndexById(obj.id) === 0) return "";
		else return `${(obj.id < 10 ? "0" : "") + obj.id}:00`;
	}
}
