import { JetView } from "webix-jet";
import Popup from "./summarypopup";

export default class BucketView extends JetView {
	constructor(app, state, pstate) {
		super(app);
		this.State = state;
		this.ParentState = pstate;
	}

	config() {
		const _ = (this._ = this.app.getService("locale")._);
		this.ValueSeparator = ",";
		const inputs = {
			margin: webix.skin.$active.layoutMargin.form,
			padding: { top: 0, bottom: 0, left: 0, right: 0 },
			localId: "forms",
			view: "form",
			type: "clean",
			elements: [],
		};
		const other = {
			localId: "other",
			view: "form",
			type: "clean",
			borderless: true,
			margin: webix.skin.$active.layoutMargin.form,
			cols: [
				{
					view: "text",
					name: "name",
					width: 150,
					placeholder: _("Bucket name"),
					value: _("Other"),
					on: {
						onChange: v => this.Changed("other", "name", v),
					},
				},
				{
					view: "label",
					css: "webix_rpt_subtitle",
					label: _("Other values"),
				},
			],
			rules: {
				name: webix.rules.isNotEmpty,
			},
		};
		const padding = webix.skin.$active.layoutPadding.form;
		return {
			borderless: true,
			type: "clean",
			padding: { top: padding, bottom: padding, left: 0, right: 0 },
			css: "webix_rpt_subview",
			rows: [
				inputs,
				{
					height: webix.skin.$active.layoutMargin.form,
				},
				other,
				{
					height: webix.skin.$active.layoutMargin.form,
				},
				{
					view: "button",
					icon: "wxi-plus",
					type: "icon",
					label: _("Add bucket"),
					inputWidth: 210,
					align: "left",
					click: () => this.AddNewBucket(),
				},
			],
		};
	}

	init() {
		this.Local = this.app.getService("local");
		this.Forms = this.$$("forms");
		this.GetRowKeys(this.State.id).then(data => {
			this.Data = data.sort((a, b) => this.Sort(a, b));
			this.CreateForm();
		});
		this.on(this.app, "onAction", (name, action, id) => {
			if (name == "bucket" && action == "focus" && id == this.State.id)
				this.Focus(0);
		});
	}
	/*
	 * Gets row keys (requires data loading)
	 * params:
	 * - the field / column id where keys need to be found
	 * Returns a promise
	 * */
	GetRowKeys(base) {
		if (base && this.ParentState.columns.length) {
			let keys = [];
			return this.GetColumnData(base).then(([data, baseOptions]) => {
				data.forEach(row => {
					const key = row[base];
					if (keys.indexOf(key) == -1) keys.push(key);
				});

				if (baseOptions)
					return keys.map(key => {
						const keyOption =
							baseOptions.data.find(option => option.id == key) || null;
						return { id: key, value: keyOption ? keyOption.value : key };
					});
				return keys.map(key => ({ id: key, value: key }));
			});
		} else return Promise.resolve([]);
	}
	GetColumnData(baseColumn) {
		const appState = this.app.getParam("state");
		let moduleConfig = this.Local.getDataConfig(appState.module);
		moduleConfig.columns = [baseColumn];
		delete moduleConfig.buckets;
		delete moduleConfig.group;
		let waitData = [
			this.Local.getData(moduleConfig),
			this.Local.getOptionsList(baseColumn),
		];
		return Promise.all(waitData);
	}

	CreateForm() {
		const options = this.State.options;
		if (options && options.length) this.LoadBuckets(options);
		else this.AddNewBucket();
		webix.delay(() => {
			this.app.callEvent("onAction", ["bucket", "resize", this.State.id]);
			this.Focus(0);
		});
	}

	AddNewBucket() {
		this.AddBucket();
		this.Changed();
		this.Focus();
	}

	LoadBuckets(data) {
		data.forEach(bucket => {
			if (!bucket.values) this.LoadOther(bucket);
			else this.AddBucket(bucket);
		});
		this.Changed();
	}
	LoadOther(b) {
		const other = this.$$("other");
		other.setValues({ name: b.id });
	}
	AddBucket(bucket) {
		bucket = bucket || {};
		this.Forms.addView(this.GetRowForm(webix.uid(), bucket));
	}

	GetRowForm(id, data) {
		let cols = [
			{
				rows: [
					{
						width: 150,
						view: "text",
						name: "name",
						value: data.id || null,
						placeholder: this._("Bucket name"),
						on: {
							onChange: v => this.Changed(id, "name", v),
						},
					},
				],
			},
			{
				view: "multicombo",
				name: "values",
				placeholder: this._("Bucket values"),
				css: "webix_multilist webix_rpt_multicombo",
				separator: this.ValueSeparator,
				suggest: {
					body: {
						css: "webix_multilist webix_rpt_bucket_values",
						data: this.Data,
						template: obj => this.OptionTemplate(id, obj),
					},
					on: {
						onBeforeShow: function() {
							this.getList().refresh();
						},
					},
				},
				value: data.values || null,
				on: {
					onChange: v => this.Changed(id, "values", v),
				},
			},
			{
				rows: [
					{
						view: "icon",
						icon: "wxi-dots",
						click: (id, ev) => this.ShowPopup(id, ev),
					},
				],
			},
		];

		return {
			view: "form",
			localId: id,
			type: "clean",
			borderless: true,
			margin: webix.skin.$active.layoutMargin.form,
			rules: {
				name: webix.rules.isNotEmpty,
				values: webix.rules.isNotEmpty,
			},
			cols: cols,
		};
	}
	ShowPopup(id, ev) {
		if (!this.Popup || !this.Popup.$view)
			this.Popup = this.ui(new Popup(this.app, "bucket"));
		const form = webix
			.$$(id)
			.getParentView()
			.getParentView();
		this.ActiveForm = form;
		this.Popup.Show(
			ev.target,
			this.GetValues(),
			form.getParentView().index(form)
		);
	}
	GetValidBuckets() {
		const otherForm = this.$$("other");
		const otherName = otherForm.elements.name.getValue();
		otherForm.validate();
		const names = {};
		if (otherName) names[otherName] = [otherForm];
		const options = [];
		this.ForEachFormRow(form => {
			form.validate();
			let values = form.getValues();
			let n = values.name;
			if (n) {
				if (names[n]) {
					this.$$(names[n]).forEach(f => f.markInvalid("name", true));
					form.markInvalid("name", true);
				} else if (values.values.length)
					options.push({ id: n, values: values.values.split(",") });
				if (!names[n]) names[n] = [];
				names[n].push(form);
			}
		});
		if (options.length && otherName) options.push({ id: otherName });
		return options;
	}
	Changed(id, name, value) {
		if (name == "values") this.CorrectValues(id, value);
		const options = this.GetValidBuckets();
		this.State.options = options;
	}

	OptionTemplate(id, obj) {
		let selected =
			!obj.$checked &&
			this.GetValues().find(
				bucket => bucket.id != id && bucket.values.indexOf(obj.value) > -1
			);
		const css =
			"webix_rpt_bucket_value" +
			(selected ? " webix_rpt_bucket_value_selected" : "");
		return (
			"<div class='" +
			css +
			"'>" +
			obj.value +
			"</div>" +
			(selected
				? "<span class='webix_rpt_bucket_icon webix_icon rpi-pail-outline'></span>"
				: "")
		);
	}
	GetValues() {
		let data = [];
		this.ForEachFormRow(form => {
			const v = form.getValues();
			v.id = form.config.localId;
			data.push(v);
		});
		return data;
	}
	ForEachFormRow(handler) {
		this.Forms.getChildViews().forEach(form => {
			if (form && form.elements) {
				handler(form);
			}
		});
	}
	CorrectValues(id, v) {
		this.ForEachFormRow(form => {
			if (form.config.localId != id) {
				let values = form.getValues().values.split(",");
				values = values.filter(a => v.indexOf(a) == -1);
				form.elements.values.setValue(values);
			}
		});
	}
	ApplyAction(id) {
		if (id == "delete") this.DeleteBucket(this.ActiveForm);
	}

	DeleteBucket(form) {
		this.Forms.removeView(form);
		this.Changed();
	}
	Sort(a, b) {
		const type = this.ParentState.fields.find(c => c.id == this.State.id).type;
		a = a.value;
		b = b.value;
		if (type == "number") {
			a = a * 1;
			b = b * 1;
			if (isNaN(b)) return 1;
			if (isNaN(a)) return -1;
		} else {
			if (!b) return 1;
			if (!a) return -1;
			a = a.toString().toLowerCase();
			b = b.toString().toLowerCase();
		}
		return a > b ? 1 : a < b ? -1 : 0;
	}
	Focus(index) {
		webix.delay(
			() => {
				const forms = this.Forms.getChildViews();
				if (typeof index == "undefined") index = forms.length - 1;
				const form = forms[index];
				if (form) form.elements.name.focus();
			},
			null,
			null,
			100
		);
	}
}
