import { JetView } from "webix-jet";
import side from "views/side";

export default class NavPopupView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const units = !!this.app.config.units;

		const list = {
			view: "list",
			css: "webix_scheduler_navlist",
			localId: "modes",
			autoheight: true,
			data: [
				{ id: "day", value: _("Day"), icon: "shi-day" },
				{ id: "week", value: _("Week"), icon: "shi-week" },
				{ id: "month", value: _("Month"), icon: "shi-month" },
				{ id: "agenda", value: _("Agenda"), icon: "shi-agenda" },
				{ id: "year", value: _("Year"), icon: "shi-year" },
			],
			on: {
				onItemClick: id => this.SetMode(id),
			},
		};

		if (units) {
			list.data.push({ id: "units", value: _("Units"), icon: "shi-units" });
		}

		if (this.app.config.timeline) {
			list.data.push({
				id: "timeline",
				value: _("Timeline"),
				icon: "shi-timeline",
			});
		}

		const ui = {
			view: "sidemenu",
			animate: false,
			borderless: true,
			css: "webix_scheduler_sidemenu",
			width: 300,
			state: state => {
				const tHeight = webix.skin.$active.tabbarHeight + 10;
				state.left = this.Parent.left;
				state.top = this.Parent.top + tHeight + 2;
				state.height = this.Parent.height - tHeight - 2;
			},
			body: list,
		};

		if (this.app.config.calendars)
			ui.body = {
				view: "scrollview",
				body: {
					type: "clean",
					rows: [list, side],
				},
			};

		return ui;
	}
	init() {
		this.State = this.app.getState();

		this.on(this.app, "view:resize", () => {
			if (this.getRoot().isVisible()) this.Show(true);
		});
	}
	Show(updatePos) {
		if (!updatePos && this.getRoot().isVisible()) return;

		this.Parent = this.app.getRoot().$view.getBoundingClientRect();
		this.$$("modes").select(this.State.mode);
		this.getRoot().show();
	}
	SetMode(value) {
		this.getRoot().hide();

		//wait until hide animation is complete
		setTimeout(() => this.State.$batch({ mode: value, selected: null }), 500);
	}
}
