import { createState, withContext } from "jet-restate";

export function bind(obj, key, ctrl, view) {
	bindOut(obj, key, ctrl);
	return bindIn(obj, key, ctrl, view);
}

function bindIn(obj, key, ctrl, view) {
	const handler = (v, o, k, meta) => {
		if (k === "*") {
			var els = ctrl.elements;
			for (var subkey in els) {
				if (typeof v[subkey] !== "undefined") els[subkey].setValue(v[subkey]);
			}
		} else if (key === "*") {
			var el = ctrl.elements[k];
			if (el && el != meta) el.setValue(v);
		} else if (meta != ctrl) ctrl.setValue(v);
	};

	return view
		? view.on(obj.$changes, key, handler)
		: obj.$changes.attachEvent(key, handler);
}

function bindOut(obj, key, ctrl) {
	if (key === "*") {
		var els = ctrl.elements;
		for (var subkey in els) {
			if (typeof obj[subkey] !== "undefined") bindOut(obj, subkey, els[subkey]);
		}
		return;
	}

	const trigger = function(newValue) {
		const oldValue = obj[key];
		if (newValue !== oldValue) {
			obj[key] = newValue;
		}
	};

	if (!ctrl.getList)
		ctrl.attachEvent("onTimedKeyPress", function() {
			const newValue = this.getValue();
			if (this.config.value !== newValue) {
				this.config.value = newValue;
				this.callEvent("onChange", [newValue]);
			}
		});

	ctrl.attachEvent("onChange", function() {
		withContext(this, () => {
			trigger(this.getValue());
		});
	});
}

export function track(obj) {
	if (!obj.$changes) obj = createState(obj);

	var observe = function(key, ctrl) {
		return bind(obj, key, ctrl, ctrl);
	};

	var observeEnd = function(id) {
		obj.$changes.detachEvent(id);
	};

	return {
		attachEvent: observe,
		detachEvent: observeEnd,
	};
}
