/* eslint-disable indent */

import { JetView } from "webix-jet";
import EditRecurringMixin from "helpers/editrecurring";
import { daysBetweenInclusive } from "helpers/dates";
import { marker, dayEventColor } from "helpers/templates";
import { getParams } from "helpers/recurring";
import { initDnD } from "helpers/dnd";

export default class MoreWindowView extends JetView {
	constructor(app, c) {
		super(app);
		this.Events = c.events;
		this.drag = webix.isUndefined(c.drag) ? true : c.drag;
	}

	config() {
		const _ = this.app.getService("locale")._;
		return {
			view: "popup",
			width: 266,
			padding: 8,
			minHeight: 70,
			relative: "right",
			body: {
				view: "list",
				css: "webix_scheduler_more_list",
				borderless: true,
				autoheight: true,
				yCount: 3,
				tooltip: {
					template: "",
				},
				onClick: {
					webix_scheduler_month_event: (e, id) => this.ShowEvent(id),
					webix_scheduler_month_event_single: (e, id) => this.ShowEvent(id),
				},
				type: {
					height: 56,
					template: (o, c) => this.MoreTemplate(o, c, _),
				},
			},
		};
	}

	init(view) {
		this.Win = view;

		EditRecurringMixin(this);

		if (this.drag) initDnD.call(this, view.getBody(), "more", true);
	}

	MoreTemplate(obj, _c, _) {
		const i18n = webix.i18n;
		const single = !daysBetweenInclusive(
			obj.start_date,
			obj.all_day ? webix.Date.add(obj.end_date, 1, "day", true) : obj.end_date
		);
		const style = single ? "" : dayEventColor(obj);

		const oneDay =
			!single && this.webix.Date.equal(obj.start_date, obj.end_date);

		let css = `webix_scheduler_month_event${single ? "_single" : ""}`;
		if (obj.$past) css += " webix_scheduler_past_event";

		return `
			<div
				class="${css}"
				style="${style}"
			>
				${single ? marker(obj) : ""}
				<div webix_tooltip="${obj.text}" class="webix_event_text">${obj.text ||
			_("(No title)")}</div>
				<div class="webix_event_time">
					${this.DayFormatStr(obj.start_date)}
					${
						oneDay
							? ""
							: `${i18n.timeFormatStr(obj.start_date)} -
						${single ? "" : " " + this.DayFormatStr(obj.end_date)}
						${i18n.timeFormatStr(obj.end_date)}`
					}
				</div>
			</div>
		`;
	}

	DayFormatStr(date) {
		return webix.Date.dateToStr("%F %j")(date);
	}

	ShowEvent(id) {
		const obj = this.Events.getItem(id);
		this.app.getState().selected = getParams(obj);
		this.HideWindow();
	}

	ShowWindow(id, node, data) {
		if (this.ID === id && this.Win.isVisible()) return this.Win.hide();

		this.ID = id;
		data = data.map(v => this.Events.getItem(v));

		const list = this.Win.getBody();
		list.clearAll();
		list.parse(data);

		this.Win.show(node);
	}

	HideWindow() {
		this.Win.hide();
	}
}
