import HourscaleView from "../common/hourscale";
import SelectionMixin from "helpers/selection";
import EditRecurringMixin from "helpers/editrecurring";
import { initDnD, locateEvent } from "helpers/dnd";
import { shrinkTo, isToday } from "helpers/dates";
import { dayEventColor } from "helpers/templates";
import { getParams } from "helpers/recurring";
import CopyPasteMixin from "helpers/copypaste";

export default class DayEventsView extends HourscaleView {
	constructor(app, config) {
		super(app);

		// when dayevents is a part of week
		if (config) {
			this.WeekDayNum = config.day;
		}
	}
	config() {
		return {
			view: "list",
			localId: "dayList",
			css: "webix_scheduler_day_events",
			scroll: false,
			autoheight: true,
			template: "",
			type: {
				height: 42,
				css: "webix_scheduler_day_scale_item",
			},
			onClick: {
				webix_scheduler_day_event: e => this.ShowEvent(e),
			},
			onDblClick: {
				webix_scheduler_day_scale_item: (_e, h) => this.ShowNew(h),
			},
		};
	}
	init() {
		this.State = this.app.getState();
		this.List = this.$$("dayList");

		SelectionMixin(this, "scale");
		EditRecurringMixin(this);

		this.List.$setSize = function(x, y) {
			if (webix.ui.view.prototype.$setSize.call(this, x, y)) {
				this.render();
			}
		};

		this.minEventHeight = 62;
		this.ParseHours();

		this.on(this.State.$changes, "readonly", () => this.List.render());

		this.List.attachEvent("onAfterRender", () => this.RenderEvents());
		initDnD.call(this, this.List, "day");

		const compact = this.getParam("compact", true);
		if (this.app.config.copypaste && !webix.env.mobile && !compact)
			CopyPasteMixin(this, this.List);
	}

	urlChange() {
		this.data = this.getParam("data");
		if (this.data) {
			this.Day = webix.Date.datePart(this.State.date, true);

			if (this.WeekDayNum > -1) {
				const weekStart = this.data.start;
				this.Day = webix.Date.add(weekStart, this.WeekDayNum, "day", true);
			}

			const to = webix.Date.add(this.Day, 1, "day", true);
			this.Events = this.FilterToday(this.Day, to);

			if (this._marker_update_interval)
				this._marker_update_interval = clearInterval(
					this._marker_update_interval
				);

			if (isToday(this.Day))
				this._marker_update_interval = setInterval(() => {
					if (isToday(this.Day)) this.UpdateMarkerPosition();
					else {
						webix.html.remove(this._Marker);
						this._Marker = this._marker_update_interval = clearInterval(
							this._marker_update_interval
						);
					}
				}, 5 * 60 * 1000); // 5 minutes

			this.List.render();
		}
	}

	GetEvent(id) {
		for (let i = 0; i < this.Events.length; i++)
			if (this.Events[i].id == id) return this.Events[i];
		return null;
	}

	FilterToday(value, to) {
		if (this.WeekDayNum > -1) {
			return this.data.single.filter(ev => {
				return ev.start_date < to && ev.end_date > value;
			});
		}
		return this.data.single;
	}

	MarkToday() {
		if (this.Day && isToday(this.Day)) {
			const marker = webix.html.create("div", {
				class: "webix_scheduler_today_marker",
			});
			this._Marker = this.List.$view.firstChild.appendChild(marker);
			this.UpdateMarkerPosition();
		} else {
			this._Marker = null;
		}
	}

	UpdateMarkerPosition() {
		if (this._Marker) {
			const current = new Date();
			const time = current.getHours() * 60 + current.getMinutes();
			this._Marker.style.top =
				Math.round((time * this.List.type.height) / 60) + "px";
		}
	}

	RenderEvents() {
		const evs = this.Events;
		if (evs && evs.length) {
			this.PrepareEvents();
			const container = webix.html.create("div", {
				role: "presentation",
			});
			container.innerHTML = evs.map(i => this.ToHTML(i)).join("");
			this._DataObj = this.List.$view.firstChild.appendChild(container);
			this.app.callEvent("events:rendered", []);
		}
		this.MarkToday();
	}

	PrepareEvents() {
		const evs = this.Events;
		let stack = [];

		for (let i = 0; i < evs.length; i++) {
			const ev = evs[i];
			ev.$inner = false;

			let orderSet = this.PruneStack(ev, stack);

			if (stack.length) {
				stack[stack.length - 1].$inner = true;
			}

			if (!orderSet) {
				if (stack.length) {
					ev.$sorder = stack[stack.length - 1].$sorder + 1;
					ev.$inner = false;
				} else {
					ev.$sorder = 0;
				}
			}

			// insert new event into stack
			stack.splice(ev.$sorder, 0, ev);
			if (stack.length > (stack.max_count || 0)) {
				stack.max_count = stack.length;
			}
		}

		this.SetPosition(stack, evs);
	}

	// returns true if new event was marked for inserting;
	// false if stack is empty or there is no suitable place for new event inside
	PruneStack(newEvent, stack) {
		// clean events from top of stack if new event starts after they end
		while (
			stack.length &&
			this.GetEventEndDate(stack[stack.length - 1]) <= newEvent.start_date
		) {
			stack.splice(stack.length - 1, 1);
		}

		// clean 1 event from within remaining stack if this event ends before new event starts
		for (let j = 0; j < stack.length; j++) {
			if (this.GetEventEndDate(stack[j]) <= newEvent.start_date) {
				newEvent.$sorder = stack[j].$sorder; // mark new event for this place in stack
				stack.splice(j, 1);
				newEvent.$inner = true;

				return true;
			}
		}

		return false;
	}

	SetPosition(stack, evs) {
		//scale start correction
		const delta = this.List.getFirstId() * this.List.type.height;

		for (let i = 0; i < evs.length; i++) {
			const ev = evs[i];

			ev.$count = stack.max_count;
			let temp_width = Math.floor(this.List.$width / ev.$count);
			ev.$left = ev.$sorder * temp_width;
			if (!ev.$inner) temp_width = temp_width * (ev.$count - ev.$sorder);
			ev.$width = temp_width - 8;

			const evCorrectedCopy = shrinkTo(
				this.Day,
				webix.Date.add(this.Day, 1, "day", true)
			)(ev);
			const sm =
				evCorrectedCopy.start_date.getHours() * 60 +
				evCorrectedCopy.start_date.getMinutes();
			const em =
				evCorrectedCopy.end_date.getHours() * 60 +
					evCorrectedCopy.end_date.getMinutes() || 24 * 60;

			ev.$top = Math.round((sm * this.List.type.height) / 60) - delta;
			ev.$height = Math.max(
				this.minEventHeight,
				((em - sm) * this.List.type.height) / 60
			);
		}
	}

	ToHTML(obj) {
		const content = this.EventTemplate(obj);
		return this.EventTemplateStart(obj) + content + "</div>";
	}

	GetEventEndDate(ev) {
		let end_date = ev.end_date;
		const duration =
			(((ev.end_date - ev.start_date) / 60000) * this.List.type.height) / 60;

		if (duration < this.minEventHeight) {
			end_date = webix.Date.add(
				ev.start_date,
				Math.ceil((this.minEventHeight / this.List.type.height) * 60),
				"minute",
				true
			);
		}
		return end_date;
	}

	EventTemplateStart(obj) {
		const css = `class="webix_scheduler_day_event ${
			this.State.selected &&
			(obj.id == this.State.selected.id || obj.id == this.State.selected.date)
				? "webix_scheduler_event_selected"
				: ""
		} ${obj.$past ? "webix_scheduler_past_event" : ""}"`;

		return `<div webix_e_id='${obj.id}' ${css} style='left:${
			obj.$left
		}px; top:${obj.$top}px; width:${obj.$width}px; height:${
			obj.$height
		}px; ${dayEventColor(obj)}'>`;
	}

	EventTemplate(obj) {
		const _ = this.app.getService("locale")._;
		const resizer = this.State.readonly
			? ""
			: "<div class='webix_icon webix_scheduler_resizer' webix_resizer='true'></div>";

		return `<div class="webix_scheduler_inner_day"><span class="webix_scheduler_event_name">${obj.text ||
			_(
				"(No title)"
			)}</span><div class="webix_scheduler_event_time">${webix.i18n.timeFormatStr(
			obj.start_date
		)} - ${webix.i18n.timeFormatStr(obj.end_date)}</div></div>${resizer}`;
	}

	ShowEvent(e) {
		const event = locateEvent(e, this.List.$view);
		if (event.id && event.id != "0") {
			const obj = this.GetEvent(event.id);
			const sel = webix.extend(event, getParams(obj), true);
			this.State.selected = sel;
		}
		return false;
	}

	/**
	 * Returns the target date for an event
	 * @param {(string|number)} h - the ID of the hour segment of the main view
	 * @returns {Date}
	 */
	GetTargetDate(h) {
		const date = webix.Date.copy(this.Day || this.State.date);
		if (h) date.setHours(h);
		return date;
	}

	/**
	 * Sets selected in the global reactive state to display a newly created event
	 * @param {(string|number)} h - the ID of the hour segment of the main view
	 */
	ShowNew(h) {
		const state = this.State;
		if (!state.readonly && !state.clipboard) {
			state.selected = { id: "0", date: this.GetTargetDate(h) };
		}
	}

	/**
	 * Updates selected in global reactive state
	 * @param {(string|number)} id - the ID of the event
	 * @param {Object} newEvent - the data of the new event that should be selected
	 */
	UpdateSelection(id, newEvent) {
		if (this.State.selected) {
			newEvent.id = id;
			this.State.selected = getParams(newEvent);
		}
	}
}
