import { JetView } from "webix-jet";

export default class DateView extends JetView {
	config() {
		const toolbar = [
			{
				view: "icon",
				icon: "wxi-angle-left",
				tooltip: () => this.NavLabel(-1),
				align: "left",
				on: { onItemClick: () => this.ChangeDate(-1) },
			},
			{ view: "label", localId: "title", align: "center", width: 160 },
			{
				view: "icon",
				icon: "wxi-angle-right",
				tooltip: () => this.NavLabel(1),
				align: "right",
				on: { onItemClick: () => this.ChangeDate(1) },
			},
		];

		return {
			cols: toolbar,
		};
	}

	init() {
		this.State = this.app.getState();
		this.on(this.State.$changes, "date", () => this.DateLabel());
		this.on(this.State.$changes, "mode", () => this.DateLabel());
		if (this.app.config.timeline)
			this.on(this.State.$changes, "timelineMode", () => this.DateLabel());
	}

	ChangeDate(dir) {
		const state = this.State;
		const step = this.GetStep(state.mode);
		const next = webix.Date.add(state.date, dir, step, true);

		// fix navigation in month mode when last day of month is selected
		if (state.mode === "month") {
			const diff = Math.abs(state.date.getMonth() - next.getMonth());
			if (diff != 1 && diff < 6) next.setDate(0);
		}

		state.$batch({
			date: next,
			selected: null,
		});
	}

	GetStep(mode) {
		switch (mode) {
			case "agenda":
				return "month";
			case "units":
				return "day";
			case "timeline":
				return this.State.timelineMode;
			default:
				return mode;
		}
	}

	DateLabel() {
		const date = this.State.date;
		const mode = this.State.mode;
		let format = webix.Date.dateToStr("%F %Y");
		if (mode === "year") format = webix.Date.dateToStr("%Y");
		else if (
			mode == "day" ||
			mode == "units" ||
			(mode == "timeline" && this.State.timelineMode == "day")
		)
			format = webix.i18n.longDateFormatStr;

		this.$$("title").setHTML(format(date));
	}

	NavLabel(dir) {
		const _ = this.app.getService("locale")._;
		const modes = ["month", "week", "day"];
		let mode = this.State.mode;
		mode = modes.indexOf(mode) !== -1 ? ` ${mode}` : "";

		dir = dir > 0 ? "Next" : "Previous";

		return _(dir + mode);
	}
}
